import {
  StyleSheet,
  Text,
  View,
  Image,
  TouchableOpacity,
  TextInput,
  Dimensions,
  CheckBox,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import OTPTextView from "react-native-otp-textinput";
import DropDownPicker from "react-native-dropdown-picker";
import axios from "axios";
import * as ImagePicker from "expo-image-picker";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import config from "../../../config";
import { set } from "react-native-reanimated";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var logo = require("../../../assets/logo.png");
var previous = require("../../../assets/Previous.png");
var loading = require("../../../assets/loading.gif");
var km = require("../../../assets/icon.png");
var numbers = require("../../../assets/profile-numbers.png");
var pp = require("../../../assets/profile.png");
var pencil = require("../../../assets/pencil.png");
var check = require("../../../assets/check.png");
var eye = require("../../../assets/eye.png");
var eye_slash = require("../../../assets/eye-slash.png");
var ProfileSettings = ({ route, navigation }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const [imageurl, setImageURL] = useState(null);
  const [imageView, setImageView] = useState(null);
  const [shopName, setShopName] = useState("");
  const [protofolioLink, setProtofolioLink] = useState("");
  const [about, setAbout] = useState("");
  const [otherSite, setOtherSite] = useState("");
  const [isAgree, setIsAgree] = useState(false);
  const [aboutUs, setAboutUs] = useState("");
  const [hidePassword, setHidePassword] = useState(true);
  const [oldPassword, setOldPassword] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  //Dropdown country
  const [openCountry, setOpenCountry] = useState(false);
  const [valueCountry, setValueCountry] = useState([]);
  const [itemsCountry, setItemsCountry] = useState([
    { label: "Egypt", value: "egypt" },
    { label: "KSA", value: "ksa" },
    { label: "UAE", value: "uae" },
  ]);

  const [country, setCountry] = useState(null);

  //Dropdown city
  const [openCity, setOpenCity] = useState(false);
  const [valueCity, setValueCity] = useState([]);
  const [itemsCity, setItemsCity] = useState([]);
  const [citiesList, setCityList] = useState({});
  //Dropdown other websites
  const [openOther, setOpenOther] = useState(false);
  const [valueOther, setValueOther] = useState([]);
  const [itemsOther, setItemsOther] = useState([
    { label: "Yes", value: true },
    { label: "No", value: false },
  ]);

  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);

  const [errMsg, setErrorMsg] = useState("");
  const [profile, setProfile] = useState({});
  function getBase64(file) {
    var reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function () {
      setImageView(reader.result);
    };
    reader.onerror = function (error) {
      console.log("Error: ", error);
    };
  }
  const [isEdit, setIsEdit] = useState(false);
  const [isChangePassword, setIsChangePassword] = useState(false);
  var changePassword = () => {
    setLoading(true);
    console.log(
      {
        password: password,
        password2: confirmPassword,
        old_password: oldPassword,
      },
      {
        auth: {
          username: config.email,
          password: password,
        },
      }
    );

    try {
      axios
        .patch(
          config.host + "/api/account/change_password/" + config.token + "/",
          {
            password: password,
            password2: confirmPassword,
            old_password: oldPassword,
          },
          {
            headers: { "Content-Type": "application/json" },
            auth: {
              username: config.email,
              password: oldPassword,
            },
          }
        )
        .then((response) => {
          setLoading(false);

          setError(true);
          setIsChangePassword(false);
          setErrorMsg("Password changed successfully!");
        });
    } catch (e) {
      console.log(e);
      setLoading(false);
    }
  };
  var toggleEdit = () => {
    setIsEdit(true);
  };

  var editProfile = () => {
    setIsEdit(false);
    // setLoading(true);
    // try {
    //   let form_data = new FormData();
    //   form_data.append("aboutArtist", about);
    //   form_data.append("howDidYouHearAboutUs", aboutUs);
    //   form_data.append("shopName", shopName);
    //   form_data.append("country", valueCountry);
    //   form_data.append("city", valueCity);
    //   form_data.append("portofolioLink", protofolioLink);
    //   form_data.append("otherWebsite_bool", valueOther);
    //   form_data.append("otherWebsite", otherSite);
    //   if (imageurl) {
    //     form_data.append("shopPhoto", imageurl, imageurl.name);
    //   }
    //   axios
    //     .post(
    //       config.host + "/api/profiles/createArtist/?token=" + config.token,
    //       form_data,
    //       {
    //         headers: {
    //           "Content-Type": "multipart/form-data",
    //         },
    //       }
    //     )
    //     .then((response) => {
    //       setLoading(false);
    //       setError(true);
    //       setErrorMsg("Profile saved successfully!");
    //       getProfile();
    //     })
    //     .catch((err) => {
    //       setLoading(false);
    //       setError(true);
    //       setErrorMsg(err.request.response);
    //     });
    // } catch (e) {
    //   setLoading(false);
    //   setError(true);
    //   setErrorMsg(e.message);
    // }
  };
  var uploadImageHandler = () => {};

  var getCountries = () => {
    axios
      .get("https://countriesnow.space/api/v0.1/countries/states")
      .then((response) => {
        var countries = response.data.data;
        var countryList = [];
        var cityList = {};
        for (var x in countries) {
          countryList.push({
            label: countries[x].name,
            value: countries[x].name,
          });
          cityList[countries[x].name] = [];
          for (var y in countries[x].states) {
            cityList[countries[x].name].push({
              label: countries[x].states[y].name,
              value: countries[x].states[y].name,
            });
          }
        }
        setItemsCountry(countryList);
        setCityList(cityList);
        console.log("cityList");
        console.log(cityList);
      });
  };

  const pickImage = async () => {
    let result = await ImagePicker.launchImageLibraryAsync({
      mediaTypes: ImagePicker.MediaTypeOptions.Images,
      allowsEditing: true,
      base64: true,
      aspect: [4, 3],
      quality: 1,
      type: "file",
    });

    if (!result.cancelled) {
      setLoading(false);
      setError(true);
      setErrorMsg("Error uploading file");
    }
  };

  var getProfile = () => {
    axios
      .get(
        config.host + "/api/profiles/getArtistProfile/?token=" + config.token
      )
      .then((response) => {
        setLoading(false);
        setProfile(response.data);
        console.log(response.data);
        setShopName(response.data.shopName);
        setAbout(response.data.aboutArtist);
        setAboutUs(response.data.howDidYouHearAboutUs);
        setValueCountry(response.data.country);
        setValueCity(response.data.city);
        setOtherSite(response.data.otherWebsite);
        setProtofolioLink(response.data.portofolioLink);
        setValueOther(response.data.otherWebsite_bool);
        setImageView(response.data.shopPhoto);
      })
      .catch((e) => {
        setLoading(false);
        setError(true);
        setErrorMsg("Something went wrong");
      });
  };

  useEffect(() => {
    getCountries();
    getProfile();
  }, []);
  useEffect(() => {
    if (valueCountry != "") {
      console.log(valueCountry);
      setItemsCity(citiesList[valueCountry]);
    }
  }, [valueCountry]);
  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: "100%",
          height: "100%",
          overflow: "hidden",
          backgroundColor: "#FFFFFF",
        }}
      >
        {isChangePassword ? (
          <View
            style={{
              position: "absolute",
              height: "100%",
              width: "100%",
              backgroundColor: "#C4C4C4AA",
              zIndex: 999,
              alignSelf: "center",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <View
              style={{
                width: "40%",
                height: "45%",
                alignItems: "center",
                backgroundColor: "#ffffff",
                borderRadius: 20,
                borderWidth: 1,
                borderColor: "grey",
                justifyContent: "center",
              }}
            >
              <TouchableOpacity
                style={{
                  width: "10%",
                  alignSelf: "flex-end",
                  height: "3%",
                  marginRight: "2%",
                  padding: 10,
                  borderRadius: 4,

                  alignItems: "center",
                  justifyContent: "center",
                  borderRadius: 10,
                  shadowRadius: 10,
                  shadowColor: "#3D9EBC",
                  backgroundColor: "#3D9EBC",
                }}
                onPress={() => {
                  setIsChangePassword(false);
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_400Regular",
                    color: "#FFFFFF",

                    fontSize: windowHeight <= 1080 ? 12 : 16,
                  }}
                >
                  Cancel
                </Text>
              </TouchableOpacity>
              <Text
                style={{
                  marginLeft: "0.5%",
                  fontFamily: "Roboto_700Bold",
                  fontSize: windowHeight <= 1080 ? 24 : 32,
                  color: "#04475C",
                }}
              >
                Change password
              </Text>
              <View style={{ height: "5%" }} />

              <View style={styles.pwfield}>
                <TextInput
                  style={{
                    borderColor: "white",
                    width: "90%",
                  }}
                  autoCompleteType={"password"}
                  secureTextEntry={hidePassword}
                  autoCorrect={false}
                  placeholder={"Old password"}
                  onChangeText={(newText) => setOldPassword(newText)}
                ></TextInput>
                <TouchableOpacity
                  style={{
                    height: "100%",
                    width: "10%",
                    justifyContent: "center",
                  }}
                  onPress={() => {
                    setHidePassword(!hidePassword);
                  }}
                >
                  <Image
                    source={hidePassword ? eye_slash : eye}
                    style={{
                      width: "100%",
                      height: 12,
                      resizeMode: "contain",
                    }}
                  />
                </TouchableOpacity>
              </View>

              <View style={styles.pwfield}>
                <TextInput
                  style={{
                    borderColor: "white",
                    width: "90%",
                  }}
                  autoCompleteType={"password"}
                  secureTextEntry={hidePassword}
                  autoCorrect={false}
                  placeholder={"New password"}
                  onChangeText={(newText) => setPassword(newText)}
                ></TextInput>
                <TouchableOpacity
                  style={{
                    height: "100%",
                    width: "10%",
                    justifyContent: "center",
                  }}
                  onPress={() => {
                    setHidePassword(!hidePassword);
                  }}
                >
                  <Image
                    source={hidePassword ? eye_slash : eye}
                    style={{
                      width: "100%",
                      height: 12,
                      resizeMode: "contain",
                    }}
                  />
                </TouchableOpacity>
              </View>
              <View style={styles.pwfield}>
                <TextInput
                  style={{
                    borderColor: "white",
                    width: "90%",
                  }}
                  autoCompleteType={"password"}
                  secureTextEntry={hidePassword}
                  autoCorrect={false}
                  placeholder={"Confirm password"}
                  onChangeText={(newText) => setConfirmPassword(newText)}
                ></TextInput>
                <TouchableOpacity
                  style={{
                    height: "100%",
                    width: "10%",
                    justifyContent: "center",
                  }}
                  onPress={() => {
                    setHidePassword(!hidePassword);
                  }}
                >
                  <Image
                    source={hidePassword ? eye_slash : eye}
                    style={{
                      width: "100%",
                      height: 12,
                      resizeMode: "contain",
                    }}
                  />
                </TouchableOpacity>
              </View>
              <View style={{ height: "8%" }} />
              <TouchableOpacity
                style={{
                  width: "15%",
                  height: "10%",
                  shadowRadius: 10,
                  shadowColor: "#3D9EBC",
                  backgroundColor: "#3D9EBC",
                  alignItems: "center",
                  alignSelf: "center",
                  justifyContent: "center",
                  borderRadius: 4,
                }}
                onPress={changePassword}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    color: "#FFFFFF",
                    fontSize: windowHeight <= 1080 ? 12 : 16,
                  }}
                >
                  Confirm
                </Text>
              </TouchableOpacity>
            </View>
          </View>
        ) : (
          <View />
        )}
        <View style={{ position: "absolute", bottom: 50, left: 50 }}>
          <Text>{config.version}</Text>
        </View>
        <View
          style={{
            height: "11%",
            width: "100%",
            left: 0,
            top: 0,
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            alignItems: "flex-start",
          }}
        >
          <View style={{ height: "15%" }}></View>
          <Image
            source={logo}
            style={{
              width: 214,
              height: "70%",
              marginLeft: 20,
              resizeMode: "contain",
            }}
          />
          <View style={{ height: "15%" }}></View>
        </View>
        <TouchableOpacity
          style={{ width: "3%", height: 25 }}
          onPress={() => {
            navigation.pop();
          }}
        >
          <Image
            source={previous}
            style={{
              width: windowHeight <= 1080 ? 60 : 80,
              height: windowHeight <= 1080 ? 40 : 50,
              resizeMode: "contain",
              marginLeft: 20,
            }}
          />
        </TouchableOpacity>
        <View
          style={{
            width: "100%",
            height: "87%",
            flexDirection: "row",
            marginTop: "1%",
          }}
        >
          <View style={{ width: "100%", height: "100%", alignItems: "center" }}>
            <View style={{ height: "2%" }} />
            <View style={{ width: "20%", height: "10%" }}>
              <Image
                source={imageView ? imageView : pp}
                style={{
                  width: "100%",
                  height: "100%",
                  resizeMode: "contain",
                }}
              />
            </View>
            {isEdit ? (
              <View
                style={{
                  position: "absolute",
                  alignItems: "flex-end",
                  width: "40%",
                  height: "2%",
                  top: "10%",
                }}
              >
                <TouchableOpacity
                  style={{
                    height: "100%",
                    width: "25%",
                    borderRadius: 20,
                    alignItems: "center",
                  }}
                  onPress={() => {}}
                >
                  <label
                    htmlFor="filePicker"
                    style={{ background: "#E2F5FB", padding: "5px 10px" }}
                  >
                    + Pick Image
                  </label>
                </TouchableOpacity>

                <input
                  id="filePicker"
                  type="file"
                  hidden="true"
                  name="image_url"
                  accept="image/jpeg,image/png,image/gif"
                  onChange={(e) => {
                    getBase64(e.target.files[0]);
                    setImageURL(e.target.files[0]);
                  }}
                />
              </View>
            ) : (
              <View />
            )}
            {isEdit ? (
              <TouchableOpacity
                style={{
                  position: "absolute",
                  width: "4.5%",
                  height: "4%",
                  shadowRadius: 10,
                  shadowColor: "#3D9EBC",
                  flexDirection: "row",
                  alignItems: "center",
                  alignSelf: "flex-end",
                  marginRight: "5%",
                  justifyContent: "center",
                  backgroundColor: "#F2F2F2",
                  borderRadius: 4,
                  marginTop: "2%",
                }}
                onPress={editProfile}
              >
                <View style={{ width: "12.5%" }} />
                <Image
                  style={{
                    height: "40%",
                    width: "25%",
                    resizeMode: "contain",
                  }}
                  source={check}
                />
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    color: "#000000",
                    marginLeft: "5%",
                    width: "45%",
                    fontSize: windowHeight <= 1080 ? 12 : 16,
                  }}
                >
                  Save
                </Text>
                <View style={{ width: "12.5%" }} />
              </TouchableOpacity>
            ) : (
              <TouchableOpacity
                style={{
                  position: "absolute",
                  width: "4.5%",
                  height: "4%",
                  shadowRadius: 10,
                  shadowColor: "#3D9EBC",
                  flexDirection: "row",
                  alignItems: "center",
                  alignSelf: "flex-end",
                  marginRight: "5%",
                  justifyContent: "center",
                  backgroundColor: "#F2F2F2",
                  borderRadius: 4,
                  marginTop: "2%",
                }}
                onPress={toggleEdit}
              >
                <View style={{ width: "12.5%" }} />
                <Image
                  style={{
                    height: "100%",
                    width: "25%",
                    resizeMode: "contain",
                  }}
                  source={pencil}
                />
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    color: "#000000",
                    marginLeft: "5%",
                    width: "45%",
                    fontSize: windowHeight <= 1080 ? 12 : 16,
                  }}
                >
                  Edit
                </Text>
                <View style={{ width: "12.5%" }} />
              </TouchableOpacity>
            )}

            <View
              style={{
                width: "100%",
                height: "8%",
                flexDirection: "row",
                justifyContent: "center",
              }}
            >
              <View style={styles.subField}>
                {shopName != "" ? (
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 12 : 18,
                      color: "#04475C",
                    }}
                  >
                    Shop name
                  </Text>
                ) : (
                  <View />
                )}
                <View style={{ height: "5%", width: "100%" }} />
                <TextInput
                  style={{
                    borderColor: "black",
                    height: "90%",
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    fontFamily: "Roboto_400Regular",
                    borderWidth: 1,
                    borderColor: "grey",
                    borderRadius: 4,
                  }}
                  autoCorrect={false}
                  placeholder={shopName == "" ? "Shop name" : shopName}
                  onChangeText={(newText) => setShopName(newText)}
                  editable={isEdit}
                ></TextInput>
              </View>
              <View style={{ width: "0.5%" }} />
              <View style={[styles.subField, { zIndex: 999 }]}>
                {aboutUs != "" ? (
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 12 : 18,
                      color: "#04475C",
                    }}
                  >
                    How did you hear about us?
                  </Text>
                ) : (
                  <View />
                )}
                <View style={{ height: "5%", width: "100%" }} />
                <TextInput
                  style={{
                    borderColor: "black",
                    height: "90%",
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    fontFamily: "Roboto_400Regular",
                    borderWidth: 1,
                    borderColor: "grey",
                    borderRadius: 4,
                  }}
                  autoCorrect={false}
                  placeholder={
                    aboutUs == "" ? "How did you hear about us?" : aboutUs
                  }
                  onChangeText={(newText) => setAboutUs(newText)}
                  editable={isEdit}
                ></TextInput>
              </View>
            </View>
            <View style={{ height: "2%" }} />
            <View
              style={{
                width: "100%",
                height: "8%",
                flexDirection: "row",
                justifyContent: "center",
                zIndex: -1,
              }}
            >
              <View style={styles.field}>
                {protofolioLink != "" ? (
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 12 : 18,
                      color: "#04475C",
                    }}
                  >
                    Portfolio link
                  </Text>
                ) : (
                  <View />
                )}
                <View style={{ height: "5%", width: "100%" }} />
                <TextInput
                  style={{
                    borderColor: "black",
                    height: "90%",
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    fontFamily: "Roboto_400Regular",
                    borderWidth: 1,
                    borderColor: "grey",
                    borderRadius: 4,
                  }}
                  autoCorrect={false}
                  placeholder={
                    protofolioLink == "" ? "Portfolio link" : protofolioLink
                  }
                  editable={isEdit}
                  onChangeText={(newText) => setProtofolioLink(newText)}
                ></TextInput>
              </View>
            </View>

            <View style={{ height: "2%" }} />
            <View
              style={{
                width: "100%",
                height: "8%",
                flexDirection: "row",
                justifyContent: "center",
                zIndex: -1,
              }}
            >
              <View style={styles.field}>
                {about != "" ? (
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 12 : 18,
                      color: "#04475C",
                    }}
                  >
                    About the artist
                  </Text>
                ) : (
                  <View />
                )}
                <View style={{ height: "5%", width: "100%" }} />
                <TextInput
                  style={{
                    borderColor: "black",
                    height: "90%",
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    fontFamily: "Roboto_400Regular",
                    borderWidth: 1,
                    borderColor: "grey",
                    borderRadius: 4,
                  }}
                  autoCorrect={false}
                  placeholder={about == "" ? "About the artist" : about}
                  editable={isEdit}
                  onChangeText={(newText) => setAbout(newText)}
                  multiline={true}
                ></TextInput>
              </View>
            </View>
            <View style={{ height: "2%" }} />

            <View
              style={{
                width: "100%",
                height: "8%",
                flexDirection: "row",
                justifyContent: "center",
              }}
            >
              <View style={styles.subField}>
                {valueCountry != "" ? (
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 12 : 18,
                      color: "#04475C",
                    }}
                  >
                    Country
                  </Text>
                ) : (
                  <View />
                )}
                <View style={{ height: "5%", width: "100%" }} />
                <DropDownPicker
                  placeholder="Country"
                  open={openCountry}
                  value={valueCountry}
                  items={itemsCountry}
                  setOpen={setOpenCountry}
                  setValue={setValueCountry}
                  setItems={setItemsCountry}
                  style={{ height: "120%" }}
                  arrowIconStyle={{
                    alignSelf: "flex-end",
                    width: 20,
                    height: 10,
                    justifyContent: "center",
                  }}
                  textStyle={{
                    marginTop: 5,
                    marginBottom: 5,
                    fontSize: windowHeight <= 1080 ? 12 : 18,

                    fontFamily: "Roboto_400Regular",
                    color: "#333333AA",
                    borderWidth: 1,
                    borderColor: "grey",
                    borderRadius: 4,
                  }}
                  containerStyle={{ zIndex: 999 }}
                  dropDownContainerStyle={{ zIndex: 999 }}
                  showArrowIcon={false}
                  disabled={!isEdit}
                />
              </View>
              <View style={{ width: "0.5%" }} />
              <View style={[styles.subField, { zIndex: 999 }]}>
                {valueCity != "" ? (
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 12 : 18,
                      color: "#04475C",
                    }}
                  >
                    City
                  </Text>
                ) : (
                  <View />
                )}
                <View style={{ height: "5%", width: "100%" }} />
                <DropDownPicker
                  placeholder="State"
                  open={openCity}
                  value={valueCity}
                  items={itemsCity}
                  setOpen={setOpenCity}
                  setValue={setValueCity}
                  setItems={setItemsCity}
                  style={{ height: "120%" }}
                  arrowIconStyle={{
                    alignSelf: "flex-end",
                    width: 20,
                    height: 10,
                    justifyContent: "center",
                  }}
                  textStyle={{
                    marginTop: 5,
                    marginBottom: 5,
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    fontFamily: "Roboto_400Regular",
                    color: "#333333AA",
                    borderWidth: 1,
                    borderColor: "grey",
                    borderRadius: 4,
                  }}
                  containerStyle={{ zIndex: 999 }}
                  dropDownContainerStyle={{ zIndex: 999 }}
                  showArrowIcon={false}
                  disabled={!isEdit}
                />
              </View>
            </View>
            <View style={{ height: "2%" }} />

            <View
              style={{
                width: "100%",
                height: "8%",
                flexDirection: "row",
                justifyContent: "center",
                zIndex: -1,
              }}
            >
              <View style={styles.subField}>
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    color: "#04475C",
                  }}
                >
                  Is there other websites you sell your art in?
                </Text>

                <View style={{ height: "5%", width: "100%" }} />
                <DropDownPicker
                  placeholder="Is there other websites you sell your art in?"
                  open={openOther}
                  value={valueOther}
                  items={itemsOther}
                  setOpen={setOpenOther}
                  setValue={setValueOther}
                  setItems={setItemsOther}
                  style={{ height: "120%" }}
                  dropDownDirection={"BOTTOM"}
                  arrowIconStyle={{
                    alignSelf: "flex-end",
                    width: 20,
                    height: 10,
                    justifyContent: "center",
                    zIndex: 999,
                  }}
                  textStyle={{
                    marginTop: 5,
                    marginBottom: 5,
                    fontSize: windowHeight <= 1080 ? 12 : 18,

                    fontFamily: "Roboto_400Regular",
                    color: "#333333AA",
                    borderWidth: 1,
                    borderColor: "grey",
                    borderRadius: 4,
                  }}
                  containerStyle={{ zIndex: 999 }}
                  dropDownContainerStyle={{ zIndex: 999 }}
                  showArrowIcon={false}
                  disabled={!isEdit}
                />
              </View>
              <View style={{ width: "0.5%" }} />
              <View style={[styles.subField, { zIndex: 999 }]}>
                {otherSite != "" ? (
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 12 : 18,
                      color: "#04475C",
                    }}
                  >
                    Website name
                  </Text>
                ) : (
                  <View />
                )}
                <View style={{ height: "5%", width: "100%" }} />
                <TextInput
                  style={{
                    borderColor: "black",
                    height: "90%",
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    fontFamily: "Roboto_400Regular",
                    borderWidth: 1,
                    borderColor: "grey",
                    borderRadius: 4,
                  }}
                  autoCorrect={false}
                  placeholder={otherSite == "" ? "Website name" : otherSite}
                  onChangeText={(newText) => setOtherSite(newText)}
                  editable={isEdit}
                ></TextInput>
              </View>
            </View>

            <TouchableOpacity
              style={{
                width: "15%",
                height: "5%",
                shadowRadius: 10,
                shadowColor: "#3D9EBC",
                backgroundColor: "#3D9EBC",
                alignItems: "center",
                alignSelf: "flex-end",
                marginRight: "5%",
                justifyContent: "center",
                borderRadius: 4,
                marginTop: "8%",
              }}
              onPress={() => {
                setIsChangePassword(true);
              }}
            >
              <Text
                style={{
                  fontFamily: "Roboto_700Bold",
                  color: "#FFFFFF",
                  fontSize: windowHeight <= 1080 ? 12 : 16,
                }}
              >
                Change password
              </Text>
            </TouchableOpacity>
          </View>
        </View>

        {isLoading ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFFCC",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "20%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 500,
                alignSelf: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Image
                source={loading}
                style={{
                  width: "90%",
                  height: "40%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "70%",
                }}
              />
            </View>
          </View>
        ) : (
          <View />
        )}

        {isError ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFF99",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "30%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 50,
                alignSelf: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 12 : 16,
                  color: "#333333",
                  marginTop: "10%",
                }}
              >
                {errMsg}
              </Text>

              <View style={{ height: "10%" }} />
              <TouchableOpacity
                style={{
                  width: "20%",
                  alignSelf: "center",
                  height: "10%",

                  backgroundColor: "#888888",
                  alignItems: "center",
                  justifyContent: "center",
                  borderRadius: 10,
                }}
                onPress={() => {
                  setError(false);
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    color: "#FFFFFF",
                    fontSize: windowHeight <= 1080 ? 12 : 16,
                  }}
                >
                  Ok
                </Text>
              </TouchableOpacity>
            </View>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

export default ProfileSettings;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    marginTop: "5%",
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    borderRadius: 50,
    width: "40%",
    height: "4%",
    padding: 15,
    shadowRadius: 10,
    shadowColor: "#F7567C",
    backgroundColor: "#F7567C",
    zIndex: 999,
  },
  button2: {
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    width: "8%",
    height: "2%",
    padding: 18,
    shadowRadius: 5,
    backgroundColor: "#3D9EBC",
  },

  gap: {
    height: 10,
  },
  field: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "96.5%",
    height: "100%",
    padding: 10,
    backgroundColor: "white",
  },
  subField: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "48%",
    height: "100%",
    padding: 10,
    backgroundColor: "white",
  },
  pwfield: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 5,
    width: "90%",
    height: "15%",
    padding: 15,
    backgroundColor: "white",
    flexDirection: "row",
    borderWidth: 1,
    borderRadius: 4,
  },
  textInputContainer: {
    marginBottom: 20,
    marginTop: 20,
  },

  roundedTextInput: {
    borderRadius: 10,
    backgroundColor: "white",
    borderWidth: 1,
    marginLeft: 20,
    marginRight: 20,
  },
});
