import {
  Animated,
  StyleSheet,
  Text,
  View,
  ImageBackground,
  Image,
  Easing,
  TouchableOpacity,
  TextInput,
  Dimensions,
  Alert,
  CheckBox,
  ScrollView,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import OTPTextView from "react-native-otp-textinput";
import DropDownPicker from "react-native-dropdown-picker";
import * as ImagePicker from "expo-image-picker";
import axios from "axios";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import NavigationBar from "../navigation-bar";
import config from "../../../config";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var logo = require("../../../assets/logo.png");
var loading = require("../../../assets/loading.gif");
var km = require("../../../assets/icon.png");
var previous = require("../../../assets/Previous.png");
var Policies = ({ route, navigation }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);
  const [errMsg, setErrorMsg] = useState("");

  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: "100%",
          height: "100%",
          overflow: "hidden",
          backgroundColor: "#FFFFFF",
        }}
      >
        <View style={{ position: "absolute", bottom: 50, left: 50 }}>
          <Text>{config.version}</Text>
        </View>
        <View
          style={{
            height: "11%",
            width: "100%",
            left: 0,
            top: 0,
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            alignItems: "center",
            justifyContent: "flex-start",

            flexDirection: "row",
          }}
        >
          <View style={{ height: "15%" }}></View>
          <Image
            source={logo}
            style={{
              width: 214,
              height: "70%",
              marginLeft: 20,
              resizeMode: "contain",
            }}
          />
          <View style={{ height: "15%" }}></View>
        </View>
        <TouchableOpacity
          style={{ width: "3%", height: 25 }}
          onPress={() => {
            navigation.pop();
          }}
        >
          <Image
            source={previous}
            style={{
              width: windowHeight <= 1080 ? 60 : 80,
              height: windowHeight <= 1080 ? 40 : 50,
              resizeMode: "contain",
              marginLeft: 20,
            }}
          />
        </TouchableOpacity>
        <Text
          style={{
            fontFamily: "Roboto_700Bold",
            fontSize: windowHeight <= 1080 ? 24 : 32,
            color: "#04475C",
            alignSelf: "center",
          }}
        >
          Policies and regulations
        </Text>
        <View
          style={{
            height: "70%",
            width: "90%",
            borderWidth: 1,
            borderColor: "#C4C4C4",
            marginTop: "1%",
            borderRadius: 4,
            alignItems: "center",
            alignSelf: "center",
            zIndex: -2,
          }}
        >
          <ScrollView style={{ height: "100%", width: "100%", padding: 50 }}>
            <Text
              style={{
                fontFamily: "Roboto_300Light",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",
              }}
            >
              Kairo Merch is the phenomenon that will change the whole market of
              print on demand in MENA, we will take it to a new level beyond
              your imagination. It is where you can show the world your Art, and
              make revenues while sitting on your couch. Each Artist will have
              his/her shop so that he/she can market for it, however, you do not
              need to do marketing as we will be handling that.
            </Text>

            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",

                marginTop: "2%",
              }}
            >
              Why sell Art on Kairo Merch?
            </Text>
            <Text
              style={{
                fontFamily: "Roboto_300Light",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",

                marginTop: "2%",
              }}
            >
              Kairo Merch is founded by a group of youth, we have more than
              three years of experience in the fields of e-commerce, printing on
              demand, dropshipping, and customer care.
            </Text>
            <Text
              style={{
                fontFamily: "Roboto_300Light",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",

                marginTop: "2%",
              }}
            >
              • We believe that your Art is precious and should be seen by the
              whole world, that is why we want to help you to focus on creating
              more awesome Art while earning revenues through our platform.
            </Text>
            <Text
              style={{
                fontFamily: "Roboto_300Light",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",
              }}
            >
              • Using high quality and Eco-friendly materials is one of our
              priorities
            </Text>
            <Text
              style={{
                fontFamily: "Roboto_300Light",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",
              }}
            >
              • Giving the customers an experience that they will never forget
              is our target!
            </Text>
            <Text
              style={{
                fontFamily: "Roboto_300Light",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",
              }}
            >
              • We care about the fine details in work the same way you care
              about your baby Art.
            </Text>
            <Text
              style={{
                fontFamily: "Roboto_300Light",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",
              }}
            >
              • Last but not least, kairo Merch is going to invade the MENA
              market so do not miss the opportunity and join the phenomenon!
            </Text>
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",

                marginTop: "2%",
              }}
            >
              How much can I earn?
            </Text>
            <Text
              style={{
                fontFamily: "Roboto_300Light",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",

                marginTop: "2%",
              }}
            >
              Earnings depend on many variables such as the product type and the
              printing area, the minimum percentage is 15% of the selling price
              and the maximum in some products could reach 90% of the selling
              price!
            </Text>
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",

                marginTop: "2%",
              }}
            >
              Do I still make a commission on discounted products?
            </Text>
            <Text
              style={{
                fontFamily: "Roboto_300Light",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",

                marginTop: "2%",
              }}
            >
              Yes, you will make commissions even on discounted products and
              this will bring you more revenues by selling more items.
            </Text>

            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",

                marginTop: "2%",
              }}
            >
              Who owns the copyright of the Art I upload?
            </Text>

            <Text
              style={{
                fontFamily: "Roboto_300Light",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",

                marginTop: "2%",
              }}
            >
              The copyright remains yours. You must be the owner of any image
              you upload. If you use an image you do not own the copyright of,
              we reserve the right to remove your products.
            </Text>

            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",

                marginTop: "2%",
              }}
            >
              How to find the website?
            </Text>

            <Text
              style={{
                fontFamily: "Roboto_300Light",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",

                marginTop: "2%",
              }}
            >
              We are in the pre-launching phase, once Artists join us and start
              uploading their Art we will move to phase B where the products
              will be ready with your awesome designs then we will publish the
              website.
            </Text>

            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",

                marginTop: "2%",
              }}
            >
              How many products do you have?
            </Text>

            <Text
              style={{
                fontFamily: "Roboto_300Light",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",

                marginTop: "2%",
              }}
            >
              We will have around 40 products from different categories,
              however, we will start by some of these products.
            </Text>

            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",

                marginTop: "2%",
              }}
            >
              Note that:
            </Text>
            <Text
              style={{
                fontFamily: "Roboto_300Light",
                fontSize: windowHeight <= 1080 ? 18 : 24,
                color: "#000000",

                marginTop: "2%",
              }}
            >
              • Sale Price depends on the product, printing area, material and,
              finishing.
              <br />• Sale price and commissions value can be changed at any
              time and we will keep you updated. <br />• Make sure your design
              is in 300 DPI <br />• Please use the board dimensions even if your
              Art is something small. <br />• We have the right to reject your
              Art if: <br />• The quality is bad <br />• The board dimensions
              are not the same as required
              <br />• The Art is not yours <br />• The Art has adult or racist
              content
            </Text>
          </ScrollView>
        </View>

        {isLoading ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFFCC",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "20%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 500,
                alignSelf: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Image
                source={loading}
                style={{
                  width: "90%",
                  height: "40%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "70%",
                }}
              />
            </View>
          </View>
        ) : (
          <View />
        )}

        {isError ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFF99",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "30%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 50,
                alignSelf: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 12 : 16,
                  color: "#333333",
                  marginTop: "10%",
                }}
              >
                {errMsg}
              </Text>

              <View style={{ height: "10%" }} />
            </View>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

export default Policies;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    marginTop: "2%",
    alignSelf: "center",
    justifyContent: "flex-start",
    alignItems: "center",
    borderRadius: 4,
    width: "15%",
    height: "4%",
    padding: 8,
    shadowRadius: 10,
    backgroundColor: "#3D9EBC",
    zIndex: 999,
    shadowColor: "#3D9EBC",

    flexDirection: "row",
  },
  button2: {
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    width: "8%",
    height: "2%",
    padding: 18,
    shadowRadius: 5,
    backgroundColor: "#3D9EBC",
  },

  gap: {
    height: 10,
  },
  field: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "96.5%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  subField: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "48%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  pwfield: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 5,
    width: "100%",
    height: "25%",
    padding: 15,
    backgroundColor: "white",
    flexDirection: "row",
  },
  textInputContainer: {
    marginBottom: 20,
    marginTop: 20,
  },

  roundedTextInput: {
    borderRadius: 10,
    backgroundColor: "white",
    borderWidth: 1,
    marginLeft: 20,
    marginRight: 20,
  },
});
