import {
  Text,
  View,
  StyleSheet,
  FlatList,
  Dimensions,
  Animated,
} from "react-native";
import React, { useCallback, useEffect, useRef, useState } from "react";
import { PanGestureHandler, State } from "react-native-gesture-handler";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;
let dropzoneHeight = 0;

let FlatItem = ({
  item,
  itemHeight,
  setItemHeight,
  itemWidth,
  setItemWidth,
  minLimitX,
  maxLimitX,
  minLimitY,
  maxLimitY,
  posxHanlder,
  posyHanlder,
}) => {
  let translateX = new Animated.Value(0);
  let translateY = new Animated.Value(0);
  let height = new Animated.Value(0);
  let onGestureEvent = Animated.event([
    {
      nativeEvent: {
        translationX: translateX,
        translationY: translateY,
      },
    },
  ]);
  let onGestureTopEvent = Animated.event([
    {
      nativeEvent: {
        translationY: height,
      },
    },
  ]);
  let _lastOffset = { x: 0, y: 0 };
  let onHandlerStateChange = (event) => {
    if (event.nativeEvent.oldState === State.ACTIVE) {
      _lastOffset.x += event.nativeEvent.translationX;
      _lastOffset.y += event.nativeEvent.translationY;
      translateX.setOffset(_lastOffset.x);
      translateX.setValue(0);
      translateY.setOffset(_lastOffset.y);
      translateY.setValue(0);
    }
    if (_lastOffset.x < minLimitX) {
      translateX.setOffset(minLimitX);
      translateX.setValue(0);

      _lastOffset.x = minLimitX;
    }

    if (_lastOffset.x > maxLimitX) {
      translateX.setOffset(maxLimitX);
      translateX.setValue(0);

      _lastOffset.x = maxLimitX;
    }

    if (_lastOffset.y < minLimitY) {
      translateY.setOffset(minLimitY);
      translateY.setValue(0);
      _lastOffset.y = minLimitY;
    }

    if (_lastOffset.y > maxLimitY) {
      translateY.setOffset(maxLimitY);
      translateY.setValue(0);
      _lastOffset.y = maxLimitY;
    }
    posxHanlder(
      parseInt(
        _lastOffset.x +
          (maxLimitX * 2 * 0.0004 * windowWidth) / 2 -
          itemWidth / 2
      )
    );
    posyHanlder(
      parseInt(
        _lastOffset.y +
          (maxLimitY * 2 * 0.0004 * windowHeight) / 2 -
          itemHeight / 2
      )
    );
    console.log(_lastOffset.x, _lastOffset.y);
  };
  useEffect(() => {
    posxHanlder(
      parseInt(
        _lastOffset.x +
          (maxLimitX * 2 * 0.0004 * windowWidth) / 2 -
          itemWidth / 2
      )
    );
    posyHanlder(
      parseInt(
        _lastOffset.y +
          (maxLimitY * 2 * 0.0004 * windowHeight) / 2 -
          itemHeight / 2
      )
    );
  }, []);
  return (
    <View>
      <PanGestureHandler
        onGestureEvent={onGestureEvent}
        onHandlerStateChange={onHandlerStateChange}
      >
        <Animated.View
          style={[
            {
              width: itemWidth,
              height: itemHeight,
              justifyContent: "center",
              alignItems: "center",
              backgroundColor: "#AAAAAACC",
              borderWidth: 1,
            },
            { transform: [{ translateX }, { translateY }] },
          ]}
        ></Animated.View>
      </PanGestureHandler>
    </View>
  );
};

export default FlatItem;
