import {
  Animated,
  StyleSheet,
  Text,
  View,
  ImageBackground,
  Image,
  Easing,
  TouchableOpacity,
  TextInput,
  Dimensions,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import config from "../../config";
import axios from "axios";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var logo = require("../../assets/logo.png");
var login_wp = require("../../assets/login_wp.png");
var previous = require("../../assets/Previous.png");
var eye = require("../../assets/eye.png");
var eye_slash = require("../../assets/eye-slash.png");
var loading = require("../../assets/loading.gif");
var km = require("../../assets/icon.png");
var Login = ({ route, navigation }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });
  const [hidePassword, setHidePassword] = useState(true);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);
  const [errMsg, setErrorMsg] = useState("");
  var login = () => {
    setLoading(true);
    console.log("Email: " + email + " Password: " + password);

    //Send information for login
    try {
      axios
        .post(config.host + "/api/account/login", {
          username: email,
          password: password,
        })
        .then((response) => {
          //Navigate to verification page
          console.log(response);
          config.token = response.data.token;
          try {
            axios
              .get(
                config.host +
                  "/api/account/getAccountStatus?token=" +
                  config.token
              )
              .then((response) => {
                //Navigate to verification page
                console.log(response);
                config.status = response.data.account_status;
                config.verified = response.data.mobile_verification;
                config.approved = response.data.account_approval;
                config.email = email;

                axios
                  .get(
                    config.host +
                      "/api/profiles/getArtistProfile/?token=" +
                      config.token
                  )
                  .then((profile_response) => {
                    console.log(profile_response);
                    config.plan = profile_response.data.commisionPlan;
                    document.cookie = "config=" + JSON.stringify(config) + ";";
                    if (config.verified) {
                      if (config.status == 0) {
                        navigation.navigate("Kairo Merch Complete Profile");
                      } else if (!config.approved) {
                        navigation.navigate("Kairo Merch Shop Setup");
                      } else if (config.status == 4) {
                        navigation.navigate("Kairo Merch Shop");
                      } else {
                        navigation.navigate("Kairo Merch Advisor", {
                          status: config.status,
                        });
                      }
                    } else {
                      navigation.navigate("Kairo Merch Mobile Verification");
                    }
                    setLoading(false);
                  })
                  .catch(() => {
                    setLoading(false);
                    setError(true);
                    setErrorMsg("Something went wrong");
                  });
              })
              .catch(() => {
                setLoading(false);
                setError(true);
                setErrorMsg("Something went wrong");
              });
          } catch (e) {
            setLoading(false);
            setError(true);

            var err = "";
            for (let x in JSON.parse(e.request.response)) {
              console.log(e.request.response.x);
              err = err + x + ": " + JSON.parse(e.request.response)[x] + "\n";
            }
            setErrorMsg(err);
            console.log(e);
          }

          // navigation.navigate("Kairo Merch Mobile Verification");
        })
        .catch((e) => {
          setLoading(false);
          setError(true);

          var err = "";
          for (let x in JSON.parse(e.request.response)) {
            console.log(e.request.response.x);
            err = err + x + ": " + JSON.parse(e.request.response)[x] + "\n";
          }
          setErrorMsg(err);
          console.log(e);
        });
    } catch (error) {
      console.log(error);
    }
  };

  const signupHandler = () => {
    navigation.navigate("Kairo Merch Signup");
  };
  useEffect(() => {
    var cookies = document.cookie.split(";");
    console.log(cookies);
    var needLogin = true;
    for (let i = 0; i < cookies.length; i = i + 1) {
      if (cookies[i].split("=")[0].includes("config")) {
        var configCookies = JSON.parse(cookies[i].split("=")[1]);
        if (configCookies.token != "") {
          config.token = configCookies.token;
          config.status = configCookies.status;
          config.verified = configCookies.verified;
          config.plan = configCookies.plan;
        }

        console.log(configCookies);
      }
    }
    if (config.token != "") {
      if (config.verified) {
        if (config.status == 0) {
          navigation.navigate("Kairo Merch Complete Profile");
        } else if (config.status == 4) {
          navigation.navigate("Kairo Merch Shop");
        } else {
          navigation.navigate("Kairo Merch Advisor", { status: config.status });
        }
      } else {
        navigation.navigate("Kairo Merch Mobile Verification");
      }
    }
  }, []);
  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: "100%",
          height: "100%",
          overflow: "hidden",
          backgroundColor: "#FFFFFF",
        }}
      >
        <View style={{ position: "absolute", bottom: 50, left: 50 }}>
          <Text>{config.version}</Text>
        </View>
        <View
          style={{
            height: 0.12 * windowHeight,
            width: "100%",
            left: 0,
            top: 0,
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            alignItems: "flex-start",
          }}
        >
          <View style={{ height: "15%" }}></View>
          <Image
            source={logo}
            style={{
              width: windowWidth * 0.12,
              height: windowHeight * 0.09,
              marginLeft: 20,
              resizeMode: "contain",
            }}
          />
        </View>
        <View
          style={{
            width: "100%",
            height: 0.89 * windowHeight,
            flexDirection: "row",
          }}
        >
          <View style={{ width: 0.5 * windowWidth, height: "100%" }}>
            <TouchableOpacity
              style={{ width: "100%", height: 0.05 * windowHeight }}
              onPress={() => {
                navigation.pop();
              }}
            >
              <Image
                source={previous}
                style={{
                  width: windowHeight <= 1080 ? 60 : 80,
                  height: windowHeight <= 1080 ? 40 : 50,
                  resizeMode: "contain",
                  marginLeft: 20,
                }}
              />
            </TouchableOpacity>
            <View
              style={{
                alignItems: "center",

                height: 0.84 * windowHeight,
              }}
            >
              <View style={{ height: 0.2 * 0.89 * windowHeight }} />
              <Text
                style={{
                  fontFamily: "Roboto_700Bold",
                  fontSize: windowHeight <= 1080 ? 24 : 32,
                  color: "#04475C",
                }}
              >
                Welcome back!
              </Text>

              <View
                style={{
                  width: "50%",
                  alignSelf: "flex-start",
                  marginLeft: "25%",
                  marginTop: 0.04 * windowHeight,
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    fontSize: windowHeight <= 1080 ? 16 : 24,
                    color: "#04475C",
                  }}
                >
                  Login
                </Text>
                <View
                  style={{
                    flexDirection: "row",
                    marginTop: 0.02 * windowHeight,
                    width: "100%",
                  }}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_400Regular",
                      fontSize: windowHeight <= 1080 ? 12 : 16,
                      color: "#333333",
                    }}
                  >
                    Need an account?
                  </Text>

                  <TouchableOpacity onPress={signupHandler}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        color: "#C54160",
                        fontSize: windowHeight <= 1080 ? 12 : 16,
                        marginLeft: 10,
                      }}
                    >
                      Sign up
                    </Text>
                  </TouchableOpacity>
                </View>
                <View style={{ height: 0.04 * windowHeight }} />
                <View style={styles.field}>
                  <TextInput
                    style={{ borderColor: "black" }}
                    autoCompleteType={"email"}
                    autoCorrect={false}
                    placeholder={"Email"}
                    onChangeText={(newText) => setEmail(newText)}
                  ></TextInput>
                </View>

                <View style={styles.pwfield}>
                  <TextInput
                    style={{ borderColor: "white", width: "90%" }}
                    autoCompleteType={"password"}
                    secureTextEntry={hidePassword}
                    autoCorrect={false}
                    placeholder={"Password"}
                    onChangeText={(newText) => setPassword(newText)}
                  ></TextInput>
                  <TouchableOpacity
                    style={{
                      height: "100%",
                      width: "10%",
                      justifyContent: "center",
                    }}
                    onPress={() => {
                      setHidePassword(!hidePassword);
                    }}
                  >
                    <Image
                      source={hidePassword ? eye_slash : eye}
                      style={{
                        width: "100%",
                        height: 12,
                        resizeMode: "contain",
                      }}
                    />
                  </TouchableOpacity>
                </View>
              </View>

              <TouchableOpacity style={styles.button} onPress={login}>
                <Text
                  style={{
                    fontFamily: "Roboto_400Regular",
                    fontSize: windowHeight <= 1080 ? 16 : 20,
                    color: "#FFFFFF",
                  }}
                >
                  Log in
                </Text>
              </TouchableOpacity>
            </View>
          </View>
          <View style={{ width: 0.5 * windowWidth, height: "100%" }}>
            <Image
              source={login_wp}
              style={{
                width: 0.5 * windowWidth,
                height: 0.89 * windowHeight,
                resizeMode: "contain",
              }}
            />
          </View>
        </View>
        {isLoading ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFFCC",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "20%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 500,
                alignSelf: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Image
                source={loading}
                style={{
                  width: "90%",
                  height: "40%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "70%",
                }}
              />
            </View>
          </View>
        ) : (
          <View />
        )}

        {isError ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFF99",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "30%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 50,
                alignSelf: "center",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              />
              <View
                style={{
                  height: "10%",
                  width: "90%",
                  marginTop: "10%",
                  alignSelf: "center",
                  alignItems: "center",
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_400Regular",
                    fontSize: windowHeight <= 1080 ? 16 : 20,
                    color: "#333333",
                    alignSelf: "center",
                  }}
                >
                  {errMsg}
                </Text>
              </View>

              <View style={{ height: "10%" }} />
              <TouchableOpacity
                style={{
                  width: "20%",
                  alignSelf: "center",
                  height: "10%",

                  backgroundColor: "#888888",
                  alignItems: "center",
                  justifyContent: "center",
                  borderRadius: 10,
                }}
                onPress={() => {
                  setError(false);
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    color: "#FFFFFF",
                    fontSize: windowHeight <= 1080 ? 12 : 16,
                  }}
                >
                  Ok
                </Text>
              </TouchableOpacity>
            </View>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

export default Login;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    marginTop: 0.06 * windowHeight,
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    borderRadius: 50,
    width: "40%",
    height: 0.04 * windowHeight,
    padding: 15,
    shadowRadius: 10,
    backgroundColor: "#F7567C",
  },
  button2: {
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    width: "8%",
    height: "2%",
    padding: 18,
    shadowRadius: 5,
    backgroundColor: "#3D9EBC",
  },

  gap: {
    height: 10,
  },
  field: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 20,
    width: "100%",
    height: "25%",
    padding: 10,
    backgroundColor: "white",
    borderWidth: 1,
    borderColor: "grey",
    borderRadius: 4,
  },
  pwfield: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 20,
    width: "100%",
    height: "25%",
    padding: 10,
    backgroundColor: "white",
    flexDirection: "row",
    borderWidth: 1,
    borderColor: "grey",
    borderRadius: 4,
  },
});
