var unchecked = require("../../../assets/unchecked.png");
var no_products = require("../../../assets/Torso.png");
var tshirt_front = require("../../../assets/White_Tshirt.jpg");
var check = {};
var CreateProductMain = ({ route, navigation, setIsview, updateProducts }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);

  const [errMsg, setErrorMsg] = useState("");
  const [imageurl, setImageURL] = useState(null);
  const [search, setSearch] = useState("");
  const [designSearch, setDesignSearch] = useState("");
  const [openCategories, setOpenCategories] = useState(false);
  const [categories, setCategories] = useState([]);
  const [selectedCategory, setSelectedCategory] = useState("");
  const [products, setProducts] = useState([]);
  const [rawProductsState, setRawProducts] = useState([]);
  const [rawMockupsState, setRawMockups] = useState([]);
  const [searchedProducts, setSearchedProducts] = useState([]);
  var [checklist, setChecklist] = useState({});
  const [designs, setDesigns] = useState([]);
  const [rawDesignsState, setRawDesigns] = useState([]);
  const [step, setStep] = useState(1);
  const [pickedDesign, setPickedDesign] = useState({});
  const [angle, setAngle] = useState("20");
  const [wrapping, setWrapping] = useState("50");
  const [width, setWidth] = useState(50);
  const [height, setHeight] = useState(50);
  const [posx, setPosx] = useState(100);
  const [posy, setPosy] = useState(100);
  const [viewedProduct, setViewedProduct] = useState(null);
  const [previewImage, setPreviewImage] = useState({});
  const [itemHeight, setItemHeight] = useState(100);
  const [itemWidth, setItemWidth] = useState(100);
  const [reduction, setReduction] = useState("50");
  var getDesigns = () => {
    setLoading(true);
    try {
      axios
        .get(config.host + "/api/mocks/GetAllArtFiles/?token=" + config.token)
        .then((response) => {
          var rawDesigns = response.data;
          console.log(rawDesigns);

          setRawDesigns(rawDesigns);
          structureDesigns(rawDesigns);
        });
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  var structureDesigns = (rawDesigns) => {
    var filteredDesigns = [];
    for (var i = 0; i < rawDesigns.length; i = i + 1) {
      if (rawDesigns[i].designNameEN.includes(designSearch)) {
        filteredDesigns.push(rawDesigns[i]);
      }
    }

    var structuredDesigns = [];
    var designsRow = { lineId: row, data: [] };
    var row = 0;
    for (var i = 0; i < filteredDesigns.length; i = i + 1) {
      if (i % 6 == 0) {
        designsRow = { lineId: row, data: [] };
      }
      designsRow.data[i % 6] = filteredDesigns[i];
      if (i % 6 == 5) {
        structuredDesigns[row] = designsRow;
        row++;
      }
    }
    if (i % 6 < 6 && i % 6 != 0) {
      structuredDesigns[row] = designsRow;
    }
    setDesigns(structuredDesigns);
    console.log(structuredDesigns);

    setLoading(false);
  };

  // var structureProducts = (rawProducts) => {
  //   var filteredProducts = [];
  //   for (var i = 0; i < rawProducts.length; i = i + 1) {
  //     filteredProducts.push(rawProducts[i]);
  //   }

  //   var structuredProducts = [];
  //   var ProductsRow = { lineId: row, data: [] };
  //   var row = 0;
  //   for (var i = 0; i < filteredProducts.length; i = i + 1) {
  //     if (i % 6 == 0) {
  //       ProductsRow = { lineId: row, data: [] };
  //     }
  //     ProductsRow.data[i % 6] = filteredProducts[i];
  //     if (i % 6 == 5) {
  //       structuredProducts[row] = ProductsRow;
  //       row++;
  //     }
  //   }
  //   if (i % 6 < 6 && i % 6 != 0) {
  //     structuredProducts[row] = ProductsRow;
  //   }
  //   setProducts(structuredProducts);
  //   console.log(structuredProducts);

  //   setLoading(false);
  // };

  var structureMockups = (rawMockups) => {
    var filteredMockups = [];
    let updateCategories = ["All"];
    for (var i = 0; i < rawMockups.length; i = i + 1) {
      if (
        rawMockups[i].productName.includes(search) &&
        (rawMockups[i].sub_category == selectedCategory ||
          selectedCategory == "")
      ) {
        filteredMockups.push(rawMockups[i]);
      }
      if (!updateCategories.includes(rawMockups[i].category)) {
        updateCategories.push(rawMockups[i].sub_category);
      }
    }

    setCategories(updateCategories);

    var structuredMockups = [];
    var MockupsRow = { lineId: row, data: [] };
    var row = 0;
    for (var i = 0; i < filteredMockups.length; i = i + 1) {
      if (i % 6 == 0) {
        MockupsRow = { lineId: row, data: [] };
      }
      MockupsRow.data[i % 6] = filteredMockups[i];
      if (i % 6 == 5) {
        structuredMockups[row] = MockupsRow;
        row++;
      }
    }
    if (i % 6 < 6 && i % 6 != 0) {
      structuredMockups[row] = MockupsRow;
    }
    setSearchedProducts(structuredMockups);
    console.log(structuredMockups);

    setLoading(false);
  };
  var getMockups = () => {
    getDesigns();
    setLoading(true);
    try {
      axios.get(config.host + "/api/mocks/GetAllMocks/").then((response) => {
        var rawMockups = response.data;
        console.log(rawMockups);

        setRawMockups(rawMockups);
        structureMockups(rawMockups);
      });
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  var getProduct = (recDesign) => {
    setLoading(true);
    var mockIDs = [];
    for (let x in checklist) {
      if (checklist[x]) {
        mockIDs.push(x);
      }
    }
    console.log(mockIDs);
    console.log(pickedDesign);
    console.log({
      mockIDS: mockIDs,
      productStatus: "D",
      angle: angle,
      wraping: wrapping,
    });
    try {
      axios
        .post(
          config.host +
            "/api/mocks/createProduct/?token=" +
            config.token +
            "&designID=" +
            recDesign.designID,
          {
            mockIDS: mockIDs,
            productStatus: "D",
            angle: angle,
            wraping: wrapping,
            posx: parseInt(posx / (0.0004 * windowWidth)),
            posy: parseInt(posy / (0.0004 * windowHeight)),
            height: parseInt(itemHeight / (0.0004 * windowHeight)),
            width: parseInt(itemWidth / (0.0004 * windowWidth)),
            reduction: "10",
            pattern: "t",
          }
        )
        .then((response) => {
          console.log(response);
          setRawProducts(response.data);
          setPreviewImage({
            uri: response.data[0].productPhoto,
          });
          setLoading(false);
          setViewedProduct(response.data[0]);
          if (step == 3) {
            var productIndex = rawProductsState.findIndex(
              (element) =>
                element.ArtistProductID == viewedProduct.ArtistProductID
            );
            console.log(response.data[0]);

            var updatedProducts = rawProductsState;
            console.log(productIndex);
            console.log("updated product", updatedProducts);
            updatedProducts[productIndex].productPhoto =
              viewedProduct.productPhoto + "?" + new Date();
            setRawProducts(updatedProducts);

            setViewedProduct(updatedProducts[productIndex]);

            setPreviewImage({
              uri:
                updatedProducts[productIndex].productPhoto + "?" + new Date(),
            });
          }
          // structureProducts(response.data);
        })
        .catch((error) => {
          console.log(error);
          setLoading(false);
        });
    } catch (error) {
      console.log(error);
      setLoading(false);
    }

    if (step < 3) {
      nextStep();
    }
  };

  var deleteProducts = () => {
    for (var i = 0; i < rawProductsState.length; i = i + 1) {
      try {
        axios
          .delete(
            config.host +
              "/api/mocks/deleteProduct/?productID=" +
              rawProductsState[i].ArtistProductID
          )
          .then((response) => {});
      } catch (error) {
        console.log(error);
        setLoading(false);
      }
    }
  };

  var updateProduct = () => {
    setLoading(true);
    try {
      axios
        .patch(
          config.host +
            "/api/mocks/updateProduct/?productID=" +
            viewedProduct.ArtistProductID,
          {
            angle: angle,
            wraping: wrapping,
            posx: parseInt(posx / (0.0004 * windowWidth)),
            posy: parseInt(posy / (0.0004 * windowHeight)),
            height: parseInt(itemHeight / (0.0004 * windowHeight)),
            width: parseInt(itemWidth / (0.0004 * windowWidth)),
            reduction: reduction,
            pattern: "t",
          }
        )
        .then((response) => {
          console.log(response);
          setRawProducts(response.data);
          setPreviewImage({
            uri: response.data[0].productPhoto,
          });
          setLoading(false);
          setViewedProduct(response.data[0]);
          if (step == 3) {
            var productIndex = rawProductsState.findIndex(
              (element) =>
                element.ArtistProductID == viewedProduct.ArtistProductID
            );
            console.log(response.data[0]);

            var updatedProducts = rawProductsState;
            console.log(productIndex);
            console.log("updated product", response.data[0]);
            updatedProducts[productIndex].productPhoto =
              viewedProduct.productPhoto + "?" + new Date();
            setRawProducts(updatedProducts);

            setViewedProduct(response.data[0]);

            setPreviewImage({
              uri: response.data[0].productPhoto + "?" + new Date(),
            });
          }
          // structureProducts(response.data);
        })
        .catch((error) => {
          console.log(error);
          setLoading(false);
        });
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  var saveAsDraft = () => {
    setLoading(true);
    for (let i = 0; i < rawProductsState.length; i = i + 1) {
      console.log(rawProductsState[i].ArtistProductID);
      try {
        axios
          .patch(
            config.host +
              "/api/mocks/setProductStatus/?productID=" +
              rawProductsState[i].ArtistProductID,
            {
              productStatus: "D",
            }
          )
          .then((response) => {
            setLoading(false);
            updateProducts();
            setIsview(false);
          });
      } catch (e) {
        console.log(e);
        setLoading(false);
      }
    }
  };
  var requestApproval = () => {
    for (let i = 0; i < rawProductsState.length; i = i + 1) {
      try {
        axios
          .patch(
            config.host +
              "/api/mocks/setProductStatus/?productID=" +
              rawProductsState[i].ArtistProductID,
            {
              productStatus: "PE",
            }
          )
          .then((response) => {
            setLoading(false);
            updateProducts();
            setIsview(false);
          });
      } catch (e) {
        console.log(e);
        setLoading(false);
      }
    }
  };
  useEffect(getMockups, [search, selectedCategory]);
  useEffect(() => {
    structureDesigns(rawDesignsState);
  }, [designSearch]);
  // useEffect(() => {
  //   let filteredProducts = [];
  //   let structuredProducts = [];
  //   let updateCategories = ["All"];
  //   for (var i = 0; i < searchedProducts.length; i++) {
  //     for (var j = 0; j < 4; j++) {
  //       if (
  //         searchedProducts[i].data[j].productName.includes(search) &&
  //         (searchedProducts[i].data[j].category == selectedCategory ||
  //           selectedCategory == "")
  //       ) {
  //         filteredProducts.push(searchedProducts[i].data[j]);
  //       }
  //       if (!updateCategories.includes(searchedProducts[i].data[j].category)) {
  //         updateCategories.push(searchedProducts[i].data[j].category);
  //       }
  //     }
  //   }
  //   setCategories(updateCategories);
  //   var row = 0;
  //   var productsRow = { lineId: row, data: [] };
  //   for (var i = 0; i < filteredProducts.length; i++) {
  //     if (i % 4 == 0) {
  //       productsRow = { lineId: row, data: [] };
  //     }
  //     productsRow.data[i % 4] = filteredProducts[i];
  //     if (i % 4 == 3) {
  //       structuredProducts[row] = productsRow;
  //       row++;
  //     }
  //   }
  //   if (i % 4 < 4 && i % 4 != 0) {
  //     structuredProducts[row] = productsRow;
  //   }
  //   console.log(structuredProducts);
  //   console.log(filteredProducts);
  //   setSearchedProducts(structuredProducts);
  // }, [search, selectedCategory]);

  var nextStep = () => {
    if (step == 1) {
      getDesigns();
    }
    setStep(step + 1);
  };

  var skipToAdvisor = () => {
    config.status = 3;
    document.cookie = "config=" + JSON.stringify(config) + ";";
    navigation.navigate("Kairo Merch Advisor", { status: 3 });
  };
  useEffect(() => {
    console.log("x: ", posx, ", y: ", posy);
  }, [posx, posy]);

  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: "100%",
          height: "100%",
          borderRadius: 20,
          backgroundColor: "#FFFFFF",
        }}
      >
        <TouchableOpacity
          style={{ width: "3%", height: 25 }}
          onPress={() => {
            if (step == 1) {
              setIsview(false);
            } else if (step == 3) {
              deleteProducts();
              var newStep = step - 1;
              setStep(newStep);
            } else {
              var newStep = step - 1;
              setStep(newStep);
            }
          }}
        >
          <Image
            source={previous}
            style={{
              width: windowHeight <= 1080 ? 60 : 80,
              height: windowHeight <= 1080 ? 40 : 50,
              resizeMode: "contain",
              marginLeft: 20,
            }}
          />
        </TouchableOpacity>

        <View
          style={{
            width: "100%",
            height: "97%",
            flexDirection: "row",

            zIndex: -1,
          }}
        >
          <View style={{ width: "100%", height: "100%", alignItems: "center" }}>
            <View
              style={{
                flexDirection: "row",
                height: "3%",
                width: "100%",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Text
                style={{
                  marginLeft: "0.5%",
                  fontFamily: "Roboto_700Bold",
                  fontSize: windowHeight <= 1080 ? 24 : 32,
                  color: "#04475C",
                }}
              >
                Create products
              </Text>
            </View>

            {step == 1 ? (
              <View
                style={{
                  height: "75%",
                  width: "90%",
                  borderWidth: 1,
                  borderColor: "#C4C4C4",
                  marginTop: "1%",
                  borderRadius: 4,
                  alignItems: "center",
                }}
              >
                <View
                  style={{
                    flexDirection: "row",
                    height: "3%",
                    width: "100%",
                    alignItems: "center",
                    justifyContent: "center",
                    marginTop: "1%",
                  }}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 16 : 24,
                      color: "#04475C",
                    }}
                  >
                    1st
                  </Text>
                  <Text
                    style={{
                      marginLeft: "0.5%",
                      fontFamily: "Roboto_400Regular",
                      fontSize: windowHeight <= 1080 ? 16 : 24,
                      color: "#04475C",
                    }}
                  >
                    Choose from a wide range of products
                  </Text>
                </View>

                <View
                  style={{
                    height: "8%",
                    width: "60%",
                    borderWidth: 1,
                    borderColor: "#C4C4C4",
                    marginTop: "1%",
                    borderRadius: 6,
                    flexDirection: "row",
                  }}
                >
                  <TouchableOpacity
                    style={{
                      width: "25%",
                      height: "100%",
                      flexDirection: "row",
                      justifyContent: "space-around",
                      alignItems: "center",
                      borderRightWidth: 1,
                      borderRightColor: "#D4D4D4",
                      backgroundColor: "#ACE2F358",
                    }}
                    onPress={() => {
                      console.log(categories);
                      setOpenCategories(!openCategories);
                    }}
                  >
                    <View
                      style={{
                        width: "80%",
                        height: "100%",

                        justifyContent: "center",
                      }}
                    >
                      <Text
                        style={{
                          marginLeft: "2%",
                          fontFamily: "Roboto_500Medium",
                          fontSize: windowHeight <= 1080 ? 12 : 18,
                          color: "#04475C",
                        }}
                      >
                        Product Category
                      </Text>
                      <Text
                        style={{
                          marginLeft: "3%",

                          fontFamily: "Roboto_400Regular",
                          fontSize: windowHeight <= 1080 ? 8 : 12,
                          color: "#04475C",
                        }}
                      >
                        {selectedCategory == ""
                          ? "All"
                          : " " + selectedCategory}
                      </Text>
                    </View>

                    <Image
                      source={down}
                      style={{
                        width: "8%",
                        height: "60%",
                        resizeMode: "contain",
                        alignSelf: "center",
                      }}
                    />
                  </TouchableOpacity>
                  {openCategories ? (
                    <View
                      style={{
                        position: "absolute",
                        top: "100%",
                        height: 0.25 * windowHeight,
                        width: "18%",
                        backgroundColor: "#FFFFFF88",
                        borderBottomLeftRadius: 4,
                        borderBottomRightRadius: 4,
                        alignItems: "center",
                        borderLeftWidth: 1,
                        borderRightWidth: 1,
                        borderBottomWidth: 1,
                        borderColor: "#ACE2F388",
                        zIndex: 0,
                        shadowRadius: 4,
                      }}
                    >
                      <ScrollView
                        style={{
                          width: "100%",
                          height: "90%",
                          zIndex: 0,
                        }}
                      >
                        {categories.map((category) => (
                          <TouchableOpacity
                            key={category}
                            style={{
                              backgroundColor:
                                selectedCategory == category
                                  ? "#ACE2F388"
                                  : "#FFFFFF88",
                              borderBottomWidth: 1,
                              height: 0.04 * windowHeight,
                              justifyContent: "center",
                            }}
                            onPress={() => {
                              if (category == "All") {
                                setSelectedCategory("");
                              } else {
                                setSelectedCategory(category);
                              }
                              setOpenCategories(false);
                            }}
                          >
                            <Text
                              style={{
                                marginLeft: "5%",
                                fontFamily: "Roboto_400Regular",
                                fontSize: windowHeight <= 1080 ? 12 : 14,
                                color: "#04475C",
                              }}
                            >
                              {category}
                            </Text>
                          </TouchableOpacity>
                        ))}
                        <View style={{ height: "10%" }} />
                      </ScrollView>
                    </View>
                  ) : (
                    <View />
                  )}

                  <Image
                    source={magnify}
                    style={{
                      width: "5%",
                      height: "100%",
                      resizeMode: "contain",
                    }}
                  />
                  <TextInput
                    style={{
                      borderColor: "black",
                      height: "100%",
                      width: "100%",
                      fontSize: windowHeight <= 1080 ? 12 : 18,
                      fontFamily: "Roboto_400Regular",
                      padding: 5,
                    }}
                    autoCorrect={false}
                    placeholder={"Search.."}
                    onChangeText={(newText) => setSearch(newText)}
                  ></TextInput>
                </View>

                <View style={{ height: "2%" }} />
                <View
                  style={{
                    height: "75%",
                    width: "98%",
                    alignItems: "center",
                    justifyContent: "center",
                    marginTop: "2%",
                    borderRadius: 20,
                    zIndex: -1,
                  }}
                >
                  <ScrollView
                    style={{ width: "100%", height: "100%", borderRadius: 20 }}
                  >
                    {searchedProducts.map((line) => (
                      <View
                        style={{
                          height: windowHeight <= 1080 ? 200 : 400,
                          width: "100%",

                          flexDirection: "row",
                          justifyContent: "space-around",

                          marginBottom: "1%",
                          marginTop: "1%",
                        }}
                        key={line.lineId}
                      >
                        {line.data.map((product) => (
                          <View
                            style={{
                              width: "15%",
                              height: "100%",
                            }}
                          >
                            <TouchableOpacity
                              style={{
                                width: windowWidth * 0.1,
                                height: "auto",
                                borderRadius: 15,
                                alignItems: "center",
                                justifyContent: "center",
                                shadowRadius: 20,
                                shadowColor: "#C4C4C4",
                              }}
                              key={product.id}
                              onPress={() => {
                                var id = product.productID;
                                check[id] = !check[id];
                                let checked = Object.assign({}, {}, check);
                                setChecklist(checked);
                                console.log(check);
                              }}
                            >
                              <View style={{ height: "80%", width: "100%" }}>
                                <View
                                  style={{
                                    position: "absolute",
                                    left: "5%",
                                    top: "5%",
                                    height: "10%",
                                    width: "10%",
                                    zIndex: 999,
                                  }}
                                >
                                  {checklist[product.productID] ? (
                                    <Image
                                      source={checked}
                                      style={{
                                        height: "100%",
                                        width: "100%",
                                        resizeMode: "contain",
                                      }}
                                    />
                                  ) : (
                                    <Image
                                      source={unchecked}
                                      style={{
                                        height: "100%",
                                        width: "100%",
                                        resizeMode: "contain",
                                      }}
                                    />
                                  )}
                                </View>
                                <Image
                                  source={
                                    product.image == ""
                                      ? placeholder
                                      : { uri: product.productPhoto }
                                  }
                                  style={{
                                    width: windowWidth * 0.1,
                                    height: windowWidth * 0.1,
                                    resizeMode: "contain",
                                    borderRadius: 15,
                                  }}
                                />
                              </View>
                              <View
                                style={{
                                  height: "20%",
                                  width: "100%",
                                  backgroundColor: "#FFFFFF",
                                  alignItems: "center",
                                  justifyContent: "center",
                                  borderBottomLeftRadius: 15,
                                  borderBottomRightRadius: 15,
                                }}
                              >
                                <Text
                                  style={{
                                    fontFamily: "Roboto_700Bold",
                                    fontSize: windowHeight <= 1080 ? 14 : 18,
                                    color: "#333333",
                                  }}
                                >
                                  {product.productName}
                                </Text>
                              </View>
                            </TouchableOpacity>
                          </View>
                        ))}
                      </View>
                    ))}
                  </ScrollView>
                </View>
              </View>
            ) : step == 2 ? (
              <View
                style={{
                  height: "75%",
                  width: "90%",
                  borderWidth: 1,
                  borderColor: "#C4C4C4",
                  marginTop: "1%",
                  borderRadius: 4,
                  alignItems: "center",
                }}
              >
                <View
                  style={{
                    flexDirection: "row",
                    height: "3%",
                    width: "100%",
                    alignItems: "center",
                    justifyContent: "center",
                    marginTop: "1%",
                  }}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 16 : 24,
                      color: "#04475C",
                    }}
                  >
                    2nd
                  </Text>
                  <Text
                    style={{
                      marginLeft: "0.5%",
                      fontFamily: "Roboto_400Regular",
                      fontSize: windowHeight <= 1080 ? 16 : 24,
                      color: "#04475C",
                    }}
                  >
                    Choose art file
                  </Text>
                </View>

                <View
                  style={{
                    height: "5%",
                    width: "60%",
                    borderWidth: 1,
                    borderColor: "#C4C4C4",
                    marginTop: "1%",
                    borderRadius: 6,
                    flexDirection: "row",
                  }}
                >
                  <Image
                    source={magnify}
                    style={{
                      width: "5%",
                      height: "100%",
                      resizeMode: "contain",
                    }}
                  />
                  <TextInput
                    style={{
                      borderColor: "black",
                      height: "100%",
                      width: "100%",
                      fontSize: windowHeight <= 1080 ? 12 : 18,
                      fontFamily: "Roboto_400Regular",
                      padding: 5,
                    }}
                    autoCorrect={false}
                    placeholder={"Search.."}
                    onChangeText={(newText) => setDesignSearch(newText)}
                  ></TextInput>
                </View>

                <View style={{ height: "2%" }} />
                <View
                  style={{
                    height: "75%",
                    width: "98%",
                    alignItems: "center",
                    justifyContent: "center",
                    marginTop: "2%",
                    borderRadius: 20,
                    zIndex: -1,
                  }}
                >
                  <ScrollView
                    style={{ width: "100%", height: "100%", borderRadius: 20 }}
                  >
                    {designs.map((line) => (
                      <View
                        style={{
                          height: windowWidth * 0.1,
                          width: "100%",
                          flexDirection: "row",
                          justifyContent: "space-between",
                          marginLeft: windowWidth * 0.02,
                          marginBottom: "3%",
                          marginTop: "1%",
                        }}
                        key={line.lineId}
                      >
                        {line.data.map((design) => (
                          <View
                            style={{
                              width: "15%",
                              height: "100%",
                            }}
                          >
                            <TouchableOpacity
                              style={{
                                width: windowWidth * 0.1,
                                height: "auto",
                                borderRadius: 15,
                                alignItems: "center",
                                justifyContent: "center",
                                shadowRadius: 20,
                                shadowColor: "#C4C4C4",
                              }}
                              key={design.designID}
                              onPress={() => {
                                console.log(design);
                                setPickedDesign(design);
                                getProduct(design);
                              }}
                            >
                              <View style={{ height: "80%", width: "100%" }}>
                                <Image
                                  source={
                                    design.designFiles == ""
                                      ? placeholder
                                      : { uri: design.designFiles }
                                  }
                                  style={{
                                    width: windowWidth * 0.1,
                                    height: windowWidth * 0.1,
                                    resizeMode: "contain",
                                    borderRadius: 15,
                                  }}
                                />
                              </View>
                              <View
                                style={{
                                  height: "20%",
                                  width: "100%",
                                  backgroundColor: "#FFFFFF",
                                  alignItems: "center",
                                  justifyContent: "center",
                                  borderBottomLeftRadius: 15,
                                  borderBottomRightRadius: 15,
                                }}
                              >
                                <Text
                                  style={{
                                    fontFamily: "Roboto_700Bold",
                                    fontSize: windowHeight <= 1080 ? 14 : 18,
                                    color: "#333333",
                                  }}
                                >
                                  {design.designNameEN}
                                </Text>
                              </View>
                            </TouchableOpacity>
                          </View>
                        ))}
                      </View>
                    ))}
                  </ScrollView>
                </View>
              </View>
            ) : step == 3 ? (
              <View
                style={{
                  height: "75%",
                  width: "90%",
                  borderWidth: 1,
                  borderColor: "#C4C4C4",
                  marginTop: "1%",
                  borderRadius: 4,
                  alignItems: "center",
                }}
              >
                <View
                  style={{
                    flexDirection: "row",
                    height: "3%",
                    width: "100%",
                    alignItems: "center",
                    justifyContent: "center",
                    marginBottom: "1%",
                    marginTop: "1%",
                  }}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 16 : 24,
                      color: "#04475C",
                    }}
                  >
                    3rd
                  </Text>
                  <Text
                    style={{
                      marginLeft: "0.5%",
                      fontFamily: "Roboto_400Regular",
                      fontSize: windowHeight <= 1080 ? 16 : 24,
                      color: "#04475C",
                    }}
                  >
                    Products review
                  </Text>
                </View>

                <View
                  style={{
                    height: "92%",
                    width: "100%",
                    borderTopWidth: 1,
                    borderColor: "#C4C4C4",
                    flexDirection: "row",

                    zIndex: -1,
                  }}
                >
                  <View
                    style={{
                      width: windowWidth * 0.12,
                      height: "100%",
                      alignItems: "center",
                      justifyContent: "center",
                      backgroundColor: "#3D9EBC88",
                    }}
                  >
                    <ScrollView
                      style={{
                        width: "100%",
                        height: "100%",
                      }}
                    >
                      {rawProductsState.map((product) => (
                        <View
                          style={{
                            height: windowWidth * 0.125,
                            width: "100%",
                          }}
                          key={product.ArtistProductID}
                        >
                          <View
                            style={{
                              height: windowWidth * 0.0125,
                              width: "100%",
                            }}
                          />
                          <View
                            style={{
                              height: windowWidth * 0.1,
                              width: "100%",
                              alignSelf: "center",
                            }}
                          >
                            <TouchableOpacity
                              style={{
                                width: windowWidth * 0.1,
                                height: "auto",
                                borderRadius: 15,
                                alignItems: "center",
                                justifyContent: "center",
                                shadowRadius: 20,
                                shadowColor: "#C4C4C4",
                                alignSelf: "center",
                                backgroundColor: "white",
                                borderWidth: viewedProduct == product ? 1 : 0,
                              }}
                              key={product.ArtistProductID}
                              onPress={() => {
                                console.log(product);
                                setViewedProduct(product);
                                setPreviewImage({
                                  uri: product.productPhoto,
                                });
                              }}
                            >
                              <View style={{ height: "80%", width: "100%" }}>
                                <Image
                                  source={
                                    product.productPhoto == ""
                                      ? placeholder
                                      : {
                                          uri: product.productPhoto,
                                        }
                                  }
                                  style={{
                                    width: windowWidth * 0.085,
                                    height: windowWidth * 0.085,
                                    resizeMode: "contain",
                                    borderRadius: 15,
                                  }}
                                />
                              </View>
                              <View
                                style={{
                                  height: "20%",
                                  width: "100%",
                                  backgroundColor: "#FFFFFF",
                                  alignItems: "center",
                                  justifyContent: "center",
                                  borderBottomLeftRadius: 15,
                                  borderBottomRightRadius: 15,
                                }}
                              >
                                <Text
                                  style={{
                                    fontFamily: "Roboto_700Bold",
                                    fontSize: windowHeight <= 1080 ? 14 : 18,
                                    color: "#333333",
                                  }}
                                >
                                  {product.productName}
                                </Text>
                              </View>
                            </TouchableOpacity>
                          </View>

                          <View
                            style={{
                              height: windowWidth * 0.0125,
                              width: "100%",
                            }}
                          />
                        </View>
                      ))}
                    </ScrollView>
                  </View>

                  {viewedProduct ? (
                    <View
                      style={{
                        height: "80%",
                        width: "90%",
                        justifyContent: "center",
                        flexDirection: "row",
                        alignItems: "center",
                      }}
                    >
                      {viewedProduct.sub_category == "Tshirt" ? (
                        <View
                          style={{
                            width: "50%",
                            height: "90%",
                            alignItems: "center",
                            justifyContent: "center",
                            alignSelf: "center",
                          }}
                        >
                          <View
                            style={{
                              width: "100%",
                              height: "100%",
                              alignItems: "center",
                              justifyContent: "center",
                              flexDirection: "row",
                            }}
                          >
                            <View
                              style={{
                                height: "80%",
                                width: "50%",
                                alignItems: "center",
                                justifyContent: "center",
                                marginTop: "2%",
                              }}
                            >
                              <Image
                                source={tshirt_front}
                                style={{
                                  width: parseInt(0.2 * windowWidth),
                                  height: parseInt(0.2 * windowHeight),
                                  resizeMode: "contain",
                                }}
                              />
                              <View
                                style={{ position: "absolute", zIndex: 999 }}
                              >
                                <FlatItem
                                  item={data[0]}
                                  itemHeight={itemHeight}
                                  setItemHeight={setItemHeight}
                                  itemWidth={itemWidth}
                                  setItemWidth={setItemWidth}
                                  maxLimitY={869 / 2}
                                  minLimitY={-869 / 2}
                                  maxLimitX={670 / 2}
                                  minLimitX={-670 / 2}
                                  posxHanlder={setPosx}
                                  posyHanlder={setPosy}
                                />
                              </View>
                            </View>

                            <View
                              style={{
                                width: "50%",
                                height: "50%",
                                alignItems: "center",
                                justifyContent: "center",
                              }}
                            >
                              <View
                                style={{
                                  width: "100%",
                                  height: "2%",
                                  alignItems: "center",
                                  marginTop: "1%",
                                  marginBottom: "2%",
                                  justifyContent: "center",
                                }}
                              >
                                <Text
                                  style={{
                                    fontFamily: "Roboto_700Bold",
                                    fontSize: windowHeight * 0.014,
                                    color: "#333333",
                                  }}
                                >
                                  Art height
                                </Text>
                              </View>

                              <Slider
                                style={{
                                  width: "50%",
                                  height: "1%",
                                  zIndex: -1,
                                }}
                                minimumValue={10}
                                maximumValue={1000}
                                minimumTrackTintColor="#3D9EBC"
                                maximumTrackTintColor="#000000"
                                thumbTintColor="#EB5276"
                                value={itemHeight}
                                onValueChange={(value) => {
                                  setItemHeight(parseInt(value));
                                }}
                              />

                              <View
                                style={{
                                  width: "100%",
                                  height: "2%",
                                  alignItems: "center",
                                  marginBottom: "2%",
                                  justifyContent: "center",
                                }}
                              >
                                <Text
                                  style={{
                                    fontFamily: "Roboto_700Bold",
                                    fontSize: windowHeight * 0.014,
                                    color: "#333333",
                                  }}
                                >
                                  Art width
                                </Text>
                              </View>
                              <Slider
                                style={{
                                  width: "50%",
                                  height: "1%",
                                  zIndex: -1,
                                }}
                                minimumValue={10}
                                maximumValue={1000}
                                minimumTrackTintColor="#3D9EBC"
                                maximumTrackTintColor="#000000"
                                thumbTintColor="#EB5276"
                                value={wrapping}
                                onValueChange={(value) => {
                                  console.log(value);
                                  setItemWidth(parseInt(value));
                                }}
                              />
                            </View>
                          </View>
                          <TouchableOpacity
                            style={{
                              width: "40%",
                              alignSelf: "center",
                              height: "8%",

                              backgroundColor: "#EB5276",
                              alignItems: "center",
                              justifyContent: "center",
                              borderRadius: 10,
                              marginTop: "8%",
                            }}
                            onPress={() => {
                              updateProduct();
                            }}
                          >
                            <Text
                              style={{
                                fontFamily: "Roboto_700Bold",
                                color: "#FFFFFF",
                                fontSize: windowHeight <= 1080 ? 12 : 16,
                              }}
                            >
                              Update Product
                            </Text>
                          </TouchableOpacity>
                        </View>
                      ) : viewedProduct.sub_category == "Mug" ? (
                        <View
                          style={{
                            width: "50%",
                            height: "60%",
                            alignItems: "center",
                            justifyContent: "center",
                          }}
                        >
                          <View
                            style={{
                              width: "100%",
                              height: "1%",
                              alignItems: "center",
                              marginTop: "1%",
                              marginBottom: "6%",
                            }}
                          >
                            <Text
                              style={{
                                fontFamily: "Roboto_700Bold",
                                fontSize: windowHeight * 0.015,
                                color: "#333333",
                              }}
                            >
                              Wrapping Percentage
                            </Text>
                            <Text
                              style={{
                                fontFamily: "Roboto_500Medium",
                                fontSize: windowHeight * 0.013,
                                color: "#EB5276",
                                backgroundColor: "#AAAAAAAA",
                                borderRadius: 50,
                                marginTop: "1%",
                                padding: 10,
                                marginBottom: 10,
                              }}
                            >
                              {parseInt(wrapping)}%
                            </Text>
                          </View>

                          <Slider
                            style={{ width: "50%", height: "1%" }}
                            minimumValue={10}
                            maximumValue={100}
                            minimumTrackTintColor="#3D9EBC"
                            maximumTrackTintColor="#000000"
                            thumbTintColor="#EB5276"
                            value={wrapping}
                            onValueChange={(value) => {
                              console.log(value);
                              setWrapping(parseInt(value).toString());
                            }}
                          />

                          <View
                            style={{
                              width: "100%",
                              height: "1%",
                              alignItems: "center",
                              marginBottom: "6%",
                            }}
                          >
                            <Text
                              style={{
                                fontFamily: "Roboto_700Bold",
                                fontSize: windowHeight * 0.015,
                                color: "#333333",
                              }}
                            >
                              Rotation Angle
                            </Text>
                            <Text
                              style={{
                                fontFamily: "Roboto_500Medium",
                                fontSize: windowHeight * 0.013,
                                color: "#EB5276",
                                backgroundColor: "#AAAAAAAA",
                                borderRadius: 50,
                                marginTop: "1%",
                                padding: 10,
                              }}
                            >
                              {parseInt(angle)}°
                            </Text>
                          </View>
                          <Slider
                            style={{ width: "50%", height: "1%" }}
                            minimumValue={0}
                            maximumValue={360}
                            minimumTrackTintColor="#3D9EBC"
                            maximumTrackTintColor="#000000"
                            thumbTintColor="#EB5276"
                            value={angle}
                            onValueChange={(value) => {
                              console.log(value);
                              setAngle(parseInt(value).toString());
                            }}
                          />
                          <TouchableOpacity
                            style={{
                              width: "20%",
                              alignSelf: "center",
                              height: "10%",

                              backgroundColor: "#EB5276",
                              alignItems: "center",
                              justifyContent: "center",
                              borderRadius: 10,
                              marginTop: "5%",
                            }}
                            onPress={() => {
                              updateProduct();
                            }}
                          >
                            <Text
                              style={{
                                fontFamily: "Roboto_700Bold",
                                color: "#FFFFFF",
                                fontSize: windowHeight <= 1080 ? 12 : 16,
                              }}
                            >
                              Update Product
                            </Text>
                          </TouchableOpacity>
                        </View>
                      ) : viewedProduct.sub_category == "Phone_Case" ? (
                        <View
                          style={{
                            width: "50%",
                            height: "60%",
                            alignItems: "center",
                            justifyContent: "center",
                          }}
                        >
                          <View
                            style={{
                              width: "100%",
                              height: "1%",
                              alignItems: "center",
                              marginTop: "1%",
                              marginBottom: "6%",
                            }}
                          >
                            <Text
                              style={{
                                fontFamily: "Roboto_700Bold",
                                fontSize: windowHeight * 0.015,
                                color: "#333333",
                              }}
                            >
                              Reduction Percentage
                            </Text>
                            <Text
                              style={{
                                fontFamily: "Roboto_500Medium",
                                fontSize: windowHeight * 0.013,
                                color: "#EB5276",
                                backgroundColor: "#AAAAAAAA",
                                borderRadius: 50,
                                marginTop: "1%",
                                padding: 10,
                                marginBottom: 10,
                              }}
                            >
                              {parseInt(reduction)}%
                            </Text>
                          </View>

                          <Slider
                            style={{ width: "50%", height: "1%" }}
                            minimumValue={1}
                            maximumValue={100}
                            minimumTrackTintColor="#3D9EBC"
                            maximumTrackTintColor="#000000"
                            thumbTintColor="#EB5276"
                            value={wrapping}
                            onValueChange={(value) => {
                              console.log(value);
                              setReduction(parseInt(value).toString());
                            }}
                          />

                          <TouchableOpacity
                            style={{
                              width: "20%",
                              alignSelf: "center",
                              height: "10%",

                              backgroundColor: "#EB5276",
                              alignItems: "center",
                              justifyContent: "center",
                              borderRadius: 10,
                              marginTop: "5%",
                            }}
                            onPress={() => {
                              updateProduct();
                            }}
                          >
                            <Text
                              style={{
                                fontFamily: "Roboto_700Bold",
                                color: "#FFFFFF",
                                fontSize: windowHeight <= 1080 ? 12 : 16,
                              }}
                            >
                              Update Product
                            </Text>
                          </TouchableOpacity>
                        </View>
                      ) : (
                        <View />
                      )}

                      <View
                        style={{
                          width: "50%",
                          height: "100%",
                          alignItems: "center",

                          justifyContent: "center",
                        }}
                      >
                        <Image
                          source={previewImage}
                          style={{
                            width: "100%",
                            height: "100%",
                            resizeMode: "contain",
                            borderRadius: 15,
                          }}
                        />
                      </View>
                    </View>
                  ) : (
                    <View
                      style={{
                        height: "90%",
                        width: "80%",
                        alignItems: "center",
                        justifyContent: "center",
                        marginTop: "2%",
                      }}
                    >
                      <Text
                        style={{
                          fontFamily: "Roboto_700Bold",
                          fontSize: windowHeight <= 1080 ? 20 : 32,
                          color: "#C54160",
                        }}
                      >
                        Choose a product to view
                      </Text>
                      <Image
                        source={no_products}
                        style={{
                          marginTop: "5%",
                          width: "40%",
                          height: "40%",
                          resizeMode: "contain",
                          alignSelf: "center",
                        }}
                      />
                    </View>
                  )}
                </View>
              </View>
            ) : (
              <View />
            )}

            <View style={{ height: "1%" }} />
            <View
              style={{
                height: "10%",
                width: "100%",
                flexDirection: "row",
              }}
            >
              <View style={{ width: "7%" }} />
              {step == 3 ? (
                <TouchableOpacity style={styles.button2} onPress={saveAsDraft}>
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 14 : 18,
                      color: "#333333",
                    }}
                  >
                    Save as draft
                  </Text>
                </TouchableOpacity>
              ) : (
                <View style={{ width: "18%" }} />
              )}

              <View
                style={{
                  width: "50%",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    fontSize: windowHeight <= 1080 ? 14 : 20,
                    color: "#333333",
                  }}
                >
                  {step == 1
                    ? "Select a mockup or more that you want to apply your art on"
                    : step == 2
                    ? "Choose an art to be applied on the selected mockups"
                    : step == 3
                    ? "Review your products"
                    : ""}
                </Text>
              </View>
              {step == 1 ? (
                <TouchableOpacity style={styles.button} onPress={nextStep}>
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 14 : 18,
                      color: "#FFFFFF",
                    }}
                  >
                    Next
                  </Text>
                </TouchableOpacity>
              ) : step == 3 ? (
                <TouchableOpacity
                  style={styles.button}
                  onPress={requestApproval}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 14 : 18,
                      color: "#FFFFFF",
                    }}
                  >
                    Request approval to publish
                  </Text>
                </TouchableOpacity>
              ) : (
                <View style={styles.button} />
              )}
              <View style={{ width: "7%" }} />
            </View>
          </View>
        </View>

        {isLoading ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFFCC",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "20%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 500,
                alignSelf: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Image
                source={loading}
                style={{
                  width: "90%",
                  height: "40%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "70%",
                }}
              />
            </View>
          </View>
        ) : (
          <View />
        )}

        {isError ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFF99",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "30%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 50,
                alignSelf: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 12 : 16,
                  color: "#333333",
                  marginTop: "10%",
                }}
              >
                {errMsg}
              </Text>

              <View style={{ height: "10%" }} />
            </View>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

