import {
  Animated,
  StyleSheet,
  Text,
  View,
  ImageBackground,
  Image,
  Easing,
  TouchableOpacity,
  TextInput,
  Dimensions,
  Alert,
  CheckBox,
  ScrollView,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import OTPTextView from "react-native-otp-textinput";
import DropDownPicker from "react-native-dropdown-picker";
import * as ImagePicker from "expo-image-picker";
import axios from "axios";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import NavigationBar from "./navigation-bar";
import config from "../../config";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var logo = require("../../assets/logo.png");
var loading = require("../../assets/loading.gif");
var km = require("../../assets/icon.png");
var trends = require("../../assets/trends.png");

var down = require("../../assets/down-arrow.png");
var magnify = require("../../assets/magnify.png");
var no_orders = require("../../assets/no-orders.png");

var Orders = ({ route, navigation }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);
  const [errMsg, setErrorMsg] = useState("");
  const [openCategories, setOpenCategories] = useState(false);
  const [categories, setCategories] = useState(["All", "Tshirt", "Mug"]);
  const [selectedCategory, setSelectedCategory] = useState("");
  const [orderType, setOrderType] = useState("All");
  const [orders, setOrders] = useState([]);
  const [search, setSearch] = useState("");
  const status = config.status;
  var createProduct = () => {};
  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: "100%",
          height: "100%",
          overflow: "hidden",
          backgroundColor: "#FFFFFF",
        }}
      >
        <View style={{ position: "absolute", bottom: 50, left: 50 }}>
          <Text>{config.version}</Text>
        </View>
        <View
          style={{
            height: "11%",
            width: "100%",
            left: 0,
            top: 0,
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            alignItems: "center",
            justifyContent: "flex-start",

            flexDirection: "row",
          }}
        >
          <View style={{ height: "15%" }}></View>
          <Image
            source={logo}
            style={{
              width: 214,
              height: "70%",
              marginLeft: 20,
              resizeMode: "contain",
            }}
          />
          <View style={{ height: "15%" }}></View>
          <View
            style={{
              height: "30%",
              width: "8%",
              alignItems: "center",
              position: "absolute",
              right: "2%",
            }}
          >
            <NavigationBar
              walletIsDimmed={true}
              notifications={[]}
              navigation={navigation}
            />
          </View>
        </View>
        <View
          style={{
            height: "5%",
            width: "100%",
            alignItems: "center",
            justifyContent: "center",
            flexDirection: "row",
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            zIndex: -1,
          }}
        >
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Kairo Merch Shop");
            }}
          >
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 14 : 20,
                color: "#04475C",
              }}
            >
              My shop
            </Text>
          </TouchableOpacity>

          <View style={{ width: "8%" }} />

          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Kairo Merch Art Files");
            }}
          >
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 14 : 20,
                color: "#04475C",
              }}
            >
              Art files
            </Text>
          </TouchableOpacity>
          <View style={{ width: "8%" }} />
          <TouchableOpacity>
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 14 : 20,
                color: "#C54160",
              }}
            >
              Orders
            </Text>
          </TouchableOpacity>
        </View>
        {/* <TouchableOpacity
                  style={{ width: "3%", height: 25 }}
                  onPress={() => {
                    navigation.pop();
                  }}
                >
                  <Image
                    source={previous}
                    style={{
                      width: windowHeight <= 1080 ? 60 : 80,
                      height: windowHeight <= 1080 ? 40 : 50,
                      resizeMode: "contain",
                      marginLeft: 20,
                    }}
                  />
                </TouchableOpacity> */}
        <TouchableOpacity
          style={{
            width: "10%",
            height: "5%",
            alignSelf: "flex-start",
            marginLeft: "2%",
            marginTop: "10%",
            position: "absolute",
            zIndex: 999,
          }}
        >
          <Image
            source={trends}
            style={{ width: "100%", height: "100%", resizeMode: "contain" }}
          />
        </TouchableOpacity>
        <View
          style={{
            height: "4%",
            width: "60%",
            borderWidth: 1,
            borderColor: "#C4C4C4",
            marginTop: "3%",
            borderRadius: 6,
            flexDirection: "row",
            alignSelf: "center",
            shadowRadius: 4,
            zIndex: -1,
          }}
        >
          <Image
            source={magnify}
            style={{
              width: "5%",
              height: "100%",
              resizeMode: "contain",
            }}
          />
          <TextInput
            style={{
              borderColor: "black",
              height: "100%",
              width: "100%",
              fontSize: windowHeight <= 1080 ? 12 : 18,
              fontFamily: "Roboto_400Regular",
              padding: 5,
            }}
            autoCorrect={false}
            placeholder={"Search.."}
            onChangeText={(newText) => setSearch(newText)}
          ></TextInput>
        </View>
        {status != 4 ? (
          <TouchableOpacity
            style={{
              position: "absolute",
              right: "2%",
              top: "21%",
              width: "10%",
              height: "5%",
              borderWidth: 1,
              borderRadius: 50,
              flexDirection: "row",
              alignItems: "center",
              shadowRadius: 10,
              shadowColor: "#EB5276",
              zIndex: -1,
            }}
            onPress={() => {
              navigation.navigate("Kairo Merch Advisor", { status: status });
            }}
          >
            <View
              style={{
                height: "100%",
                width: "22%",
                borderRadius: 50,
                backgroundColor: "#F7567C",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Text
                style={{
                  fontFamily: "Roboto_700Bold",
                  fontSize: windowHeight <= 1080 ? 12 : 16,
                  color: "#FFFFFF",
                }}
              >
                {status == 1
                  ? "25%"
                  : status == 2
                  ? "50%"
                  : status == 3
                  ? "75%"
                  : "100%"}
              </Text>
            </View>
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 12 : 16,
                color: "#3D9EBC",
                marginLeft: "5%",
              }}
            >
              Kairomerch Advisor
            </Text>
          </TouchableOpacity>
        ) : (
          <View />
        )}
        <View
          style={{
            marginTop: "2%",
            height: "4%",
            width: "25%",
            alignSelf: "center",
            alignItems: "center",
            flexDirection: "row",
            justifyContent: "space-between",
            zIndex: -2,
          }}
        >
          <TouchableOpacity
            style={{
              height: "100%",
              width: "20%",
              backgroundColor: orderType == "All" ? "#EB5276" : "#FFB1C366",
              alignItems: "center",
              justifyContent: "center",
              borderRadius: 50,
            }}
            onPress={() => {
              setOrderType("All");
            }}
          >
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 16 : 24,
                color: orderType == "All" ? "#FFFFFF" : "#000000",
              }}
            >
              All
            </Text>
          </TouchableOpacity>
          <TouchableOpacity
            style={{
              height: "100%",
              width: "35%",
              backgroundColor: orderType == "Ordered" ? "#EB5276" : "#FFB1C366",
              alignItems: "center",
              justifyContent: "center",
              borderRadius: 50,
            }}
            onPress={() => {
              setOrderType("Ordered");
            }}
          >
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 16 : 24,
                color: orderType == "Ordered" ? "#FFFFFF" : "#000000",
              }}
            >
              Ordered
            </Text>
          </TouchableOpacity>
          <TouchableOpacity
            style={{
              height: "100%",
              width: "35%",
              backgroundColor:
                orderType == "Delivered" ? "#EB5276" : "#FFB1C366",
              alignItems: "center",
              justifyContent: "center",
              borderRadius: 50,
            }}
            onPress={() => {
              setOrderType("Delivered");
            }}
          >
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 16 : 24,
                color: orderType == "Delivered" ? "#FFFFFF" : "#000000",
              }}
            >
              Delivered
            </Text>
          </TouchableOpacity>
        </View>
        <View
          style={{
            height: "52%",
            width: "90%",
            borderWidth: 1,
            borderColor: "#C4C4C4",
            marginTop: "1%",
            borderRadius: 4,
            alignItems: "center",
            alignSelf: "center",
            zIndex: -2,
          }}
        >
          {orders.length == 0 ? (
            <View
              style={{
                height: "90%",
                width: "100%",
                alignItems: "center",
                justifyContent: "flex-start",
                marginTop: "2%",
              }}
            >
              <Image
                source={no_orders}
                style={{
                  marginTop: "5%",
                  width: "60%",
                  height: "60%",
                  resizeMode: "contain",
                  alignSelf: "center",
                }}
              />
            </View>
          ) : (
            <View />
          )}
        </View>

        {isLoading ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFFCC",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "20%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 500,
                alignSelf: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Image
                source={loading}
                style={{
                  width: "90%",
                  height: "40%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "70%",
                }}
              />
            </View>
          </View>
        ) : (
          <View />
        )}

        {isError ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFF99",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "30%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 50,
                alignSelf: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 12 : 16,
                  color: "#333333",
                  marginTop: "10%",
                }}
              >
                {errMsg}
              </Text>

              <View style={{ height: "10%" }} />
            </View>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

export default Orders;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    marginTop: "2%",
    alignSelf: "center",
    justifyContent: "flex-start",
    alignItems: "center",
    borderRadius: 4,
    width: "15%",
    height: "4%",
    padding: 8,
    shadowRadius: 10,
    backgroundColor: "#3D9EBC",
    zIndex: 999,
    shadowColor: "#3D9EBC",

    flexDirection: "row",
  },
  button2: {
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    width: "8%",
    height: "2%",
    padding: 18,
    shadowRadius: 5,
    backgroundColor: "#3D9EBC",
  },

  gap: {
    height: 10,
  },
  field: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "96.5%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  subField: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "48%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  pwfield: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 5,
    width: "100%",
    height: "25%",
    padding: 15,
    backgroundColor: "white",
    flexDirection: "row",
  },
  textInputContainer: {
    marginBottom: 20,
    marginTop: 20,
  },

  roundedTextInput: {
    borderRadius: 10,
    backgroundColor: "white",
    borderWidth: 1,
    marginLeft: 20,
    marginRight: 20,
  },
});
