import {
  Animated,
  StyleSheet,
  Text,
  View,
  ImageBackground,
  Image,
  Easing,
  TouchableOpacity,
  TextInput,
  Dimensions,
  Alert,
  CheckBox,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import OTPTextView from "react-native-otp-textinput";
import DropDownPicker from "react-native-dropdown-picker";
import * as ImagePicker from "expo-image-picker";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import NavigationBar from "../navigation-bar";
import config from "../../../config";
import axios from "axios";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var logo = require("../../../assets/logo.png");
var previous = require("../../../assets/Previous.png");
var loading = require("../../../assets/loading.gif");
var km = require("../../../assets/icon.png");
var notification = require("../../../assets/notification.png");
var placeholder = require("../../../assets/upload.png");
var AddArt = ({ route, navigation }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);

  const [errMsg, setErrorMsg] = useState("");
  const [imageurl, setImageURL] = useState(null);
  const [imageView, setImageView] = useState(null);
  const [designName, setDesignName] = useState("");
  const [designNameArabic, setDesignNameArabic] = useState("");
  const [hashtags, setHashtags] = useState("");
  const [hashtagsArabic, setHashtagsArabic] = useState("");

  var saveArt = () => {
    setLoading(true);
    //Save to BE

    try {
      let form_data = new FormData();
      form_data.append("designNameEN", designName);
      form_data.append("designNameAR", designNameArabic);
      form_data.append("tagsEN", hashtags);
      form_data.append("tagsAR", hashtagsArabic);
      form_data.append("designFiles", imageurl, imageurl.name);

      axios
        .post(
          config.host + "/api/mocks/createDesign/?token=" + config.token,
          form_data,
          {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          }
        )
        .then((response) => {
          setLoading(false);

          config.status = 3;
          document.cookie = "config=" + JSON.stringify(config) + ";";
          navigation.navigate("Kairo Merch Advisor", { status: 3 });
        })
        .catch((err) => {
          setLoading(false);
          setError(true);
          setErrorMsg(err.request.response);
        });
    } catch (e) {
      setLoading(false);
      setError(true);
      setErrorMsg(e.message);
    }
  };

  var skipToAdvisor = () => {
    config.status = 2;
    document.cookie = "config=" + JSON.stringify(config) + ";";
    navigation.navigate("Kairo Merch Advisor", { status: 2 });
  };
  function getBase64(file) {
    var reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function () {
      setImageView(reader.result);
    };
    reader.onerror = function (error) {
      console.log("Error: ", error);
    };
  }

  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: "100%",
          height: "100%",
          overflow: "hidden",
          backgroundColor: "#FFFFFF",
        }}
      >
        <View
          style={{
            height: "11%",
            width: "100%",
            left: 0,
            top: 0,
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            alignItems: "center",
            justifyContent: "flex-start",

            flexDirection: "row",
          }}
        >
          <View style={{ height: "15%" }}></View>
          <Image
            source={logo}
            style={{
              width: 214,
              height: "70%",
              marginLeft: 20,
              resizeMode: "contain",
            }}
          />
          <View style={{ height: "15%" }}></View>
          <View
            style={{
              height: "30%",
              width: "8%",
              alignItems: "center",
              position: "absolute",
              right: "2%",
            }}
          >
            <NavigationBar
              walletIsDimmed={true}
              navigation={navigation}
              notifications={[
                {
                  icon: notification,
                  text: "Welcome abroad! your account has been verified!",
                  date: Date.now(),
                },
              ]}
            />
          </View>
        </View>
        <TouchableOpacity
          style={{ width: "3%", height: 25 }}
          onPress={() => {
            navigation.pop();
          }}
        >
          <Image
            source={previous}
            style={{
              width: windowHeight <= 1080 ? 60 : 80,
              height: windowHeight <= 1080 ? 40 : 50,
              resizeMode: "contain",
              marginLeft: 20,
            }}
          />
        </TouchableOpacity>

        <View
          style={{
            width: "100%",
            height: "87%",
            flexDirection: "row",

            zIndex: -1,
          }}
        >
          <View style={{ width: "100%", height: "100%", alignItems: "center" }}>
            <View
              style={{
                flexDirection: "row",
                height: "3%",
                width: "100%",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 16 : 24,
                  color: "#04475C",
                }}
              >
                Step 3
              </Text>
              <Text
                style={{
                  marginLeft: "0.5%",
                  fontFamily: "Roboto_700Bold",
                  fontSize: windowHeight <= 1080 ? 24 : 32,
                  color: "#04475C",
                }}
              >
                Upload your first art
              </Text>
            </View>

            <View
              style={{
                flexDirection: "row",
                height: "3%",
                width: "100%",
                alignItems: "center",
                justifyContent: "center",
                marginTop: "2%",
              }}
            >
              <Text
                style={{
                  marginLeft: "0.5%",
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 16 : 24,
                  color: "#04475C",
                }}
              >
                Add art details
              </Text>
            </View>

            <View style={{ height: "2%" }} />
            <View
              style={{
                height: "60%",
                width: "100%",
                flexDirection: "row",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <View
                style={{
                  height: "90%",
                  width: "60%",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <View style={styles.field}>
                  {designName != "" ? (
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      Design name
                    </Text>
                  ) : (
                    <View />
                  )}

                  <TextInput
                    style={{
                      borderColor: "black",
                      height: "60%",
                      padding: 5,
                      borderWidth: 1,
                      borderRadius: 4,
                    }}
                    placeholder={"Design name"}
                    onChangeText={(newText) => setDesignName(newText)}
                  ></TextInput>
                </View>

                <View style={styles.field}>
                  {designNameArabic != "" ? (
                    <Text
                      style={{
                        fontWeight: "bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      اسم التصميم
                    </Text>
                  ) : (
                    <View />
                  )}

                  <TextInput
                    style={{
                      borderColor: "black",
                      height: "60%",
                      padding: 5,
                      direction: "rtl",
                      borderWidth: 1,
                      borderRadius: 4,
                    }}
                    placeholder={"اسم التصميم"}
                    onChangeText={(newText) => setDesignNameArabic(newText)}
                  ></TextInput>
                </View>
                <View style={styles.field}>
                  {hashtags != "" ? (
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                        borderWidth: 1,
                        borderRadius: 4,
                      }}
                    >
                      #Hashtags
                    </Text>
                  ) : (
                    <View />
                  )}

                  <TextInput
                    style={{
                      borderColor: "black",
                      height: "60%",
                      padding: 5,
                      borderWidth: 1,
                      borderRadius: 4,
                    }}
                    placeholder={"#Hashtags"}
                    onChangeText={(newText) => setHashtags(newText)}
                  ></TextInput>
                </View>
                <View style={styles.field}>
                  {hashtagsArabic != "" ? (
                    <Text
                      style={{
                        fontWeight: "bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      #هاشتاجات
                    </Text>
                  ) : (
                    <View />
                  )}

                  <TextInput
                    style={{
                      borderColor: "black",
                      height: "60%",
                      padding: 5,
                      direction: "rtl",
                      borderWidth: 1,
                      borderRadius: 4,
                    }}
                    placeholder={"#هاشتاجات"}
                    onChangeText={(newText) => setHashtagsArabic(newText)}
                  ></TextInput>
                </View>
              </View>
              <input
                id="filePicker"
                type="file"
                hidden="true"
                name="image_url"
                accept="image/jpeg,image/png,image/gif"
                onChange={(e) => {
                  getBase64(e.target.files[0]);
                  setImageURL(e.target.files[0]);
                }}
              />
              <View
                style={{
                  height: "100%",
                  width: "40%",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <TouchableOpacity
                  style={{
                    height: "100%",
                    width: "100%",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <label
                    htmlFor="filePicker"
                    style={{
                      width: "100%",
                      height: "100%",
                    }}
                  >
                    <Image
                      source={imageView ? imageView : placeholder}
                      style={{
                        width: "100%",
                        height: "90%",
                        resizeMode: "contain",
                        borderRadius: 20,
                      }}
                    />
                  </label>
                </TouchableOpacity>
              </View>
            </View>

            <View style={{ height: "5%" }} />
            <View
              style={{
                height: "10%",
                width: "100%",
                flexDirection: "row",
              }}
            >
              <View style={{ width: "7%" }} />
              <TouchableOpacity style={styles.button2} onPress={skipToAdvisor}>
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    fontSize: windowHeight <= 1080 ? 14 : 18,
                    color: "#333333",
                  }}
                >
                  Skip for now
                </Text>
              </TouchableOpacity>
              <View style={{ width: "50%" }} />
              <TouchableOpacity style={styles.button} onPress={saveArt}>
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    fontSize: windowHeight <= 1080 ? 14 : 18,
                    color: "#FFFFFF",
                  }}
                >
                  Next
                </Text>
              </TouchableOpacity>
              <View style={{ width: "7%" }} />
            </View>
          </View>
        </View>

        {isLoading ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFFCC",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "20%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 500,
                alignSelf: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Image
                source={loading}
                style={{
                  width: "90%",
                  height: "40%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "70%",
                }}
              />
            </View>
          </View>
        ) : (
          <View />
        )}

        {isError ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFF99",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "30%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 50,
                alignSelf: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 12 : 16,
                  color: "#333333",
                  marginTop: "10%",
                }}
              >
                {errMsg}
              </Text>

              <View style={{ height: "10%" }} />
            </View>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

export default AddArt;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    marginTop: "2%",
    alignItems: "center",
    justifyContent: "center",
    borderRadius: 50,
    width: "18%",
    height: "6%",
    padding: 18,
    shadowRadius: 10,
    backgroundColor: "#F7567C",
    zIndex: 999,
  },
  button2: {
    marginTop: "2%",
    alignItems: "center",
    justifyContent: "center",
    borderRadius: 50,
    width: "18%",
    height: "6%",
    padding: 18,
    shadowRadius: 10,
    backgroundColor: "#FFFFFF",
    borderColor: "black",
    borderWidth: 1,
    zIndex: 999,
  },

  gap: {
    height: 10,
  },
  field: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "80%",
    height: "18%",
    padding: 8,
    backgroundColor: "white",
  },
  subField: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "48%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  pwfield: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 5,
    width: "100%",
    height: "25%",
    padding: 15,
    backgroundColor: "white",
    flexDirection: "row",
  },
  textInputContainer: {
    marginBottom: 20,
    marginTop: 20,
  },

  roundedTextInput: {
    borderRadius: 10,
    backgroundColor: "white",
    borderWidth: 1,
    marginLeft: 20,
    marginRight: 20,
  },
});
