import {
  StyleSheet,
  Text,
  View,
  Image,
  TouchableOpacity,
  TextInput,
  Dimensions,
  CheckBox,
  ScrollView,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import OTPTextView from "react-native-otp-textinput";
import DropDownPicker from "react-native-dropdown-picker";
import axios from "axios";
import * as ImagePicker from "expo-image-picker";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import config from "../../config";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var logo = require("../../assets/logo.png");
var previous = require("../../assets/Previous.png");
var loading = require("../../assets/loading.gif");
var km = require("../../assets/icon.png");
var numbers = require("../../assets/profile-numbers.png");
var pp = require("../../assets/profile.png");
var pencil = require("../../assets/pencil.png");

var CompleteProfile = ({ route, navigation }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const [imageurl, setImageURL] = useState(null);
  const [imageView, setImageView] = useState(null);
  const [shopName, setShopName] = useState("");
  const [protofolioLink, setProtofolioLink] = useState("");
  const [about, setAbout] = useState("");
  const [otherSite, setOtherSite] = useState("");
  const [isAgree, setIsAgree] = useState(false);
  const [aboutUs, setAboutUs] = useState("");

  //Dropdown country
  const [openCountry, setOpenCountry] = useState(false);
  const [valueCountry, setValueCountry] = useState([]);
  const [itemsCountry, setItemsCountry] = useState([
    { label: "Egypt", value: "egypt" },
    { label: "KSA", value: "ksa" },
    { label: "UAE", value: "uae" },
  ]);

  const [country, setCountry] = useState(null);

  //Dropdown city
  const [openCity, setOpenCity] = useState(false);
  const [valueCity, setValueCity] = useState([]);
  const [itemsCity, setItemsCity] = useState([]);
  const [citiesList, setCityList] = useState({});
  //Dropdown other websites
  const [openOther, setOpenOther] = useState(false);
  const [valueOther, setValueOther] = useState([]);
  const [itemsOther, setItemsOther] = useState([
    { label: "Yes", value: true },
    { label: "No", value: false },
  ]);

  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);

  const [errMsg, setErrorMsg] = useState("");
  function getBase64(file) {
    var reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function () {
      setImageView(reader.result);
    };
    reader.onerror = function (error) {
      console.log("Error: ", error);
    };
  }

  var setupShopHandler = () => {
    setLoading(true);
    if (
      about == "" ||
      aboutUs == "" ||
      shopName == "" ||
      valueCountry == "" ||
      protofolioLink == ""
    ) {
      console.log(
        about,
        aboutUs,
        shopName,
        valueCountry,
        valueCity,
        protofolioLink,
        valueOther
      );
      setLoading(false);
      setError(true);
      setErrorMsg("Please complete all required fields");
      return;
    }
    if (imageurl == null) {
      setLoading(false);
      setError(true);
      setErrorMsg("Please upload a shop image first.");
      return;
    }
    if (!isAgree) {
      setLoading(false);
      setError(true);
      setErrorMsg("Please agree to Policies and Regulations first.");
      return;
    }
    try {
      let form_data = new FormData();
      form_data.append("aboutArtist", about);
      form_data.append("howDidYouHearAboutUs", aboutUs);
      form_data.append("shopName", shopName);
      form_data.append("country", valueCountry);
      form_data.append("city", valueCity);
      form_data.append("portofolioLink", protofolioLink);
      form_data.append("otherWebsite_bool", valueOther);
      form_data.append("otherWebsite", otherSite);
      form_data.append("shopPhoto", imageurl, imageurl.name);

      axios
        .post(
          config.host + "/api/profiles/createArtist/?token=" + config.token,
          form_data,
          {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          }
        )
        .then((response) => {
          console.log(response);
          if (response.status == 201) {
            navigation.navigate("Kairo Merch Shop Setup");
            setLoading(false);
          } else if (
            response.status == 200 &&
            response.data.response == "Error"
          ) {
            setLoading(false);
            setError(true);
            setErrorMsg("Please verify your phone number first.");
          }
        })
        .catch((err) => {
          setLoading(false);
          setError(true);
          setErrorMsg(err.request.response);
        });
    } catch (e) {
      setLoading(false);
      setError(true);
      setErrorMsg(e.message);
    }
  };

  var uploadImageHandler = () => {};

  var getCountries = () => {
    axios
      .get("https://countriesnow.space/api/v0.1/countries/states")
      .then((response) => {
        var countries = response.data.data;
        var countryList = [];
        var cityList = {};
        for (var x in countries) {
          countryList.push({
            label: countries[x].name,
            value: countries[x].name,
          });
          cityList[countries[x].name] = [];
          for (var y in countries[x].states) {
            cityList[countries[x].name].push({
              label: countries[x].states[y].name,
              value: countries[x].states[y].name,
            });
          }
        }
        setItemsCountry(countryList);
        setCityList(cityList);
        console.log("cityList");
        console.log(cityList);
      });
  };

  const pickImage = async () => {
    let result = await ImagePicker.launchImageLibraryAsync({
      mediaTypes: ImagePicker.MediaTypeOptions.Images,
      allowsEditing: true,
      base64: true,
      aspect: [4, 3],
      quality: 1,
      type: "file",
    });

    if (!result.cancelled) {
      setLoading(false);
      setError(true);
      setErrorMsg("Error uploading file");
    }
  };

  useEffect(() => {
    getCountries();
  }, []);
  useEffect(() => {
    if (valueCountry != "") {
      console.log(valueCountry);
      setItemsCity(citiesList[valueCountry]);
    }
  }, [valueCountry]);
  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: "100%",
          height: "100%",
          overflow: "hidden",
          backgroundColor: "#FFFFFF",
        }}
      >
        <View
          style={{
            height: "11%",
            width: "100%",
            left: 0,
            top: 0,
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            alignItems: "flex-start",
          }}
        >
          <View style={{ height: "15%" }}></View>
          <Image
            source={logo}
            style={{
              width: 214,
              height: "70%",
              marginLeft: 20,
              resizeMode: "contain",
            }}
          />
          <View style={{ height: "15%" }}></View>
        </View>
        <TouchableOpacity
          style={{ width: "3%", height: 25 }}
          onPress={() => {
            navigation.pop();
          }}
        >
          <Image
            source={previous}
            style={{
              width: windowHeight <= 1080 ? 60 : 80,
              height: windowHeight <= 1080 ? 40 : 50,
              resizeMode: "contain",
              marginLeft: 20,
            }}
          />
        </TouchableOpacity>
        <Image
          source={numbers}
          style={{
            alignSelf: "center",
            height: "2%",
            width: "10%",
            resizeMode: "contain",
          }}
        />

        <View
          style={{
            width: "100%",
            height: "87%",
            flexDirection: "row",
            marginTop: "1%",
          }}
        >
          <View style={{ width: "100%", height: "100%", alignItems: "center" }}>
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 24 : 32,
                color: "#04475C",
              }}
            >
              Complete your shop
            </Text>

            <View style={{ height: "2%" }} />
            <View style={{ width: "20%", height: "10%" }}>
              <Image
                source={imageView ? imageView : pp}
                style={{
                  width: "100%",
                  height: "100%",
                  resizeMode: "contain",
                }}
              />
            </View>

            <View
              style={{
                position: "absolute",
                alignItems: "flex-end",
                width: "40%",
                height: "2%",
                top: "10%",
              }}
            >
              <TouchableOpacity
                style={{
                  height: "100%",
                  width: "25%",
                  borderRadius: 20,
                  alignItems: "center",
                }}
                onPress={() => {}}
              >
                <Text style={{ color: "red" }}>*</Text>
                <label
                  htmlFor="filePicker"
                  style={{ background: "#E2F5FB", padding: "5px 10px" }}
                >
                  + Pick Image
                </label>
              </TouchableOpacity>

              <input
                id="filePicker"
                type="file"
                hidden="true"
                name="image_url"
                accept="image/jpeg,image/png,image/gif"
                onChange={(e) => {
                  getBase64(e.target.files[0]);
                  setImageURL(e.target.files[0]);
                }}
              />
            </View>

            <View
              style={{
                width: "100%",
                height: "8%",
                flexDirection: "row",
                justifyContent: "center",
              }}
            >
              <View style={styles.subField}>
                {shopName != "" ? (
                  <View style={{ flexDirection: "row" }}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      Shop name
                    </Text>
                    <Text style={{ color: "red", marginLeft: "0.5%" }}>*</Text>
                  </View>
                ) : (
                  <View style={{ flexDirection: "row" }}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      Shop name
                    </Text>
                    <Text style={{ color: "red", marginLeft: "0.5%" }}>*</Text>
                  </View>
                )}

                <TextInput
                  style={{
                    borderColor: "black",
                    height: "90%",
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    fontFamily: "Roboto_400Regular",
                  }}
                  autoCorrect={false}
                  placeholder={"Shop name"}
                  onChangeText={(newText) => setShopName(newText)}
                ></TextInput>
              </View>
              <View style={{ width: "0.5%" }} />
              <View style={[styles.subField, { zIndex: 999 }]}>
                {aboutUs != "" ? (
                  <View style={{ flexDirection: "row" }}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      How did you hear about us?
                    </Text>
                    <Text style={{ color: "red", marginLeft: "0.5%" }}>*</Text>
                  </View>
                ) : (
                  <View style={{ flexDirection: "row" }}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      How did you hear about us?
                    </Text>
                    <Text style={{ color: "red", marginLeft: "0.5%" }}>*</Text>
                  </View>
                )}
                <TextInput
                  style={{
                    borderColor: "black",
                    height: "90%",
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    fontFamily: "Roboto_400Regular",
                  }}
                  autoCorrect={false}
                  placeholder={"How did you hear about us?"}
                  onChangeText={(newText) => setAboutUs(newText)}
                ></TextInput>
              </View>
            </View>
            <View style={{ height: "2%" }} />
            <View
              style={{
                width: "100%",
                height: "8%",
                flexDirection: "row",
                justifyContent: "center",
                zIndex: -1,
              }}
            >
              <View style={styles.field}>
                {protofolioLink != "" ? (
                  <View style={{ flexDirection: "row" }}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      Portfolio link
                    </Text>

                    <Text style={{ color: "red", marginLeft: "0.5%" }}>*</Text>
                  </View>
                ) : (
                  <View style={{ flexDirection: "row" }}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      Portfolio link
                    </Text>

                    <Text style={{ color: "red", marginLeft: "0.5%" }}>*</Text>
                  </View>
                )}
                <TextInput
                  style={{
                    borderColor: "black",
                    height: "90%",
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    fontFamily: "Roboto_400Regular",
                  }}
                  autoCorrect={false}
                  placeholder={"Portfolio link"}
                  onChangeText={(newText) => setProtofolioLink(newText)}
                ></TextInput>
              </View>
            </View>

            <View style={{ height: "2%" }} />
            <View
              style={{
                width: "100%",
                height: "8%",
                flexDirection: "row",
                justifyContent: "center",
                zIndex: -1,
              }}
            >
              <View style={styles.field}>
                {about != "" ? (
                  <View style={{ flexDirection: "row" }}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      About the artist
                    </Text>
                    <Text style={{ color: "red", marginLeft: "0.5%" }}>*</Text>
                  </View>
                ) : (
                  <View style={{ flexDirection: "row" }}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      About the artist
                    </Text>
                    <Text style={{ color: "red", marginLeft: "0.5%" }}>*</Text>
                  </View>
                )}
                <TextInput
                  style={{
                    borderColor: "black",
                    height: "90%",
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    fontFamily: "Roboto_400Regular",
                  }}
                  autoCorrect={false}
                  placeholder={"About the artist"}
                  onChangeText={(newText) => setAbout(newText)}
                ></TextInput>
              </View>
            </View>
            <View style={{ height: "2%" }} />

            <View
              style={{
                width: "100%",
                height: "8%",
                flexDirection: "row",
                justifyContent: "center",
              }}
            >
              <View
                style={[styles.subField, { zIndex: openCountry ? 999 : -1 }]}
              >
                {valueCountry != "" ? (
                  <View style={{ flexDirection: "row" }}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      Country
                    </Text>
                    <Text style={{ color: "red", marginLeft: "0.5%" }}>*</Text>
                  </View>
                ) : (
                  <View style={{ flexDirection: "row" }}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      Country
                    </Text>
                    <Text style={{ color: "red", marginLeft: "0.5%" }}>*</Text>
                  </View>
                )}
                <DropDownPicker
                  placeholder="Country"
                  open={openCountry}
                  value={valueCountry}
                  items={itemsCountry}
                  setOpen={setOpenCountry}
                  setValue={setValueCountry}
                  setItems={setItemsCountry}
                  style={{ height: "120%" }}
                  arrowIconStyle={{
                    alignSelf: "flex-end",
                    width: 20,
                    height: 10,
                    justifyContent: "center",
                  }}
                  textStyle={{
                    marginTop: 5,
                    marginBottom: 5,
                    fontSize: windowHeight <= 1080 ? 12 : 18,

                    fontFamily: "Roboto_400Regular",
                    color: "#333333AA",
                  }}
                  containerStyle={{ zIndex: openCountry ? 999 : -1 }}
                  dropDownContainerStyle={{ zIndex: openCountry ? 999 : -1 }}
                  showArrowIcon={false}
                />
              </View>
              <View style={{ width: "0.5%" }} />
              <View style={[styles.subField, { zIndex: openCity ? 999 : -1 }]}>
                {valueCity != "" ? (
                  <View style={{ flexDirection: "row" }}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      City
                    </Text>
                  </View>
                ) : (
                  <View style={{ flexDirection: "row" }}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      City
                    </Text>
                  </View>
                )}
                <DropDownPicker
                  placeholder="State"
                  open={openCity}
                  value={valueCity}
                  items={itemsCity}
                  setOpen={setOpenCity}
                  setValue={setValueCity}
                  setItems={setItemsCity}
                  style={{ height: "120%" }}
                  arrowIconStyle={{
                    alignSelf: "flex-end",
                    width: 20,
                    height: 10,
                    justifyContent: "center",
                  }}
                  textStyle={{
                    marginTop: 5,
                    marginBottom: 5,
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    fontFamily: "Roboto_400Regular",
                    color: "#333333AA",
                  }}
                  containerStyle={{ zIndex: openCity ? 999 : -1 }}
                  dropDownContainerStyle={{ zIndex: openCity ? 999 : -1 }}
                  showArrowIcon={false}
                />
              </View>
            </View>
            <View style={{ height: "2%" }} />

            <View
              style={{
                width: "100%",
                height: "8%",
                flexDirection: "row",
                justifyContent: "center",
                zIndex: -1,
              }}
            >
              <View style={styles.subField}>
                {valueOther != "" ? (
                  <View style={{ flexDirection: "row" }}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      Is there other websites you sell your art in?
                    </Text>

                    <Text style={{ color: "red", marginLeft: "0.5%" }}>*</Text>
                  </View>
                ) : (
                  <View style={{ flexDirection: "row" }}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      Is there other websites you sell your art in?
                    </Text>

                    <Text style={{ color: "red", marginLeft: "0.5%" }}>*</Text>
                  </View>
                )}
                <DropDownPicker
                  placeholder="Is there other websites you sell your art in?"
                  open={openOther}
                  value={valueOther}
                  items={itemsOther}
                  setOpen={setOpenOther}
                  setValue={setValueOther}
                  setItems={setItemsOther}
                  style={{ height: "120%" }}
                  arrowIconStyle={{
                    alignSelf: "flex-end",
                    width: 20,
                    height: 10,
                    justifyContent: "center",
                    zIndex: openOther ? 999 : -1,
                  }}
                  textStyle={{
                    marginTop: 5,
                    marginBottom: 5,
                    fontSize: windowHeight <= 1080 ? 12 : 18,

                    fontFamily: "Roboto_400Regular",
                    color: "#333333AA",
                  }}
                  containerStyle={{ zIndex: openOther ? 999 : -1 }}
                  dropDownContainerStyle={{ zIndex: openOther ? 999 : -1 }}
                  showArrowIcon={false}
                  dropDownDirection={"DOWN"}
                />
              </View>
              <View style={{ width: "0.5%" }} />
              <View style={[styles.subField, { zIndex: -1 }]}>
                {otherSite != "" ? (
                  <View style={{ flexDirection: "row" }}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      Website name
                    </Text>

                    <Text style={{ color: "red", marginLeft: "0.5%" }}>*</Text>
                  </View>
                ) : (
                  <View style={{ flexDirection: "row" }}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      Website name
                    </Text>

                    <Text style={{ color: "red", marginLeft: "0.5%" }}>*</Text>
                  </View>
                )}
                <TextInput
                  style={{
                    borderColor: "black",
                    height: "90%",
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    fontFamily: "Roboto_400Regular",
                  }}
                  autoCorrect={false}
                  placeholder={"Website name"}
                  onChangeText={(newText) => setOtherSite(newText)}
                ></TextInput>
              </View>
            </View>
            <View
              style={{
                width: "100%",
                height: "10%",
                marginTop: "2%",
                justifyContent: "flex-end",
                flexDirection: "row",
                alignItems: "center",
                zIndex: -2,
              }}
            >
              <CheckBox value={isAgree} onValueChange={setIsAgree} />
              <View style={{ width: "0.5%" }} />
              <Text
                style={{
                  fontSize: windowHeight <= 1080 ? 12 : 18,
                  fontFamily: "Roboto_400Regular",
                }}
              >
                I agree to
              </Text>
              <View style={{ width: "0.25%" }} />
              <TouchableOpacity
                onPress={() => {
                  navigation.navigate("Kairo Merch Policies and Regulations");
                }}
              >
                <Text
                  style={{
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    fontFamily: "Roboto_700Bold",
                    color: "#04475C",
                  }}
                >
                  Policies and regulations
                </Text>
              </TouchableOpacity>
              <View style={{ width: "3%" }} />
            </View>
            <TouchableOpacity
              style={{
                width: "20%",
                height: "5%",
                shadowRadius: 10,
                backgroundColor: "#F7567C",
                alignItems: "center",
                justifyContent: "center",
                borderRadius: 10,
              }}
              onPress={setupShopHandler}
            >
              <Text
                style={{
                  fontFamily: "Roboto_700Bold",
                  color: "#FFFFFF",
                  fontSize: windowHeight <= 1080 ? 12 : 16,
                }}
              >
                Next
              </Text>
            </TouchableOpacity>
          </View>
        </View>

        {isLoading ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFFCC",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "20%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 500,
                alignSelf: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Image
                source={loading}
                style={{
                  width: "90%",
                  height: "40%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "70%",
                }}
              />
            </View>
          </View>
        ) : (
          <View />
        )}

        {isError ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFF99",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: 0.4 * windowHeight,
                width: 0.2 * windowWidth,
                backgroundColor: "#FFFFFFDA",
                borderRadius: 50,
                alignSelf: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: 0.4 * 0.2 * windowWidth,
                  height: 0.4 * 0.2 * windowHeight,
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                  marginBottom: "10%",
                }}
              />
              <ScrollView
                style={{
                  width: 0.8 * 0.2 * windowWidth,
                  height: 0.8 * 0.4 * windowHeight,
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_400Regular",
                    fontSize: windowHeight <= 1080 ? 16 : 20,
                    color: "#333333",
                  }}
                >
                  {errMsg}
                </Text>
              </ScrollView>

              <View style={{ height: "10%" }} />
              <TouchableOpacity
                style={{
                  width: "20%",
                  alignSelf: "center",
                  height: "10%",

                  backgroundColor: "#888888",
                  alignItems: "center",
                  justifyContent: "center",
                  borderRadius: 10,
                }}
                onPress={() => {
                  setError(false);
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    color: "#FFFFFF",
                    fontSize: windowHeight <= 1080 ? 12 : 16,
                  }}
                >
                  Ok
                </Text>
              </TouchableOpacity>
            </View>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

export default CompleteProfile;

