import {
  Animated,
  StyleSheet,
  Text,
  View,
  ImageBackground,
  Image,
  Easing,
  TouchableOpacity,
  TextInput,
  Dimensions,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import { slideOutLeft } from "react-animations";
import Radium, { StyleRoot } from "radium";
import axios from "axios";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import config from "../../config";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var logo = require("../../assets/logo.png");
var info1 = require("../../assets/money-info-1.png");
var info2 = require("../../assets/money-info-2.png");
var demo = require("../../assets/demo.png");

var Home = ({ route, navigation }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const loginHandler = () => {
    navigation.navigate("Kairo Merch Login");
  };
  const signupHandler = () => {
    navigation.navigate("Kairo Merch Signup");
  };
  useEffect(() => {
    var cookies = document.cookie.split(";");
    console.log(cookies);

    for (let i = 0; i < cookies.length; i = i + 1) {
      if (cookies[i].split("=")[0].includes("config")) {
        var configCookies = JSON.parse(cookies[i].split("=")[1]);
        if (configCookies.token != "") {
          try {
            axios
              .get(
                config.host +
                  "/api/account/getAccountStatus?token=" +
                  configCookies.token
              )
              .then((response) => {
                //Navigate to verification page
                console.log(response);
                config.status = response.data.account_status;
                config.verified = response.data.mobile_verification;
                config.approved = response.data.account_approval;
                config.token = configCookies.token;
                config.plan = configCookies.plan;
                if (config.verified) {
                  if (config.status == 0) {
                    navigation.navigate("Kairo Merch Complete Profile");
                  } else if (!config.approved) {
                    navigation.navigate("Kairo Merch Shop Setup");
                  } else if (config.status == 4) {
                    navigation.navigate("Kairo Merch Shop");
                  } else {
                    navigation.navigate("Kairo Merch Advisor", {
                      status: config.status,
                    });
                  }
                } else {
                  navigation.navigate("Kairo Merch Mobile Verification");
                }
              })
              .catch((e) => {
                navigation.navigate("Kairo Merch Complete Profile");
              });
          } catch (e) {
            var err = "";
            for (let x in JSON.parse(e.request.response)) {
              console.log(e.request.response.x);
              err = err + JSON.parse(e.request.response)[x] + "\n";
            }
          }
        }

        console.log(configCookies);
      }
    }
  }, []);
  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: "100%",
          height: "100%",
          overflow: "hidden",
          backgroundColor: "#FFFFFF",
        }}
      >
        <View style={{ position: "absolute", bottom: 50, left: 50 }}>
          <Text>{config.version}</Text>
        </View>
        <View
          style={{
            height: 0.12 * windowHeight,
            width: "100%",
            left: 0,
            top: 0,
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            alignItems: "flex-start",
          }}
        >
          <View style={{ height: "15%" }}></View>
          <Image
            source={logo}
            style={{
              width: windowWidth * 0.12,
              height: windowHeight * 0.09,
              marginLeft: 20,
              resizeMode: "contain",
            }}
          />
        </View>
        <View style={{ height: 0.03 * windowHeight }}></View>

        <View style={{ height: 0.75 * windowHeight, width: "100%" }}>
          <View style={{ height: "auto", width: "100%" }}>
            <View
              style={{ flexDirection: "row", justifyContent: "space-between" }}
            >
              <View style={{ width: "30%" }}>
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    fontSize: windowHeight <= 1080 ? 36 : 48,
                    color: "#3D9EBC",
                    marginLeft: 36,
                    marginTop: "1%",
                  }}
                >
                  Welcome to KairoMerch!
                </Text>
              </View>
              <View style={{ width: "50%" }} />

              <View style={{ flexDirection: "row", width: "100%" }}>
                <TouchableOpacity style={styles.button2} onPress={loginHandler}>
                  <Text
                    style={{
                      fontFamily: "Roboto_400Regular",
                      fontSize: windowHeight <= 1080 ? 16 : 20,
                      color: "#FFFFFF",
                    }}
                  >
                    Login
                  </Text>
                </TouchableOpacity>
                <View style={{ width: "1%" }} />
                <TouchableOpacity
                  style={styles.button2}
                  onPress={signupHandler}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_400Regular",
                      fontSize: windowHeight <= 1080 ? 16 : 20,
                      color: "#FFFFFF",
                    }}
                  >
                    Sign up
                  </Text>
                </TouchableOpacity>
              </View>
              <View style={{ width: "1%" }} />
            </View>

            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 24 : 32,
                color: "#333333",
                marginLeft: 50,
                marginTop: 0.02 * windowHeight,
              }}
            >
              Hello Artist, we would love to have you onboard.
            </Text>
            <Text
              style={{
                fontFamily: "Roboto_400Regular",
                fontSize: windowHeight <= 1080 ? 16 : 24,
                color: "#000000B3",
                marginLeft: 50,
                marginTop: 0.02 * windowHeight,
              }}
            >
              Kairo Merch is the fastest solution for independent artists to
              launch an online shop and sell their creative art through online
              products.
            </Text>
            <Text
              style={{
                fontFamily: "Roboto_400Regular",
                fontSize: windowHeight <= 1080 ? 24 : 32,
                color: "#C54160",
                marginLeft: 65,
                marginTop: 0.04 * windowHeight,
              }}
            >
              Join us now and gain cash effortlessly!
            </Text>

            <Text
              style={{
                fontFamily: "Roboto_400Regular",
                fontSize: windowHeight <= 1080 ? 16 : 24,
                color: "#000000B3",
                marginLeft: 65,
                marginTop: 0.02 * windowHeight,
              }}
            >
              How to gain money with us?
            </Text>

            <View style={{ height: "10%" }}></View>

            <View
              style={{
                width: "50%",

                height: "50%",
                flexDirection: "row",
                marginLeft: 65,
                marginTop: 0.01 * windowHeight,
              }}
            >
              <View style={{ alignItems: "center", width: "50%" }}>
                <Image
                  source={info1}
                  style={{
                    width: 0.25 * windowWidth,
                    height: 0.2 * windowHeight,
                    resizeMode: "center",
                  }}
                />
                <View
                  style={{
                    flexDirection: "row",
                    marginTop: 10,
                  }}
                >
                  <View
                    style={{
                      width: "100%",
                      alignItems: "center",
                    }}
                  >
                    <Text
                      style={{
                        fontFamily: "Roboto_500Medium",
                        fontSize: windowHeight <= 1080 ? 12 : 20,
                        color: "#04475C",
                        width: "80%",
                        marginTop: 0.02 * windowHeight,
                      }}
                    >
                      Easily upload your art to a wide range of high quality
                      products
                    </Text>
                    <Text
                      style={{
                        fontFamily: "Roboto_500Medium",
                        fontSize: windowHeight <= 1080 ? 12 : 20,
                        color: "#04475C",
                        width: "80%",
                        marginTop: 0.02 * windowHeight,
                      }}
                    >
                      All you need to do is to set up your unique shop
                    </Text>
                  </View>
                </View>
              </View>
              <View style={{ alignItems: "center", width: "50%" }}>
                <Image
                  source={info2}
                  style={{
                    width: 0.25 * windowWidth,
                    height: 0.2 * windowHeight,
                    resizeMode: "center",
                  }}
                />
                <View
                  style={{
                    width: "100%",
                    alignItems: "center",
                  }}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_500Medium",
                      fontSize: windowHeight <= 1080 ? 12 : 20,
                      color: "#04475C",
                      marginTop: 0.02 * windowHeight,
                      width: "60%",
                    }}
                  >
                    Customers order the products they love featuring your art
                    and you get paid!
                  </Text>
                  <Text
                    style={{
                      fontFamily: "Roboto_500Medium",
                      fontSize: windowHeight <= 1080 ? 12 : 20,
                      color: "#04475C",
                      width: "60%",

                      marginTop: 0.02 * windowHeight,
                    }}
                  >
                    We handle the orders fulfillment and delivery
                  </Text>
                </View>
              </View>
            </View>
            <View
              style={{
                width: 0.4 * windowWidth,
                height: 0.4 * windowHeight,
                position: "absolute",
                top: 0.2 * windowHeight,
                right: 0.06 * windowWidth,
              }}
            >
              <Image
                source={demo}
                style={{
                  width: 0.5 * windowWidth,
                  height: 0.5 * windowHeight,
                  resizeMode: "contain",
                }}
              />
            </View>

            <View style={{ marginLeft: 65, width: "30%" }}></View>
          </View>
        </View>
        <TouchableOpacity style={styles.button} onPress={signupHandler}>
          <Text
            style={{
              fontFamily: "Roboto_400Regular",
              fontSize: windowHeight <= 1080 ? 16 : 20,
              color: "#FFFFFF",
            }}
          >
            Start now
          </Text>
        </TouchableOpacity>
      </View>
    );
  }
};

export default Home;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    borderRadius: 50,
    width: "20%",
    height: 0.05 * windowHeight,
    shadowRadius: 10,
    backgroundColor: "#F7567C",
  },
  button2: {
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    width: "8%",
    height: 0.05 * windowHeight,
    padding: 18,
    shadowRadius: 5,
    backgroundColor: "#3D9EBC",
  },
});
