import {
  Animated,
  StyleSheet,
  Text,
  View,
  ImageBackground,
  Image,
  Easing,
  TouchableOpacity,
  TextInput,
  Dimensions,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import axios from "axios";
import config from "../../config";
import { ScrollView } from "react-native-gesture-handler";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var logo = require("../../assets/logo.png");
var sign_wp = require("../../assets/sign_wp.png");
var previous = require("../../assets/Previous.png");
var eye = require("../../assets/eye.png");
var eye_slash = require("../../assets/eye-slash.png");
var loading = require("../../assets/loading.gif");
var km = require("../../assets/icon.png");
var numbers = require("../../assets/signup-numbers.png");
var Signup = ({ route, navigation }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });
  const [hidePassword, setHidePassword] = useState(true);
  const [fullName, setFullName] = useState("");
  const [username, setUserName] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [confirmPassword, setConfirmPassword] = useState("");
  const [phoneNumber, setPhoneNumber] = useState("");
  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);
  const [errMsg, setErrorMsg] = useState(null);
  var verifyMobileNumber = () => {
    setLoading(true);
    //Verify inputs

    //Send information for mobile verification
    try {
      axios
        .post(config.host + "/api/account/register", {
          name: fullName,
          username: username,
          password: password,
          password2: confirmPassword,
          email: email,
          mphone: phoneNumber,
        })
        .then((response) => {
          //Navigate to verification page
          console.log(response);
          config.token = response.data.token;
          config.status = 0;
          config.verified = false;
          setLoading(false);
          navigation.navigate("Kairo Merch Mobile Verification");
        })
        .catch((e) => {
          setLoading(false);
          setError(true);

          var err = "";
          for (let x in JSON.parse(e.request.response)) {
            console.log(e);
            err = err + x + ": " + JSON.parse(e.request.response)[x] + "\n";
          }
          setErrorMsg(err);
        });
    } catch (error) {
      console.log(error);
    }
  };
  const loginHandler = () => {
    navigation.navigate("Kairo Merch Login");
  };

  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: "100%",
          height: "100%",
          overflow: "hidden",
          backgroundColor: "#FFFFFF",
        }}
      >
        <View style={{ position: "absolute", bottom: 50, left: 50 }}>
          <Text>{config.version}</Text>
        </View>

        {isLoading ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFFCC",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "20%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 500,
                alignSelf: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: 0.9 * 0.2 * windowWidth,
                  height: 0.5 * 0.2 * windowHeight,
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Image
                source={loading}
                style={{
                  width: 0.9 * 0.2 * windowWidth,
                  height: 0.4 * 0.2 * windowHeight,
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "70%",
                }}
              />
            </View>
          </View>
        ) : (
          <View />
        )}
        <View
          style={{
            height: 0.12 * windowHeight,
            width: "100%",
            left: 0,
            top: 0,
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            alignItems: "flex-start",
          }}
        >
          <View style={{ height: "15%" }}></View>
          <Image
            source={logo}
            style={{
              width: windowWidth * 0.12,
              height: windowHeight * 0.09,
              marginLeft: 20,
              resizeMode: "contain",
            }}
          />
        </View>
        <Image
          source={numbers}
          style={{
            alignSelf: "center",
            height: "2%",
            width: "10%",
            resizeMode: "contain",
          }}
        />

        <View style={{ width: "100%", height: "89%", flexDirection: "row" }}>
          <View style={{ width: "50%", height: "100%" }}>
            <TouchableOpacity
              style={{ width: "100%", height: "5%" }}
              onPress={() => {
                navigation.pop();
              }}
            >
              <Image
                source={previous}
                style={{
                  width: windowHeight <= 1080 ? 60 : 80,
                  height: windowHeight <= 1080 ? 40 : 50,
                  resizeMode: "contain",
                  marginLeft: 20,
                }}
              />
            </TouchableOpacity>
            <View
              style={{
                alignItems: "center",
                height: "98%",
                width: "100%",
              }}
            >
              <View style={{ height: 0.04 * windowHeight }} />
              <Text
                style={{
                  fontFamily: "Roboto_700Bold",
                  fontSize: windowHeight <= 1080 ? 24 : 32,
                  color: "#04475C",
                }}
              >
                Sign up now and gain money for free!
              </Text>
              <View style={{ height: 0.04 * windowHeight }} />

              <View
                style={{
                  width: "80%",
                  height: "90%",
                  alignSelf: "flex-start",
                  marginLeft: "10%",
                }}
              >
                <View
                  style={{
                    justifyContent: "flex-end",
                    flexDirection: "row",

                    width: "100%",
                    height: "5%",
                  }}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_400Regular",
                      fontSize: windowHeight <= 1080 ? 12 : 16,
                      color: "#333333",
                    }}
                  >
                    Already have an account?
                  </Text>

                  <TouchableOpacity onPress={loginHandler}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        color: "#C54160",
                        fontSize: windowHeight <= 1080 ? 12 : 16,
                        marginLeft: 10,
                      }}
                    >
                      Login
                    </Text>
                  </TouchableOpacity>
                </View>

                <View style={{ width: "100%", height: "90%" }}>
                  <View style={styles.field}>
                    <TextInput
                      style={{ borderColor: "black" }}
                      autoCorrect={false}
                      placeholder={"Full Name"}
                      onChangeText={(newText) => setFullName(newText)}
                    ></TextInput>
                  </View>

                  <View style={styles.field}>
                    <TextInput
                      style={{ borderColor: "black" }}
                      autoCompleteType={"username"}
                      autoCorrect={false}
                      placeholder={"Username"}
                      onChangeText={(newText) => setUserName(newText)}
                    ></TextInput>
                  </View>

                  <View style={styles.field}>
                    <TextInput
                      style={{ borderColor: "black" }}
                      autoCompleteType={"email"}
                      autoCorrect={false}
                      placeholder={"Email"}
                      onChangeText={(newText) => setEmail(newText)}
                    ></TextInput>
                  </View>

                  <View style={styles.pwfield}>
                    <TextInput
                      style={{ borderColor: "white", width: "90%" }}
                      autoCompleteType={"password"}
                      secureTextEntry={hidePassword}
                      autoCorrect={false}
                      placeholder={"Password"}
                      onChangeText={(newText) => setPassword(newText)}
                    ></TextInput>
                    <TouchableOpacity
                      style={{
                        height: "100%",
                        width: "10%",
                        justifyContent: "center",
                      }}
                      onPress={() => {
                        setHidePassword(!hidePassword);
                      }}
                    >
                      <Image
                        source={hidePassword ? eye_slash : eye}
                        style={{
                          width: "100%",
                          height: 12,
                          resizeMode: "contain",
                        }}
                      />
                    </TouchableOpacity>
                  </View>
                  <View style={styles.pwfield}>
                    <TextInput
                      style={{ borderColor: "white", width: "90%" }}
                      autoCompleteType={"password"}
                      secureTextEntry={hidePassword}
                      autoCorrect={false}
                      placeholder={"Confirm Password"}
                      onChangeText={(newText) => setConfirmPassword(newText)}
                    ></TextInput>
                    <TouchableOpacity
                      style={{
                        height: "100%",
                        width: "10%",
                        justifyContent: "center",
                      }}
                      onPress={() => {
                        setHidePassword(!hidePassword);
                      }}
                    >
                      <Image
                        source={hidePassword ? eye_slash : eye}
                        style={{
                          width: "100%",
                          height: 12,
                          resizeMode: "contain",
                        }}
                      />
                    </TouchableOpacity>
                  </View>

                  <View style={styles.field}>
                    <TextInput
                      style={{ borderColor: "black" }}
                      autoCompleteType={"mobilenumber"}
                      autoCorrect={false}
                      placeholder={"Phone number"}
                      onChangeText={(newText) => setPhoneNumber(newText)}
                    ></TextInput>
                  </View>

                  <TouchableOpacity
                    style={styles.button}
                    onPress={verifyMobileNumber}
                  >
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 14 : 18,
                        color: "#FFFFFF",
                      }}
                    >
                      Verify mobile number
                    </Text>
                  </TouchableOpacity>
                </View>
              </View>
            </View>
          </View>
          <View style={{ width: "50%", height: "100%" }}>
            <Image
              source={sign_wp}
              style={{ width: "100%", height: "100%", resizeMode: "contain" }}
            />
          </View>
        </View>

        {isError ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFF99",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: 0.4 * windowHeight,
                width: 0.2 * windowWidth,
                backgroundColor: "#FFFFFFDA",
                borderRadius: 50,
                alignSelf: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: 0.4 * 0.2 * windowWidth,
                  height: 0.4 * 0.2 * windowHeight,
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                  marginBottom: "10%",
                }}
              />
              <ScrollView
                style={{
                  width: 0.8 * 0.2 * windowWidth,
                  height: 0.8 * 0.4 * windowHeight,
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_400Regular",
                    fontSize: windowHeight <= 1080 ? 16 : 20,
                    color: "#333333",
                  }}
                >
                  {errMsg}
                </Text>
              </ScrollView>

              <View style={{ height: "10%" }} />
              <TouchableOpacity
                style={{
                  width: "20%",
                  alignSelf: "center",
                  height: "10%",

                  backgroundColor: "#888888",
                  alignItems: "center",
                  justifyContent: "center",
                  borderRadius: 10,
                }}
                onPress={() => {
                  setError(false);
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    color: "#FFFFFF",
                    fontSize: windowHeight <= 1080 ? 12 : 16,
                  }}
                >
                  Ok
                </Text>
              </TouchableOpacity>
            </View>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

export default Signup;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    marginTop: "5%",
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    borderRadius: 50,
    width: "60%",
    height: "4%",
    padding: 15,
    shadowRadius: 10,
    backgroundColor: "#F7567C",
  },
  button2: {
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    width: "8%",
    height: "2%",
    padding: 18,
    shadowRadius: 5,
    backgroundColor: "#3D9EBC",
  },

  gap: {
    height: 10,
  },
  field: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 20,
    width: "100%",
    height: "8%",
    padding: 8,
    backgroundColor: "white",
    borderWidth: 1,
    borderRadius: 4,
    borderColor: "grey",
  },
  pwfield: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 20,
    width: "100%",
    height: "8%",
    padding: 8,
    backgroundColor: "white",
    flexDirection: "row",
    borderWidth: 1,
    borderColor: "grey",
    borderRadius: 4,
  },
});
