import {
  Animated,
  StyleSheet,
  Text,
  View,
  ImageBackground,
  Image,
  Easing,
  TouchableOpacity,
  TextInput,
  Dimensions,
  Alert,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import OTPTextView from "react-native-otp-textinput";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import config from "../../config";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var logo = require("../../assets/logo.png");
var verify_wp = require("../../assets/verify_wp.png");
var verified = require("../../assets/verified.png");
var wrong = require("../../assets/wrong.png");
var previous = require("../../assets/Previous.png");
var sms = require("../../assets/sms.png");
var smsVerified = require("../../assets/sms-verified.png");
var loading = require("../../assets/loading.gif");
var km = require("../../assets/icon.png");
var numbers = require("../../assets/vmn-numbers.png");
var VerifyMobileNumber = ({ route, navigation }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const [trueOtp, setTrueOtp] = useState("0000");
  const [otpNumber, setOtpNumber] = useState("");
  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);
  const [isVerified, setVerified] = useState(false);
  const [errMsg, setErrorMsg] = useState("");
  var otpInput;

  var sendAgainHandler = () => {
    setError(false);
    setLoading(true);
    otpInput.clear();
    setTimeout(() => {
      setLoading(false);
    }, 3000);
  };

  var retryHandler = () => {
    setError(false);
    otpInput.clear();
  };

  var verifiedHandler = () => {
    setLoading(true);
    setTimeout(() => {
      setLoading(false);
      setVerified(true);
    }, 3000);

    //Send verified OTP
  };

  useEffect(() => {
    console.log(config);
  }, []);
  var profileHandler = () => {
    //Send verified OTP
    //Navigate to
    console.log("To profile");
    //Navigate to profile page
    navigation.navigate("Kairo Merch Complete Profile");
  };
  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: "100%",
          height: "100%",
          overflow: "hidden",
          backgroundColor: "#FFFFFF",
        }}
      >
        <View style={{ position: "absolute", bottom: 50, left: 50 }}>
          <Text>{config.version}</Text>
        </View>
        <View
          style={{
            height: "11%",
            width: "100%",
            left: 0,
            top: 0,
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            alignItems: "flex-start",
          }}
        >
          <View style={{ height: "15%" }}></View>
          <Image
            source={logo}
            style={{
              width: 214,
              height: "70%",
              marginLeft: 20,
              resizeMode: "contain",
            }}
          />
          <View style={{ height: "15%" }}></View>
        </View>
        <Image
          source={numbers}
          style={{
            alignSelf: "center",
            height: "2%",
            width: "10%",
            resizeMode: "contain",
          }}
        />

        <View style={{ width: "100%", height: "87%", flexDirection: "row" }}>
          <TouchableOpacity
            style={{ width: "3%", height: 25 }}
            onPress={() => {
              navigation.pop();
            }}
          >
            <Image
              source={previous}
              style={{
                width: windowHeight <= 1080 ? 60 : 80,
                height: windowHeight <= 1080 ? 40 : 50,
                resizeMode: "contain",
                marginLeft: 20,
              }}
            />
          </TouchableOpacity>
          <View style={{ width: "60%", height: "100%" }}>
            <Image
              source={isError ? wrong : isVerified ? verified : verify_wp}
              style={{ width: "100%", height: "100%", resizeMode: "contain" }}
            />
          </View>
          <View style={{ width: "40%", height: "100%" }}>
            <View style={{ height: "5%" }} />
            <View
              style={{
                alignItems: "center",
                width: "100%",
                height: "70%",
              }}
            >
              <Image
                source={isVerified ? smsVerified : sms}
                style={{ width: "100%", height: "100%", resizeMode: "contain" }}
              />
              <View style={{ height: "1%" }} />
              {isVerified ? (
                <View />
              ) : (
                <TouchableOpacity
                  style={styles.button}
                  onPress={sendAgainHandler}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 14 : 18,
                      color: "#FFFFFF",
                    }}
                  >
                    Send Again
                  </Text>
                </TouchableOpacity>
              )}

              {isVerified ? (
                <TouchableOpacity
                  style={styles.button}
                  onPress={profileHandler}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 14 : 18,
                      color: "#FFFFFF",
                    }}
                  >
                    Complete profile
                  </Text>
                </TouchableOpacity>
              ) : (
                <OTPTextView
                  tintColor="#FFB1C399"
                  ref={(e) => (otpInput = e)}
                  handleTextChange={(e) => {
                    setOtpNumber(e);
                    if (e.length == 4) {
                      console.log("true otp:", trueOtp);
                      console.log("entered otp:", e);
                      if (e == trueOtp) {
                        verifiedHandler();
                      } else {
                        setErrorMsg("Wrong verification code");
                        setError(true);
                      }
                    }
                  }}
                  containerStyle={styles.textInputContainer}
                  textInputStyle={styles.roundedTextInput}
                  defaultValue=""
                  selectionColor={"black"}
                  inputCount={4}
                />
              )}
            </View>
          </View>
        </View>

        {isLoading ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFFCC",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "20%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 500,
                alignSelf: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Image
                source={loading}
                style={{
                  width: "90%",
                  height: "40%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "70%",
                }}
              />
            </View>
          </View>
        ) : (
          <View />
        )}

        {isError ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFF99",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "30%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 50,
                alignSelf: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 12 : 16,
                  color: "#333333",
                  marginTop: "10%",
                }}
              >
                {errMsg}
              </Text>

              <View style={{ height: "10%" }} />

              <View
                style={{
                  flexDirection: "row",
                  height: "10%",
                  width: "100%",
                  justifyContent: "center",
                }}
              >
                <TouchableOpacity
                  style={{
                    width: "40%",

                    backgroundColor: "#F7567C",
                    alignItems: "center",
                    justifyContent: "center",
                    borderRadius: 10,
                  }}
                  onPress={sendAgainHandler}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      color: "#FFFFFF",
                      fontSize: windowHeight <= 1080 ? 12 : 16,
                    }}
                  >
                    Send Again
                  </Text>
                </TouchableOpacity>

                <View style={{ width: "2%" }}></View>
                <TouchableOpacity
                  style={{
                    width: "20%",

                    backgroundColor: "#888888",
                    alignItems: "center",
                    justifyContent: "center",
                    borderRadius: 10,
                  }}
                  onPress={retryHandler}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      color: "#FFFFFF",
                      fontSize: windowHeight <= 1080 ? 12 : 16,
                    }}
                  >
                    Retry
                  </Text>
                </TouchableOpacity>
              </View>
            </View>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

export default VerifyMobileNumber;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    marginTop: "5%",
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    borderRadius: 50,
    width: "40%",
    height: "4%",
    padding: 15,
    shadowRadius: 10,
    backgroundColor: "#F7567C",
    zIndex: 999,
  },
  button2: {
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    width: "8%",
    height: "2%",
    padding: 18,
    shadowRadius: 5,
    backgroundColor: "#3D9EBC",
  },

  gap: {
    height: 10,
  },
  field: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 20,
    width: "100%",
    height: "25%",
    padding: 10,
    backgroundColor: "white",
  },
  pwfield: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 20,
    width: "100%",
    height: "25%",
    padding: 10,
    backgroundColor: "white",
    flexDirection: "row",
  },
  textInputContainer: {
    marginBottom: 20,
    marginTop: 20,
  },

  roundedTextInput: {
    borderRadius: 10,
    backgroundColor: "white",
    borderWidth: 1,
    marginLeft: 20,
    marginRight: 20,
  },
});
