import {
  Animated,
  StyleSheet,
  Text,
  View,
  ImageBackground,
  Image,
  Easing,
  TouchableOpacity,
  TextInput,
  Dimensions,
  Alert,
  CheckBox,
  ScrollView,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import OTPTextView from "react-native-otp-textinput";
import DropDownPicker from "react-native-dropdown-picker";
import * as ImagePicker from "expo-image-picker";
import axios from "axios";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import NavigationBar from "./navigation-bar";
import config from "../../config";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var logo = require("../../assets/logo.png");
var loading = require("../../assets/loading.gif");
var km = require("../../assets/icon.png");
var trends = require("../../assets/trends.png");
var placeholder = require("../../assets/placeholder.png");
var down = require("../../assets/down-arrow.png");
var magnify = require("../../assets/magnify.png");
var no_arts = require("../../assets/no-arts.png");
var trash = require("../../assets/trash.png");

var Art = ({ route, navigation }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);
  const [errMsg, setErrorMsg] = useState("");
  const [openCategories, setOpenCategories] = useState(false);
  const [categories, setCategories] = useState(["All", "Tshirt", "Mug"]);
  const [selectedCategory, setSelectedCategory] = useState("");
  const [artType, setArtType] = useState("All");
  const [designs, setDesigns] = useState([]);
  const [rawDesignsState, setRawDesigns] = useState([]);
  const [search, setSearch] = useState("");
  const [isAdd, setIsAdd] = useState(false);
  const [isView, setIsView] = useState(false);
  const [imageurl, setImageURL] = useState(null);
  const [imageView, setImageView] = useState(null);
  const [designName, setDesignName] = useState("");
  const [designNameArabic, setDesignNameArabic] = useState("");
  const [hashtags, setHashtags] = useState("");
  const [hashtagsArabic, setHashtagsArabic] = useState("");
  const [selectedArt, setSelectedArt] = useState({});

  const status = config.status;
  var addDesign = () => {
    setLoading(true);
    try {
      let form_data = new FormData();
      form_data.append("designNameEN", designName);
      form_data.append("designNameAR", designNameArabic);
      form_data.append("tagsEN", hashtags);
      form_data.append("tagsAR", hashtagsArabic);
      form_data.append("designFiles", imageurl, imageurl.name);

      axios
        .post(
          config.host + "/api/mocks/createDesign/?token=" + config.token,
          form_data,
          {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          }
        )
        .then((response) => {
          setLoading(false);
          setIsAdd(false);
          setError(true);
          setErrorMsg("Your art file has been added successfully.");
          getDesigns();
        })
        .catch((err) => {
          setLoading(false);
          setError(true);
          setErrorMsg(err.request.response);
        });
    } catch (e) {
      setLoading(false);
      setError(true);
      setErrorMsg(e.message);
    }
  };
  var getDesigns = () => {
    setLoading(true);
    try {
      axios
        .get(config.host + "/api/mocks/GetAllArtFiles/?token=" + config.token)
        .then((response) => {
          var rawDesigns = response.data;
          console.log(rawDesigns);

          setRawDesigns(rawDesigns);
          structureDesigns(rawDesigns);
        });
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  var deleteDesign = () => {
    setLoading(true);
    try {
      axios
        .delete(
          config.host +
            "/api/mocks/deleteDesign/?designID=" +
            selectedArt.designID
        )
        .then((response) => {
          setIsView(false);
          setLoading(false);
          getDesigns();
        });
    } catch (error) {
      console.log(error);
      setLoading(false);
    }
  };

  var structureDesigns = (rawDesigns) => {
    var filteredDesigns = [];
    for (var i = 0; i < rawDesigns.length; i = i + 1) {
      if (
        rawDesigns[i].designNameEN.includes(search) &&
        ((rawDesigns[i].orders > 0 && artType == "Orders") ||
          (rawDesigns[i].orders == 0 && artType == "Saved") ||
          artType == "All")
      ) {
        filteredDesigns.push(rawDesigns[i]);
      }
    }

    var structuredDesigns = [];
    var designsRow = { lineId: row, data: [] };
    var row = 0;
    for (var i = 0; i < filteredDesigns.length; i = i + 1) {
      if (i % 6 == 0) {
        designsRow = { lineId: row, data: [] };
      }
      designsRow.data[i % 6] = filteredDesigns[i];
      if (i % 6 == 5) {
        structuredDesigns[row] = designsRow;
        row++;
      }
    }
    if (i % 6 < 6 && i % 6 != 0) {
      structuredDesigns[row] = designsRow;
    }
    setDesigns(structuredDesigns);
    console.log(structuredDesigns);

    setLoading(false);
  };
  function getBase64(file) {
    var reader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = function () {
      setImageView(reader.result);
    };
    reader.onerror = function (error) {
      console.log("Error: ", error);
    };
  }
  useEffect(() => {
    structureDesigns(rawDesignsState);
  }, [search, artType]);
  useEffect(getDesigns, []);
  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: "100%",
          height: "100%",
          overflow: "hidden",
          backgroundColor: "#FFFFFF",
        }}
      >
        {isAdd ? (
          <View
            style={{
              position: "absolute",
              height: "100%",
              width: "100%",
              backgroundColor: "#C4C4C4AA",
              zIndex: 999,
              alignSelf: "center",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <input
              id="filePicker"
              type="file"
              hidden="true"
              name="image_url"
              accept="image/jpeg,image/png,image/gif"
              onChange={(e) => {
                getBase64(e.target.files[0]);
                setImageURL(e.target.files[0]);
              }}
            />
            <View
              style={{
                width: "80%",
                height: "80%",
                alignItems: "center",
                backgroundColor: "#ffffff",
                borderRadius: 20,
                borderWidth: 1,
                justifyContent: "center",
              }}
            >
              <TouchableOpacity
                style={{
                  width: "5%",
                  alignSelf: "flex-end",
                  height: "3%",
                  marginRight: "2%",
                  padding: 10,
                  borderRadius: 4,

                  alignItems: "center",
                  justifyContent: "center",
                  borderRadius: 10,
                  shadowRadius: 10,
                  shadowColor: "#3D9EBC",
                  backgroundColor: "#3D9EBC",
                }}
                onPress={() => {
                  setIsAdd(false);
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_400Regular",
                    color: "#FFFFFF",

                    fontSize: windowHeight <= 1080 ? 12 : 16,
                  }}
                >
                  Cancel
                </Text>
              </TouchableOpacity>
              <View
                style={{
                  flexDirection: "row",
                  height: "3%",
                  width: "100%",
                  alignItems: "center",
                  justifyContent: "center",
                  marginTop: "2%",
                }}
              >
                <Text
                  style={{
                    marginLeft: "0.5%",
                    fontFamily: "Roboto_700Bold",
                    fontSize: windowHeight <= 1080 ? 16 : 24,
                    color: "#04475C",
                  }}
                >
                  Add art details
                </Text>
              </View>

              <View style={{ height: "2%" }} />
              <View
                style={{
                  height: "60%",
                  width: "100%",
                  flexDirection: "row",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <View
                  style={{
                    height: "90%",
                    width: "60%",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <View style={styles.field}>
                    {designName != "" ? (
                      <Text
                        style={{
                          fontFamily: "Roboto_700Bold",
                          fontSize: windowHeight <= 1080 ? 12 : 18,
                          color: "#04475C",
                        }}
                      >
                        Design name
                      </Text>
                    ) : (
                      <View />
                    )}

                    <TextInput
                      style={{
                        borderColor: "black",
                        height: "60%",
                        padding: 5,
                        borderWidth: 1,
                        borderRadius: 4,
                      }}
                      placeholder={"Design name"}
                      onChangeText={(newText) => setDesignName(newText)}
                    ></TextInput>
                  </View>

                  <View style={styles.field}>
                    {designNameArabic != "" ? (
                      <Text
                        style={{
                          fontWeight: "bold",
                          fontSize: windowHeight <= 1080 ? 12 : 18,
                          color: "#04475C",
                        }}
                      >
                        اسم التصميم
                      </Text>
                    ) : (
                      <View />
                    )}

                    <TextInput
                      style={{
                        borderColor: "black",
                        height: "60%",
                        padding: 5,
                        direction: "rtl",
                        borderWidth: 1,
                        borderRadius: 4,
                      }}
                      placeholder={"اسم التصميم"}
                      onChangeText={(newText) => setDesignNameArabic(newText)}
                    ></TextInput>
                  </View>
                  <View style={styles.field}>
                    {hashtags != "" ? (
                      <Text
                        style={{
                          fontFamily: "Roboto_700Bold",
                          fontSize: windowHeight <= 1080 ? 12 : 18,
                          color: "#04475C",
                        }}
                      >
                        #Hashtags
                      </Text>
                    ) : (
                      <View />
                    )}

                    <TextInput
                      style={{
                        borderColor: "black",
                        height: "60%",
                        padding: 5,
                        borderWidth: 1,
                        borderRadius: 4,
                      }}
                      placeholder={"#Hashtags"}
                      onChangeText={(newText) => setHashtags(newText)}
                    ></TextInput>
                  </View>
                  <View style={styles.field}>
                    {hashtagsArabic != "" ? (
                      <Text
                        style={{
                          fontWeight: "bold",
                          fontSize: windowHeight <= 1080 ? 12 : 18,
                          color: "#04475C",
                        }}
                      >
                        #هاشتاجات
                      </Text>
                    ) : (
                      <View />
                    )}

                    <TextInput
                      style={{
                        borderColor: "black",
                        height: "60%",
                        padding: 5,
                        direction: "rtl",
                        borderWidth: 1,
                        borderRadius: 4,
                      }}
                      placeholder={"#هاشتاجات"}
                      onChangeText={(newText) => setHashtagsArabic(newText)}
                    ></TextInput>
                  </View>
                </View>
                <View
                  style={{
                    height: "100%",
                    width: "40%",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <TouchableOpacity
                    style={{
                      height: "100%",
                      width: "100%",
                      alignItems: "center",
                      justifyContent: "center",
                    }}
                  >
                    <label
                      htmlFor="filePicker"
                      style={{
                        width: "100%",
                        height: "100%",
                      }}
                    >
                      <Image
                        source={imageView ? imageView : placeholder}
                        style={{
                          width: "100%",
                          height: "90%",
                          resizeMode: "contain",
                          borderRadius: 20,
                        }}
                      />
                    </label>
                  </TouchableOpacity>
                </View>
              </View>

              <View style={{ height: "5%" }} />
              <TouchableOpacity style={styles.button} onPress={addDesign}>
                <Text
                  style={{
                    fontFamily: "Roboto_400Regular",
                    fontSize: windowHeight <= 1080 ? 16 : 20,
                    color: "#FFFFFF",
                  }}
                >
                  Save art file
                </Text>
              </TouchableOpacity>
            </View>
          </View>
        ) : (
          <View />
        )}

        {isView ? (
          <View
            style={{
              position: "absolute",
              height: "100%",
              width: "100%",
              backgroundColor: "#C4C4C4AA",
              zIndex: 999,
              alignSelf: "center",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <View
              style={{
                width: "80%",
                height: "80%",
                alignItems: "center",
                backgroundColor: "#ffffff",
                borderRadius: 20,
                borderWidth: 1,
                justifyContent: "center",
              }}
            >
              <View
                style={{
                  height: "5%",
                  width: "5%",
                  alignSelf: "flex-end",
                  marginRight: "2%",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <TouchableOpacity
                  style={{
                    width: "100%",
                    alignSelf: "flex-end",
                    height: "3%",
                    marginRight: "2%",
                    padding: 10,
                    borderRadius: 4,

                    alignItems: "center",
                    justifyContent: "center",
                    borderRadius: 10,
                    shadowRadius: 10,
                    shadowColor: "#3D9EBC",
                    backgroundColor: "#3D9EBC",
                  }}
                  onPress={() => {
                    setIsView(false);
                  }}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_400Regular",
                      color: "#FFFFFF",

                      fontSize: windowHeight <= 1080 ? 12 : 16,
                    }}
                  >
                    Close
                  </Text>
                </TouchableOpacity>

                <TouchableOpacity
                  style={{
                    height: "80%",
                    width: "80%",
                    position: "absolute",
                    top: "120%",
                    marginBottom: "2%",
                  }}
                  onPress={deleteDesign}
                >
                  <Image
                    source={trash}
                    style={{
                      width: "100%",
                      height: "90%",
                      resizeMode: "contain",
                      borderRadius: 20,
                    }}
                  />
                </TouchableOpacity>
              </View>
              <View
                style={{
                  flexDirection: "row",
                  height: "3%",
                  width: "100%",
                  alignItems: "center",
                  justifyContent: "center",
                  marginTop: "2%",
                }}
              >
                <Text
                  style={{
                    marginLeft: "0.5%",
                    fontFamily: "Roboto_700Bold",
                    fontSize: windowHeight <= 1080 ? 16 : 24,
                    color: "#04475C",
                  }}
                >
                  Art Details
                </Text>
              </View>

              <View style={{ height: "2%" }} />
              <View
                style={{
                  height: "60%",
                  width: "100%",
                  flexDirection: "row",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <View
                  style={{
                    height: "90%",
                    width: "60%",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <View style={styles.field}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      Design name
                    </Text>

                    <TextInput
                      style={{
                        borderColor: "black",
                        height: "60%",
                        padding: 5,
                        borderWidth: 1,
                        borderRadius: 4,
                      }}
                      placeholder={selectedArt.designNameEN}
                      onChangeText={(newText) => setDesignName(newText)}
                      editable={false}
                    ></TextInput>
                  </View>

                  <View style={styles.field}>
                    <Text
                      style={{
                        fontWeight: "bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      اسم التصميم
                    </Text>

                    <TextInput
                      style={{
                        borderColor: "black",
                        height: "60%",
                        padding: 5,
                        direction: "rtl",
                        borderWidth: 1,
                        borderRadius: 4,
                      }}
                      placeholder={selectedArt.designNameAR}
                      editable={false}
                      onChangeText={(newText) => setDesignNameArabic(newText)}
                    ></TextInput>
                  </View>
                  <View style={styles.field}>
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      #Hashtags
                    </Text>

                    <TextInput
                      style={{
                        borderColor: "black",
                        height: "60%",
                        padding: 5,
                        borderWidth: 1,
                        borderRadius: 4,
                      }}
                      placeholder={selectedArt.tagEN}
                      editable={false}
                      onChangeText={(newText) => setHashtags(newText)}
                    ></TextInput>
                  </View>
                  <View style={styles.field}>
                    <Text
                      style={{
                        fontWeight: "bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      #هاشتاجات
                    </Text>

                    <TextInput
                      style={{
                        borderColor: "black",
                        height: "60%",
                        padding: 5,
                        direction: "rtl",
                        borderWidth: 1,
                        borderRadius: 4,
                      }}
                      placeholder={selectedArt.tagAR}
                      editable={false}
                      onChangeText={(newText) => setHashtagsArabic(newText)}
                    ></TextInput>
                  </View>
                </View>
                <View
                  style={{
                    height: "100%",
                    width: "40%",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <Image
                    source={{ uri: selectedArt.designFiles }}
                    style={{
                      width: "100%",
                      height: "90%",
                      resizeMode: "contain",
                      borderRadius: 20,
                    }}
                  />
                </View>
              </View>

              <View style={{ height: "5%" }} />
              {/* <TouchableOpacity style={styles.button} onPress={addDesign}>
                <Text
                  style={{
                    fontFamily: "Roboto_400Regular",
                    fontSize: windowHeight <= 1080 ? 16 : 20,
                    color: "#FFFFFF",
                  }}
                >
                  Save art file
                </Text>
              </TouchableOpacity> */}
            </View>
          </View>
        ) : (
          <View />
        )}
        <View style={{ position: "absolute", bottom: 50, left: 50 }}>
          <Text>{config.version}</Text>
        </View>
        <View
          style={{
            height: "11%",
            width: "100%",
            left: 0,
            top: 0,
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            alignItems: "center",
            justifyContent: "flex-start",

            flexDirection: "row",
          }}
        >
          <View style={{ height: "15%" }}></View>
          <Image
            source={logo}
            style={{
              width: 214,
              height: "70%",
              marginLeft: 20,
              resizeMode: "contain",
            }}
          />
          <View style={{ height: "15%" }}></View>
          <View
            style={{
              height: "30%",
              width: "8%",
              alignItems: "center",
              position: "absolute",
              right: "2%",
            }}
          >
            <NavigationBar
              walletIsDimmed={true}
              notifications={[]}
              navigation={navigation}
            />
          </View>
        </View>
        <View
          style={{
            height: "5%",
            width: "100%",
            alignItems: "center",
            justifyContent: "center",
            flexDirection: "row",
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            zIndex: -1,
          }}
        >
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Kairo Merch Shop");
            }}
          >
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 14 : 20,
                color: "#04475C",
              }}
            >
              My shop
            </Text>
          </TouchableOpacity>

          <View style={{ width: "8%" }} />

          <TouchableOpacity>
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 14 : 20,
                color: "#C54160",
              }}
            >
              Art files
            </Text>
          </TouchableOpacity>
          <View style={{ width: "8%" }} />
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Kairo Merch Orders");
            }}
          >
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 14 : 20,
                color: "#04475C",
              }}
            >
              Orders
            </Text>
          </TouchableOpacity>
        </View>
        {/* <TouchableOpacity
                style={{ width: "3%", height: 25 }}
                onPress={() => {
                  navigation.pop();
                }}
              >
                <Image
                  source={previous}
                  style={{
                    width: windowHeight <= 1080 ? 60 : 80,
                    height: windowHeight <= 1080 ? 40 : 50,
                    resizeMode: "contain",
                    marginLeft: 20,
                  }}
                />
              </TouchableOpacity> */}
        <TouchableOpacity
          style={{
            width: "10%",
            height: "5%",
            alignSelf: "flex-start",
            marginLeft: "2%",
            marginTop: "10%",
            position: "absolute",
            zIndex: 0,
          }}
        >
          <Image
            source={trends}
            style={{ width: "100%", height: "100%", resizeMode: "contain" }}
          />
        </TouchableOpacity>
        <View
          style={{
            height: "4%",
            width: "60%",
            borderWidth: 1,
            borderColor: "#C4C4C4",
            marginTop: "3%",
            borderRadius: 6,
            flexDirection: "row",
            alignSelf: "center",
            shadowRadius: 4,
            zIndex: -1,
          }}
        >
          <Image
            source={magnify}
            style={{
              width: "5%",
              height: "100%",
              resizeMode: "contain",
            }}
          />
          <TextInput
            style={{
              borderColor: "black",
              height: "100%",
              width: "100%",
              fontSize: windowHeight <= 1080 ? 12 : 18,
              fontFamily: "Roboto_400Regular",
              padding: 5,
            }}
            autoCorrect={false}
            placeholder={"Search.."}
            onChangeText={(newText) => setSearch(newText)}
          ></TextInput>
        </View>
        {status != 4 ? (
          <TouchableOpacity
            style={{
              position: "absolute",
              right: "2%",
              top: "21%",
              width: "10%",
              height: "5%",
              borderWidth: 1,
              borderRadius: 50,
              flexDirection: "row",
              alignItems: "center",
              shadowRadius: 10,
              shadowColor: "#EB5276",
              zIndex: -1,
            }}
            onPress={() => {
              if (rawDesignsState.length == 0) {
                navigation.navigate("Kairo Merch Advisor", { status: status });
              } else {
                if (status == 2) {
                  config.status = 3;
                  document.cookie = "config=" + JSON.stringify(config) + ";";
                  navigation.navigate("Kairo Merch Advisor", {
                    status: 3,
                  });
                } else {
                  navigation.navigate("Kairo Merch Advisor", {
                    status: status,
                  });
                }
              }
            }}
          >
            <View
              style={{
                height: "100%",
                width: "22%",
                borderRadius: 50,
                backgroundColor: "#F7567C",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Text
                style={{
                  fontFamily: "Roboto_700Bold",
                  fontSize: windowHeight <= 1080 ? 12 : 16,
                  color: "#FFFFFF",
                }}
              >
                {status == 1
                  ? "25%"
                  : status == 2
                  ? "50%"
                  : status == 3
                  ? "75%"
                  : "100%"}
              </Text>
            </View>
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 12 : 16,
                color: "#3D9EBC",
                marginLeft: "5%",
              }}
            >
              Kairomerch Advisor
            </Text>
          </TouchableOpacity>
        ) : (
          <View />
        )}
        <View
          style={{
            marginTop: "2%",
            height: "4%",
            width: "25%",
            alignSelf: "center",
            alignItems: "center",
            flexDirection: "row",
            justifyContent: "space-between",
            zIndex: -2,
          }}
        >
          <TouchableOpacity
            style={{
              height: "100%",
              width: "20%",
              backgroundColor: artType == "All" ? "#EB5276" : "#FFB1C366",
              alignItems: "center",
              justifyContent: "center",
              borderRadius: 50,
            }}
            onPress={() => {
              setArtType("All");
            }}
          >
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 16 : 24,
                color: artType == "All" ? "#FFFFFF" : "#000000",
              }}
            >
              All
            </Text>
          </TouchableOpacity>
          <TouchableOpacity
            style={{
              height: "100%",
              width: "35%",
              backgroundColor: artType == "Ordered" ? "#EB5276" : "#FFB1C366",
              alignItems: "center",
              justifyContent: "center",
              borderRadius: 50,
            }}
            onPress={() => {
              setArtType("Ordered");
            }}
          >
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 16 : 24,
                color: artType == "Ordered" ? "#FFFFFF" : "#000000",
              }}
            >
              Ordered
            </Text>
          </TouchableOpacity>
          <TouchableOpacity
            style={{
              height: "100%",
              width: "35%",
              backgroundColor: artType == "Saved" ? "#EB5276" : "#FFB1C366",
              alignItems: "center",
              justifyContent: "center",
              borderRadius: 50,
            }}
            onPress={() => {
              setArtType("Saved");
            }}
          >
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 16 : 24,
                color: artType == "Saved" ? "#FFFFFF" : "#000000",
              }}
            >
              Saved
            </Text>
          </TouchableOpacity>
        </View>
        <View
          style={{
            height: "52%",
            width: "90%",
            borderWidth: 1,
            borderColor: "#C4C4C4",
            marginTop: "1%",
            borderRadius: 4,
            alignItems: "center",
            alignSelf: "center",
            zIndex: -2,
          }}
        >
          {rawDesignsState.length == 0 ? (
            <View
              style={{
                height: "90%",
                width: "100%",
                alignItems: "center",
                justifyContent: "flex-start",
                marginTop: "2%",
              }}
            >
              <Text
                style={{
                  fontFamily: "Roboto_700Bold",
                  fontSize: windowHeight <= 1080 ? 20 : 32,
                  color: "#C54160",
                }}
              >
                Upload your first art file
              </Text>
              <Image
                source={no_arts}
                style={{
                  marginTop: "5%",
                  width: "60%",
                  height: "60%",
                  resizeMode: "contain",
                  alignSelf: "center",
                }}
              />
            </View>
          ) : (
            <ScrollView
              style={{ width: "100%", height: "100%", borderRadius: 20 }}
            >
              {designs.map((line) => (
                <View
                  style={{
                    height: windowWidth * 0.1,
                    width: "100%",
                    flexDirection: "row",
                    justifyContent: "space-between",
                    marginLeft: windowWidth * 0.02,
                    marginBottom: "3%",
                    marginTop: "1%",
                  }}
                  key={line.lineId}
                >
                  {line.data.map((design) => (
                    <View
                      style={{
                        width: "15%",
                        height: "100%",
                      }}
                    >
                      <TouchableOpacity
                        style={{
                          width: windowWidth * 0.1,
                          height: "auto",
                          borderRadius: 15,
                          alignItems: "center",
                          justifyContent: "center",
                          shadowRadius: 20,
                          shadowColor: "#C4C4C4",
                        }}
                        key={design.designID}
                        onPress={() => {
                          setSelectedArt(design);
                          setIsView(true);
                        }}
                      >
                        <View
                          style={{
                            height: "80%",
                            width: windowWidth * 0.1,
                          }}
                        >
                          <Image
                            source={design.designFiles}
                            style={{
                              width: windowWidth * 0.1,
                              height: windowWidth * 0.1,
                              resizeMode: "contain",
                              borderRadius: 15,
                            }}
                          />
                        </View>
                        <View
                          style={{
                            height: "20%",
                            width: "100%",
                            backgroundColor: "#FFFFFF",
                            alignItems: "center",
                            justifyContent: "center",
                            borderBottomLeftRadius: 15,
                            borderBottomRightRadius: 15,
                          }}
                        >
                          <Text
                            style={{
                              fontFamily: "Roboto_700Bold",
                              fontSize: windowHeight <= 1080 ? 14 : 18,
                              color: "#333333",
                            }}
                          >
                            {design.designNameEN}
                          </Text>
                        </View>
                      </TouchableOpacity>
                    </View>
                  ))}
                </View>
              ))}
            </ScrollView>
          )}
        </View>
        {!isAdd ? (
          <TouchableOpacity
            style={styles.button}
            onPress={() => {
              setIsAdd(true);
            }}
          >
            <View style={{ width: "5%" }} />
            <Text
              style={{
                fontFamily: "Roboto_400Regular",
                fontSize: windowHeight <= 1080 ? 20 : 28,
                color: "#FFFFFF",
              }}
            >
              +
            </Text>
            <View style={{ width: "15%" }} />
            <Text
              style={{
                fontFamily: "Roboto_400Regular",
                fontSize: windowHeight <= 1080 ? 16 : 20,
                color: "#FFFFFF",
              }}
            >
              Add new art file
            </Text>
          </TouchableOpacity>
        ) : (
          <View />
        )}

        {isLoading ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFFCC",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "20%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 500,
                alignSelf: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Image
                source={loading}
                style={{
                  width: "90%",
                  height: "40%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "70%",
                }}
              />
            </View>
          </View>
        ) : (
          <View />
        )}

        {isError ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFF99",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "30%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 50,
                alignSelf: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 12 : 16,
                  color: "#333333",
                  marginTop: "10%",
                }}
              >
                {errMsg}
              </Text>

              <View style={{ height: "10%" }} />
              <TouchableOpacity
                style={{
                  width: "20%",
                  alignSelf: "center",
                  height: "10%",

                  backgroundColor: "#888888",
                  alignItems: "center",
                  justifyContent: "center",
                  borderRadius: 10,
                }}
                onPress={() => {
                  setError(false);
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    color: "#FFFFFF",
                    fontSize: windowHeight <= 1080 ? 12 : 16,
                  }}
                >
                  Ok
                </Text>
              </TouchableOpacity>
            </View>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

export default Art;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    marginTop: "2%",
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    borderRadius: 4,
    width: "15%",
    height: "4%",
    padding: 8,
    shadowRadius: 10,
    backgroundColor: "#3D9EBC",
    zIndex: 999,
    shadowColor: "#3D9EBC",

    flexDirection: "row",
  },
  button2: {
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    width: "8%",
    height: "2%",
    padding: 18,
    shadowRadius: 5,
    backgroundColor: "#3D9EBC",
  },

  gap: {
    height: 10,
  },
  field: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "80%",
    height: "18%",
    padding: 8,
    backgroundColor: "white",
  },
  subField: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "48%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  pwfield: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 5,
    width: "100%",
    height: "25%",
    padding: 15,
    backgroundColor: "white",
    flexDirection: "row",
  },
  textInputContainer: {
    marginBottom: 20,
    marginTop: 20,
  },

  roundedTextInput: {
    borderRadius: 10,
    backgroundColor: "white",
    borderWidth: 1,
    marginLeft: 20,
    marginRight: 20,
  },
});
