import {
  Animated,
  StyleSheet,
  Text,
  View,
  ImageBackground,
  Image,
  Easing,
  TouchableOpacity,
  TextInput,
  Dimensions,
  Alert,
  CheckBox,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import OTPTextView from "react-native-otp-textinput";
import DropDownPicker from "react-native-dropdown-picker";
import * as ImagePicker from "expo-image-picker";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import NavigationBar from "../navigation-bar";
import config from "../../../config";
import axios from "axios";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var logo = require("../../../assets/logo.png");
var previous = require("../../../assets/Previous.png");
var loading = require("../../../assets/loading.gif");
var km = require("../../../assets/icon.png");
var notification = require("../../../assets/notification.png");
var vcash = require("../../../assets/vcash.png");
var ocash = require("../../../assets/ocash.png");
var ecash = require("../../../assets/ecash.png");
var btransfer = require("../../../assets/btransfer.png");
var paypal = require("../../../assets/paypal.png");

var PaymentMethod = ({ route, navigation }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const [selected, setSelected] = useState("btransfer");
  const [pm, setPm] = useState("BT");

  const [name, setName] = useState("");
  const [number, setNumber] = useState("01");
  const [id, setID] = useState("");
  const [bankName, setBankName] = useState("");
  const [bankNumber, setBankNumber] = useState("");
  const [ibanNumber, setIbanNumber] = useState("");
  const [swiftCode, setSwiftCode] = useState("");
  const [username, setUsername] = useState("");
  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);

  const [errMsg, setErrorMsg] = useState("");

  var vcashHandler = () => {
    setSelected("vcash");
    setPm("V");
    setNumber("01");
    setName("");
    setID("");
  };
  var ocashHandler = () => {
    setSelected("ocash");
    setPm("O");
    setNumber("01");
    setName("");
    setID("");
  };
  var ecashHandler = () => {
    setSelected("ecash");
    setPm("E");
    setNumber("01");
    setName("");
    setID("");
  };

  var bankHandler = () => {
    setSelected("btransfer");
    setPm("BT");
  };

  var paypalHandler = () => {
    setSelected("paypal");
    setPm("PP");
  };

  var savePaymentMethod = () => {
    if (selected == "paypal") {
      if (!username.includes(".com") || !username.includes("@")) {
        setError(true);
        setErrorMsg("Please enter a valid account's email address");
        return;
      }
    } else if (selected == "btransfer") {
      if (bankName == "" || bankNumber == "") {
        setError(true);
        setErrorMsg("Please enter the required fields");
        return;
      }
    } else if (
      selected == "vcash" ||
      selected == "ocash" ||
      selected == "ecash"
    ) {
      if (number == "" || name == "" || id == "") {
        setError(true);
        setErrorMsg("Please enter the required fields");
        return;
      }
    }
    setLoading(true);

    //Save to BE

    try {
      let form_data = new FormData();
      form_data.append("preferredPaymentMethod", pm);
      form_data.append("payment_owner_name", name);
      form_data.append("payment_mphone", number);
      form_data.append("payment_national_id", id);
      form_data.append("bank_account_name", bankName);
      form_data.append("bank_account_number", bankNumber);
      form_data.append("iban_number", ibanNumber);
      form_data.append("swift_code", swiftCode);
      form_data.append("username", username);

      axios
        .patch(
          config.host +
            "/api/profiles/createArtistFinancialAccount/?token=" +
            config.token,
          form_data,
          {
            headers: {
              "Content-Type": "multipart/form-data",
            },
          }
        )
        .then((response) => {
          setLoading(false);
          config.status = 2;
          document.cookie = "config=" + JSON.stringify(config) + ";";
          navigation.navigate("Kairo Merch Advisor", { status: 2 });
        })
        .catch((err) => {
          setLoading(false);
          setError(true);
          setErrorMsg(err.request.response);
        });
    } catch (e) {
      setLoading(false);
      setError(true);
      setErrorMsg(e.message);
    }
  };

  var skipToAdvisor = () => {
    config.status = 1;
    document.cookie = "config=" + JSON.stringify(config) + ";";
    navigation.navigate("Kairo Merch Advisor", { status: 1 });
  };

  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View style={{ width: "100%", height: "100%", overflow: "hidden" }}>
        <View
          style={{
            height: "11%",
            width: "100%",
            left: 0,
            top: 0,
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            alignItems: "center",
            justifyContent: "flex-start",

            flexDirection: "row",
          }}
        >
          <View style={{ height: "15%" }}></View>
          <Image
            source={logo}
            style={{
              width: 214,
              height: "70%",
              marginLeft: 20,
              resizeMode: "contain",
            }}
          />
          <View style={{ height: "15%" }}></View>
          <View
            style={{
              height: "30%",
              width: "8%",
              alignItems: "center",
              position: "absolute",
              right: "2%",
            }}
          >
            <NavigationBar
              walletIsDimmed={true}
              notifications={[
                {
                  icon: notification,
                  text: "Welcome abroad! your account has been verified!",
                  date: Date.now(),
                },
              ]}
              navigation={navigation}
            />
          </View>
        </View>
        <TouchableOpacity
          style={{ width: "3%", height: 25 }}
          onPress={() => {
            navigation.pop();
          }}
        >
          <Image
            source={previous}
            style={{
              width: windowHeight <= 1080 ? 60 : 80,
              height: windowHeight <= 1080 ? 40 : 50,
              resizeMode: "contain",
              marginLeft: 20,
            }}
          />
        </TouchableOpacity>

        <View
          style={{
            width: "100%",
            height: "87%",
            flexDirection: "row",

            zIndex: -1,
          }}
        >
          <View style={{ width: "100%", height: "100%", alignItems: "center" }}>
            <View
              style={{
                flexDirection: "row",
                height: "3%",
                width: "100%",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 16 : 24,
                  color: "#04475C",
                }}
              >
                Step 2
              </Text>
              <Text
                style={{
                  marginLeft: "0.5%",
                  fontFamily: "Roboto_700Bold",
                  fontSize: windowHeight <= 1080 ? 24 : 32,
                  color: "#04475C",
                }}
              >
                Setting up your financial account
              </Text>
            </View>

            <View
              style={{
                flexDirection: "row",
                height: "3%",
                width: "100%",
                alignItems: "center",
                justifyContent: "center",
                marginTop: "2%",
              }}
            >
              <Text
                style={{
                  fontFamily: "Roboto_700Bold",
                  fontSize: windowHeight <= 1080 ? 16 : 24,
                  color: "#04475C",
                }}
              >
                2nd
              </Text>
              <Text
                style={{
                  marginLeft: "0.5%",
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 16 : 24,
                  color: "#04475C",
                }}
              >
                Setup how would you like to get paid
              </Text>
            </View>

            <View style={{ height: "2%" }} />
            <View
              style={{
                height: "60%",
                width: "100%",
                flexDirection: "row",
              }}
            >
              <View
                style={{
                  height: "90%",
                  width: "50%",
                  borderRightWidth: 1,
                  borderRightColor: "black",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <View
                  style={{
                    flexDirection: "row",

                    height: "40%",
                    width: "100%",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <TouchableOpacity
                    style={{
                      width: "25%",
                      height: "100%",
                      margin: 20,
                      borderWidth: selected == "btransfer" ? 1 : 0,
                      backgroundColor: "#E2F5FB",
                      borderRadius: 20,
                    }}
                    onPress={bankHandler}
                  >
                    <Image
                      source={btransfer}
                      style={{
                        width: "100%",
                        height: "100%",
                        resizeMode: "contain",
                      }}
                    />
                  </TouchableOpacity>
                  <TouchableOpacity
                    style={{
                      width: "25%",
                      height: "100%",
                      margin: 20,
                      borderWidth: selected == "paypal" ? 1 : 0,
                      backgroundColor: "#E2F5FB",
                      borderRadius: 20,
                    }}
                    onPress={paypalHandler}
                  >
                    <Image
                      source={paypal}
                      style={{
                        width: "100%",
                        height: "100%",
                        resizeMode: "contain",
                      }}
                    />
                  </TouchableOpacity>
                </View>

                <View
                  style={{
                    flexDirection: "row",
                    marginTop: "5%",
                    height: "40%",
                    width: "100%",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <TouchableOpacity
                    style={{
                      width: "25%",
                      height: "100%",
                      margin: 20,
                      borderWidth: selected == "vcash" ? 1 : 0,
                      backgroundColor: "#E2F5FB",
                      borderRadius: 20,
                    }}
                    onPress={vcashHandler}
                  >
                    <Image
                      source={vcash}
                      style={{
                        width: "100%",
                        height: "100%",
                        resizeMode: "contain",
                      }}
                    />
                  </TouchableOpacity>
                  <TouchableOpacity
                    style={{
                      width: "25%",
                      height: "100%",
                      margin: 20,
                      borderWidth: selected == "ocash" ? 1 : 0,
                      backgroundColor: "#E2F5FB",
                      borderRadius: 20,
                    }}
                    onPress={ocashHandler}
                  >
                    <Image
                      source={ocash}
                      style={{
                        width: "100%",
                        height: "100%",
                        resizeMode: "contain",
                      }}
                    />
                  </TouchableOpacity>
                  <TouchableOpacity
                    style={{
                      width: "25%",
                      height: "100%",
                      margin: 20,
                      borderWidth: selected == "ecash" ? 1 : 0,
                      backgroundColor: "#E2F5FB",
                      borderRadius: 20,
                    }}
                    onPress={ecashHandler}
                  >
                    <Image
                      source={ecash}
                      style={{
                        width: "100%",
                        height: "100%",
                        resizeMode: "contain",
                      }}
                    />
                  </TouchableOpacity>
                </View>
              </View>
              <View
                style={{
                  height: "90%",
                  width: "50%",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                {selected == "btransfer" ? (
                  <View
                    style={{
                      height: "100%",
                      width: "100%",
                      alignItems: "center",
                      justifyContent: "center",
                    }}
                  >
                    <View style={styles.field}>
                      <Text
                        style={{
                          fontFamily: "Roboto_700Bold",
                          fontSize: windowHeight <= 1080 ? 12 : 18,
                          color: "#04475C",
                        }}
                      >
                        Account Owner's Name
                      </Text>

                      <TextInput
                        style={{ borderColor: "black" }}
                        placeholder={"Account Owner's Name"}
                        onChangeText={(newText) => setBankName(newText)}
                      ></TextInput>
                    </View>
                    <View style={styles.field}>
                      <Text
                        style={{
                          fontFamily: "Roboto_700Bold",
                          fontSize: windowHeight <= 1080 ? 12 : 18,
                          color: "#04475C",
                        }}
                      >
                        Bank Account Number
                      </Text>

                      <TextInput
                        style={{ borderColor: "black" }}
                        placeholder={"Bank Account"}
                        onChangeText={(newText) => setBankNumber(newText)}
                      ></TextInput>
                    </View>
                    <View style={styles.field}>
                      <Text
                        style={{
                          fontFamily: "Roboto_700Bold",
                          fontSize: windowHeight <= 1080 ? 12 : 18,
                          color: "#04475C",
                        }}
                      >
                        Swift Code
                      </Text>

                      <TextInput
                        style={{ borderColor: "black" }}
                        placeholder={"Swift Code"}
                        onChangeText={(newText) => setSwiftCode(newText)}
                      ></TextInput>
                    </View>
                    <View style={styles.field}>
                      <Text
                        style={{
                          fontFamily: "Roboto_700Bold",
                          fontSize: windowHeight <= 1080 ? 12 : 18,
                          color: "#04475C",
                        }}
                      >
                        IBAN
                      </Text>

                      <TextInput
                        style={{ borderColor: "black" }}
                        placeholder={"IBAN"}
                        onChangeText={(newText) => setIbanNumber(newText)}
                      ></TextInput>
                    </View>
                  </View>
                ) : selected == "paypal" ? (
                  <View
                    style={{
                      height: "100%",
                      width: "100%",
                      alignItems: "center",
                      justifyContent: "center",
                    }}
                  >
                    <View style={styles.field}>
                      <Text
                        style={{
                          fontFamily: "Roboto_700Bold",
                          fontSize: windowHeight <= 1080 ? 12 : 18,
                          color: "#04475C",
                        }}
                      >
                        Account's email address
                      </Text>

                      <TextInput
                        style={{ borderColor: "black" }}
                        placeholder={"Email"}
                        onChangeText={(newText) => setUsername(newText)}
                      ></TextInput>
                    </View>
                  </View>
                ) : (
                  <View
                    style={{
                      height: "100%",
                      width: "100%",
                      alignItems: "center",
                      justifyContent: "center",
                    }}
                  >
                    <View style={styles.field}>
                      <Text
                        style={{
                          fontFamily: "Roboto_700Bold",
                          fontSize: windowHeight <= 1080 ? 12 : 18,
                          color: "#04475C",
                        }}
                      >
                        Owner's Name
                      </Text>

                      <TextInput
                        style={{ borderColor: "black" }}
                        placeholder={"Owner's Name"}
                        onChangeText={(newText) => setName(newText)}
                      ></TextInput>
                    </View>
                    <View style={styles.field}>
                      <Text
                        style={{
                          fontFamily: "Roboto_700Bold",
                          fontSize: windowHeight <= 1080 ? 12 : 18,
                          color: "#04475C",
                        }}
                      >
                        Mobile Number
                      </Text>

                      <TextInput
                        style={{ borderColor: "black" }}
                        placeholder={"Mobile Number"}
                        onChangeText={(newText) => setNumber(newText)}
                        value={
                          selected == "vcash"
                            ? number == "01"
                              ? number + "0"
                              : number
                            : selected == "ocash"
                            ? number == "01"
                              ? number + "2"
                              : number
                            : selected == "ecash"
                            ? number == "01"
                              ? number + "1"
                              : number
                            : number == "01"
                            ? number + "0"
                            : number
                        }
                      ></TextInput>
                    </View>
                    <View style={styles.field}>
                      <Text
                        style={{
                          fontFamily: "Roboto_700Bold",
                          fontSize: windowHeight <= 1080 ? 12 : 18,
                          color: "#04475C",
                        }}
                      >
                        Owner's National ID
                      </Text>

                      <TextInput
                        style={{ borderColor: "black" }}
                        placeholder={"Owner's National ID"}
                        onChangeText={(newText) => setID(newText)}
                      ></TextInput>
                    </View>
                  </View>
                )}
              </View>
            </View>

            <View style={{ height: "5%" }} />
            <View
              style={{
                height: "10%",
                width: "100%",
                flexDirection: "row",
              }}
            >
              <View style={{ width: "7%" }} />
              <TouchableOpacity style={styles.button2} onPress={skipToAdvisor}>
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    fontSize: windowHeight <= 1080 ? 14 : 18,
                    color: "#333333",
                  }}
                >
                  Skip for now
                </Text>
              </TouchableOpacity>
              <View style={{ width: "50%" }} />
              <TouchableOpacity
                style={styles.button}
                onPress={savePaymentMethod}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    fontSize: windowHeight <= 1080 ? 14 : 18,
                    color: "#FFFFFF",
                  }}
                >
                  Next
                </Text>
              </TouchableOpacity>
              <View style={{ width: "7%" }} />
            </View>
          </View>
        </View>

        {isLoading ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFFCC",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "20%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 500,
                alignSelf: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Image
                source={loading}
                style={{
                  width: "90%",
                  height: "40%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "70%",
                }}
              />
            </View>
          </View>
        ) : (
          <View />
        )}

        {isError ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFF99",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "30%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 50,
                alignSelf: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 12 : 16,
                  color: "#333333",
                  marginTop: "10%",
                }}
              >
                {errMsg}
              </Text>

              <View style={{ height: "10%" }} />
              <TouchableOpacity
                style={{
                  width: "20%",
                  alignSelf: "center",
                  height: "10%",

                  backgroundColor: "#888888",
                  alignItems: "center",
                  justifyContent: "center",
                  borderRadius: 10,
                }}
                onPress={() => {
                  setError(false);
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    color: "#FFFFFF",
                    fontSize: windowHeight <= 1080 ? 12 : 16,
                  }}
                >
                  Ok
                </Text>
              </TouchableOpacity>
            </View>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

export default PaymentMethod;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    marginTop: "2%",
    alignItems: "center",
    justifyContent: "center",
    borderRadius: 50,
    width: "18%",
    height: "6%",
    padding: 18,
    shadowRadius: 10,
    backgroundColor: "#F7567C",
    zIndex: 999,
  },
  button2: {
    marginTop: "2%",
    alignItems: "center",
    justifyContent: "center",
    borderRadius: 50,
    width: "18%",
    height: "6%",
    padding: 18,
    shadowRadius: 10,
    backgroundColor: "#FFFFFF",
    borderColor: "black",
    borderWidth: 1,
    zIndex: 999,
  },

  gap: {
    height: 10,
  },
  field: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 20,
    width: "80%",
    height: "15%",
    padding: 8,
    backgroundColor: "white",
  },
  subField: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "48%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  pwfield: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 5,
    width: "100%",
    height: "25%",
    padding: 15,
    backgroundColor: "white",
    flexDirection: "row",
  },
  textInputContainer: {
    marginBottom: 20,
    marginTop: 20,
  },

  roundedTextInput: {
    borderRadius: 10,
    backgroundColor: "white",
    borderWidth: 1,
    marginLeft: 20,
    marginRight: 20,
  },
});
