import {
  Animated,
  StyleSheet,
  Text,
  View,
  ImageBackground,
  Image,
  Easing,
  TouchableOpacity,
  TextInput,
  Dimensions,
  Alert,
  CheckBox,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import OTPTextView from "react-native-otp-textinput";
import DropDownPicker from "react-native-dropdown-picker";
import * as ImagePicker from "expo-image-picker";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import NavigationBar from "../navigation-bar";
import config from "../../../config";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var logo = require("../../../assets/logo.png");
var previous = require("../../../assets/Previous.png");
var loading = require("../../../assets/loading.gif");
var km = require("../../../assets/icon.png");
var notification = require("../../../assets/notification.png");
var commission_plan = require("../../../assets/commission-plan.png");
var advance = require("../../../assets/advanced.png");

var CommissionPlan = ({ route, navigation }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);

  const [errMsg, setErrorMsg] = useState("");

  var paymentMethodHandler = () => {
    navigation.navigate("Kairo Merch Financial Account Payment Method");
  };
  var skipToAdvisor = () => {
    config.status = 1;
    document.cookie = "config=" + JSON.stringify(config) + ";";
    navigation.navigate("Kairo Merch Advisor", { status: 1 });
  };

  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: "100%",
          height: "100%",
          overflow: "hidden",
          backgroundColor: "#FFFFFF",
        }}
      >
        <View
          style={{
            height: "11%",
            width: "100%",
            left: 0,
            top: 0,
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            alignItems: "center",
            justifyContent: "flex-start",
            flexDirection: "row",
          }}
        >
          <View style={{ height: "15%" }}></View>
          <Image
            source={logo}
            style={{
              width: 214,
              height: "70%",
              marginLeft: 20,
              resizeMode: "contain",
            }}
          />
          <View style={{ height: "15%" }}></View>
          <View
            style={{
              height: "30%",
              width: "8%",
              alignItems: "center",
              position: "absolute",
              right: "2%",
            }}
          >
            <NavigationBar
              walletIsDimmed={true}
              notifications={[
                {
                  icon: notification,
                  text: "Welcome abroad! your account has been verified!",
                  date: Date.now(),
                },
              ]}
              navigation={navigation}
            />
          </View>
        </View>
        <TouchableOpacity
          style={{ width: "3%", height: 25 }}
          onPress={() => {
            navigation.pop();
          }}
        >
          <Image
            source={previous}
            style={{
              width: windowHeight <= 1080 ? 60 : 80,
              height: windowHeight <= 1080 ? 40 : 50,
              resizeMode: "contain",
              marginLeft: 20,
            }}
          />
        </TouchableOpacity>

        <View
          style={{
            width: "100%",
            height: "87%",
            flexDirection: "row",

            zIndex: -1,
          }}
        >
          <View style={{ width: "100%", height: "100%", alignItems: "center" }}>
            <View
              style={{
                flexDirection: "row",
                height: "3%",
                width: "100%",
                alignItems: "center",
                justifyContent: "center",
              }}
            >
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 16 : 24,
                  color: "#04475C",
                }}
              >
                Step 2
              </Text>
              <Text
                style={{
                  marginLeft: "0.5%",
                  fontFamily: "Roboto_700Bold",
                  fontSize: windowHeight <= 1080 ? 24 : 32,
                  color: "#04475C",
                }}
              >
                Setting up your financial account
              </Text>
            </View>

            <View
              style={{
                flexDirection: "row",
                height: "3%",
                width: "100%",
                alignItems: "center",
                justifyContent: "center",
                marginTop: "2%",
              }}
            >
              <Text
                style={{
                  fontFamily: "Roboto_700Bold",
                  fontSize: windowHeight <= 1080 ? 16 : 24,
                  color: "#04475C",
                }}
              >
                1st
              </Text>
              <Text
                style={{
                  marginLeft: "0.5%",
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 16 : 24,
                  color: "#04475C",
                }}
              >
                Preview your commission plan
              </Text>
            </View>

            <Image
              source={config.plan == "A" ? advance : commission_plan}
              style={{
                height: 0.55 * windowHeight,
                width: 0.7 * windowWidth,
                resizeMode: "center",
                marginTop: "2%",
                zIndex: -1,
              }}
            />
            <View style={{ height: "5%" }} />
            <View
              style={{
                height: "10%",
                width: "100%",
                flexDirection: "row",
              }}
            >
              <View style={{ width: "7%" }} />
              <TouchableOpacity style={styles.button2} onPress={skipToAdvisor}>
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    fontSize: windowHeight <= 1080 ? 14 : 18,
                    color: "#333333",
                  }}
                >
                  Skip for now
                </Text>
              </TouchableOpacity>
              <View style={{ width: "50%" }} />
              <TouchableOpacity
                style={styles.button}
                onPress={paymentMethodHandler}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    fontSize: windowHeight <= 1080 ? 14 : 18,
                    color: "#FFFFFF",
                  }}
                >
                  Next
                </Text>
              </TouchableOpacity>
              <View style={{ width: "7%" }} />
            </View>
          </View>
        </View>

        {isLoading ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFFCC",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "20%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 500,
                alignSelf: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Image
                source={loading}
                style={{
                  width: "90%",
                  height: "40%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "70%",
                }}
              />
            </View>
          </View>
        ) : (
          <View />
        )}

        {isError ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFF99",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "30%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 50,
                alignSelf: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 12 : 16,
                  color: "#333333",
                  marginTop: "10%",
                }}
              >
                {errMsg}
              </Text>

              <View style={{ height: "10%" }} />
            </View>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

export default CommissionPlan;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    marginTop: "2%",
    alignItems: "center",
    justifyContent: "center",
    borderRadius: 50,
    width: "18%",
    height: "6%",
    padding: 18,
    shadowRadius: 10,
    backgroundColor: "#F7567C",
    zIndex: 999,
  },
  button2: {
    marginTop: "2%",
    alignItems: "center",
    justifyContent: "center",
    borderRadius: 50,
    width: "18%",
    height: "6%",
    padding: 18,
    shadowRadius: 10,
    backgroundColor: "#FFFFFF",
    borderColor: "black",
    borderWidth: 1,
    zIndex: 999,
  },

  gap: {
    height: 10,
  },
  field: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "96.5%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  subField: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "48%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  pwfield: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 5,
    width: "100%",
    height: "25%",
    padding: 15,
    backgroundColor: "white",
    flexDirection: "row",
  },
  textInputContainer: {
    marginBottom: 20,
    marginTop: 20,
  },

  roundedTextInput: {
    borderRadius: 10,
    backgroundColor: "white",
    borderWidth: 1,
    marginLeft: 20,
    marginRight: 20,
  },
});
