import {
  Animated,
  StyleSheet,
  Text,
  View,
  ImageBackground,
  Image,
  Easing,
  TouchableOpacity,
  TextInput,
  Dimensions,
  Alert,
  CheckBox,
  ScrollView,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import OTPTextView from "react-native-otp-textinput";
import DropDownPicker from "react-native-dropdown-picker";
import * as ImagePicker from "expo-image-picker";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import config from "../../config";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var wallet = require("../../assets/wallet.png");
var shop = require("../../assets/shop.png");
var bell = require("../../assets/bell.png");
var profile = require("../../assets/profile.png");
var no_notifications = require("../../assets/no-notification.png");

var NavigationBar = ({
  style,
  route,
  navigation,
  shopIsDimmed,
  walletIsDimmed,
  notificationIsDimmed,
  profileIsDimmed,
  notifications,
}) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);
  const [isApproved, setIsApproved] = useState(false);
  const [errMsg, setErrorMsg] = useState("");
  const [openNotification, setOpenNotification] = useState(false);
  const [openSettings, setOpenSettings] = useState(false);
  var logout = () => {
    setOpenSettings(false);
    config.token = "";
    document.cookie = "config=" + JSON.stringify(config) + ";";
    navigation.navigate("Kairo Merch Dashboard");
  };
  var profileSettings = () => {
    setOpenSettings(false);
    navigation.navigate("Kairo Merch Profile Settings");
  };
  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={[
          style,
          {
            zIndex: 999,
            height: "100%",
            width: "100%",
            flexDirection: "row",
            alignItems: "center",
            justifyContent: "center",
          },
        ]}
      >
        {walletIsDimmed ? (
          <View style={{ height: "100%", width: "25%" }}>
            <Image
              source={wallet}
              style={{
                width: "100%",
                height: "100%",
                resizeMode: "contain",
                opacity: "30%",
              }}
            />
          </View>
        ) : (
          <TouchableOpacity style={{ height: "100%", width: "25%" }}>
            <Image
              source={wallet}
              style={{ width: "100%", height: "100%", resizeMode: "contain" }}
            />
          </TouchableOpacity>
        )}

        {walletIsDimmed ? (
          <View style={{ height: "100%", width: "25%" }}>
            <Image
              source={shop}
              style={{
                width: "100%",
                height: "100%",
                resizeMode: "contain",
                opacity: "30%",
              }}
            />
          </View>
        ) : (
          <TouchableOpacity style={{ height: "100%", width: "25%" }}>
            <Image
              source={shop}
              style={{ width: "100%", height: "100%", resizeMode: "contain" }}
            />
          </TouchableOpacity>
        )}

        <TouchableOpacity
          style={{ height: "100%", width: "25%" }}
          onPress={() => {
            setOpenSettings(false);
            setOpenNotification(!openNotification);
          }}
        >
          <Image
            source={bell}
            style={{ width: "100%", height: "100%", resizeMode: "contain" }}
          />
        </TouchableOpacity>
        <TouchableOpacity
          style={{ height: "100%", width: "25%" }}
          onPress={() => {
            setOpenSettings(!openSettings);
            setOpenNotification(false);
          }}
        >
          <Image
            source={profile}
            style={{ width: "100%", height: "100%", resizeMode: "contain" }}
          />
        </TouchableOpacity>
        {openNotification ? (
          <View
            style={{
              position: "absolute",
              top: "120%",
              right: "35%",
              width: "300%",
              height: "1000%",

              backgroundColor: "white",
              zIndex: 999,
              borderRadius: 4,
              shadowRadius: 10,
              shadowColor: "grey",
              borderColor: "grey",
              alignItems: "center",
            }}
          >
            {notifications.length == 0 ? (
              <View
                style={{
                  marginTop: "5%",
                  height: "15%",
                  width: "90%",
                  flexDirection: "row",
                  alignItems: "center",
                  backgroundColor: "#E2F5FB",
                  borderRadius: 20,
                }}
              >
                <Image
                  source={no_notifications}
                  style={{ height: "50%", width: "10%", resizeMode: "contain" }}
                />
                <View
                  style={{
                    height: "80%",
                    width: "80%",
                    alignItems: "center",
                    justifyContent: "center",
                  }}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_400Regular",
                      fontSize: windowHeight <= 1080 ? 14 : 18,
                      color: "#333333",
                    }}
                  >
                    There is no notifications yet
                  </Text>
                </View>
              </View>
            ) : (
              <View
                style={{
                  width: "100%",
                  height: "100%",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <ScrollView
                  style={{
                    width: "100%",
                    height: "100%",
                  }}
                >
                  {notifications.map((notification) => (
                    <TouchableOpacity
                      style={{
                        marginTop: "5%",
                        height: 0.05 * windowHeight,
                        width: "90%",
                        flexDirection: "row",
                        alignItems: "center",
                        justifyContent: "center",
                        backgroundColor: "#E2F5FB",
                        borderRadius: 20,
                        alignSelf: "center",
                      }}
                    >
                      <Image
                        source={{ uri: notification.icon }}
                        style={{
                          height: "50%",
                          width: "10%",
                          resizeMode: "contain",
                        }}
                      />
                      <View
                        style={{
                          height: "80%",
                          width: "80%",
                          alignItems: "center",
                          justifyContent: "center",
                        }}
                      >
                        <Text
                          style={{
                            fontFamily: "Roboto_400Regular",
                            fontSize: windowHeight <= 1080 ? 14 : 18,
                            color: "#333333",
                          }}
                        >
                          {notification.text}
                        </Text>
                      </View>
                    </TouchableOpacity>
                  ))}
                </ScrollView>
              </View>
            )}
          </View>
        ) : (
          <View />
        )}
        {openSettings ? (
          <View
            style={{
              position: "absolute",
              top: "120%",
              right: "10%",
              width: "130%",
              height: "auto",
              backgroundColor: "white",
              zIndex: 999,
              borderRadius: 4,
              shadowRadius: 10,
              shadowColor: "grey",
              borderColor: "grey",
              alignItems: "center",
            }}
          >
            <TouchableOpacity
              style={{
                height: 0.05 * windowHeight,
                width: "100%",
                flexDirection: "row",
                alignItems: "center",
                justifyContent: "flex-start",
                borderBottomWidth: 1,
                alignSelf: "center",
              }}
              onPress={profileSettings}
            >
              <View
                style={{
                  alignItems: "center",
                  justifyContent: "center",
                  marginLeft: "10%",
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_400Regular",
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    color: "#333333",
                  }}
                >
                  Profile settings
                </Text>
              </View>
            </TouchableOpacity>
            {/* <TouchableOpacity
              style={{
                height: 0.05 * windowHeight,
                width: "100%",
                flexDirection: "row",
                alignItems: "center",
                justifyContent: "flex-start",
                borderBottomWidth: 1,
                alignSelf: "center",
              }}
            >
              <View
                style={{
                  alignItems: "center",
                  justifyContent: "center",
                  marginLeft: "10%",
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_400Regular",
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    color: "#333333",
                  }}
                >
                  Financial account
                </Text>
              </View>
            </TouchableOpacity> */}
            <TouchableOpacity
              style={{
                height: 0.05 * windowHeight,
                width: "100%",
                flexDirection: "row",
                alignItems: "center",
                justifyContent: "flex-start",
                borderBottomWidth: 1,
                alignSelf: "center",
              }}
              onPress={() => {
                navigation.navigate("Kairo Merch Policies and Regulations");
              }}
            >
              <View
                style={{
                  alignItems: "center",
                  justifyContent: "center",
                  marginLeft: "10%",
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_400Regular",
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    color: "#333333",
                  }}
                >
                  Policies and regulations
                </Text>
              </View>
            </TouchableOpacity>
            <TouchableOpacity
              style={{
                height: 0.05 * windowHeight,
                width: "100%",
                flexDirection: "row",
                alignItems: "center",
                justifyContent: "flex-start",
                borderBottomWidth: 1,
                alignSelf: "center",
              }}
              onPress={() => {
                navigation.navigate("Kairo Merch Contact us");
              }}
            >
              <View
                style={{
                  alignItems: "center",
                  justifyContent: "center",
                  marginLeft: "10%",
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_400Regular",
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    color: "#333333",
                  }}
                >
                  Contact us
                </Text>
              </View>
            </TouchableOpacity>
            <TouchableOpacity
              style={{
                height: 0.05 * windowHeight,
                width: "100%",
                flexDirection: "row",
                alignItems: "center",
                justifyContent: "flex-start",
                borderBottomWidth: 1,
                alignSelf: "center",
              }}
              onPress={logout}
            >
              <View
                style={{
                  alignItems: "center",
                  justifyContent: "center",
                  marginLeft: "10%",
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_400Regular",
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    color: "#333333",
                  }}
                >
                  Logout
                </Text>
              </View>
            </TouchableOpacity>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

export default NavigationBar;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    marginTop: "5%",
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    borderRadius: 50,
    width: "18%",
    height: "8%",
    padding: 20,
    shadowRadius: 10,
    backgroundColor: "#F7567C",
    zIndex: 999,
  },
  button2: {
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    width: "8%",
    height: "2%",
    padding: 18,
    shadowRadius: 5,
    backgroundColor: "#3D9EBC",
  },

  gap: {
    height: 10,
  },
  field: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "96.5%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  subField: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "48%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  pwfield: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 5,
    width: "100%",
    height: "25%",
    padding: 15,
    backgroundColor: "white",
    flexDirection: "row",
  },
  textInputContainer: {
    marginBottom: 20,
    marginTop: 20,
  },

  roundedTextInput: {
    borderRadius: 10,
    backgroundColor: "white",
    borderWidth: 1,
    marginLeft: 20,
    marginRight: 20,
  },
});
