import {
  Animated,
  StyleSheet,
  Text,
  View,
  ImageBackground,
  Image,
  Easing,
  TouchableOpacity,
  TextInput,
  Dimensions,
  Alert,
  CheckBox,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import OTPTextView from "react-native-otp-textinput";
import DropDownPicker from "react-native-dropdown-picker";
import * as ImagePicker from "expo-image-picker";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import NavigationBar from "./navigation-bar";
import config from "../../config";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var logo = require("../../assets/logo.png");
var loading = require("../../assets/loading.gif");
var km = require("../../assets/icon.png");
var trends = require("../../assets/trends.png");
var status_25 = require("../../assets/status-25.png");
var advisor_financial = require("../../assets/advisor-financial.png");
var status_50 = require("../../assets/status-50.png");
var advisor_art = require("../../assets/advisor-art.png");
var status_75 = require("../../assets/status-75.png");
var advisor_product = require("../../assets/advisor-product.png");
var Advisor = ({ route, navigation }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);
  const [errMsg, setErrorMsg] = useState("");
  const status = route.params.status;

  var actionHandler = () => {
    console.log(status);
    if (status == 1) {
      navigation.navigate("Kairo Merch Financial Account Commission Plan");
    } else if (status == 2) {
      navigation.navigate("Kairo Merch Add Art");
    } else if (status == 3) {
      navigation.navigate("Kairo Merch Create Product");
    } else {
      navigation.navigate("Kairo Merch Shop");
    }
  };

  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: "100%",
          height: "100%",
          overflow: "hidden",
          backgroundColor: "#FFFFFF",
        }}
      >
        <View style={{ position: "absolute", bottom: 50, left: 50 }}>
          <Text>{config.version}</Text>
        </View>
        <View
          style={{
            height: "11%",
            width: "100%",
            left: 0,
            top: 0,
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            alignItems: "center",
            justifyContent: "flex-start",

            flexDirection: "row",
          }}
        >
          <View style={{ height: "15%" }}></View>
          <Image
            source={logo}
            style={{
              width: 214,
              height: "70%",
              marginLeft: 20,
              resizeMode: "contain",
            }}
          />
          <View style={{ height: "15%" }}></View>
          <View
            style={{
              height: "30%",
              width: "8%",
              alignItems: "center",
              position: "absolute",
              right: "2%",
            }}
          >
            <NavigationBar
              walletIsDimmed={true}
              notifications={[]}
              navigation={navigation}
            />
          </View>
        </View>
        <View
          style={{
            height: "5%",
            width: "100%",
            alignItems: "center",
            justifyContent: "center",
            flexDirection: "row",
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            zIndex: -1,
          }}
        >
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Kairo Merch Shop");
            }}
          >
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight * 0.018,
                color: "#04475C",
              }}
            >
              My shop
            </Text>
          </TouchableOpacity>

          <View style={{ width: "8%" }} />

          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Kairo Merch Art Files");
            }}
          >
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight * 0.018,
                color: "#04475C",
              }}
            >
              Art files
            </Text>
          </TouchableOpacity>
          <View style={{ width: "8%" }} />
          <TouchableOpacity
            onPress={() => {
              navigation.navigate("Kairo Merch Orders");
            }}
          >
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight * 0.018,
                color: "#04475C",
              }}
            >
              Orders
            </Text>
          </TouchableOpacity>
        </View>
        {/* <TouchableOpacity
            style={{ width: "3%", height: 25 }}
            onPress={() => {
              navigation.pop();
            }}
          >
            <Image
              source={previous}
              style={{
                width: windowHeight <= 1080 ? 60 : 80,
                height: windowHeight <= 1080 ? 40 : 50,
                resizeMode: "contain",
                marginLeft: 20,
              }}
            />
          </TouchableOpacity> */}

        <View
          style={{
            width: "100%",
            height: "84%",
            flexDirection: "row",
            marginTop: "1%",
            zIndex: -1,
          }}
        >
          <TouchableOpacity
            style={{
              width: "10%",
              height: "5%",
              alignSelf: "flex-start",
              marginLeft: "2%",
              position: "absolute",
              zIndex: 999,
            }}
          >
            <Image
              source={trends}
              style={{ width: "100%", height: "100%", resizeMode: "contain" }}
            />
          </TouchableOpacity>

          <View
            style={{
              width: "100%",
              height: "95%",
              alignItems: "center",
            }}
          >
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight * 0.025,
                color: "#04475C",
              }}
            >
              Start selling on KairoMerch
            </Text>
            <View style={{ height: "10%" }} />
            <View
              style={{
                width: "80%",
                height: "80%",
                flexDirection: "row",
              }}
            >
              <View
                style={{
                  height: "100%",
                  width: "50%",
                  alignItems: "center",
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    fontSize: windowHeight * 0.022,
                    color: "#04475C",
                  }}
                >
                  Your current progress
                </Text>

                <View style={{ height: "5%" }} />
                <Image
                  source={
                    status == 1
                      ? status_25
                      : status == 2
                      ? status_50
                      : status == 3
                      ? status_75
                      : status_75
                  }
                  style={{
                    width: 0.7 * 0.5 * windowWidth,
                    height: 0.5 * windowHeight,
                    resizeMode: "contain",
                  }}
                />
              </View>
              {status != 3 ? (
                <View
                  style={{
                    height: "100%",
                    width: "50%",
                    alignItems: "center",
                  }}
                >
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight * 0.022,
                      color: "#04475C",
                    }}
                  >
                    Your next step
                  </Text>
                  <View style={{ height: "5%" }} />
                  <Image
                    source={
                      status == 1
                        ? advisor_financial
                        : status == 2
                        ? advisor_art
                        : status == 3
                        ? advisor_product
                        : advisor_product
                    }
                    style={{
                      width: 0.7 * 0.5 * windowWidth,
                      height: 0.5 * windowHeight,
                      resizeMode: "contain",
                    }}
                  />
                  <TouchableOpacity
                    style={styles.button}
                    onPress={actionHandler}
                  >
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight * 0.012,
                        color: "#FFFFFF",
                      }}
                    >
                      {status == 1
                        ? "Set up financial account"
                        : status == 2
                        ? "Upload art"
                        : status == 3
                        ? "Create product"
                        : "Create product"}
                    </Text>
                  </TouchableOpacity>
                </View>
              ) : (
                <View />
              )}
            </View>
          </View>
        </View>

        {isLoading ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFFCC",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "20%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 500,
                alignSelf: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Image
                source={loading}
                style={{
                  width: "90%",
                  height: "40%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "70%",
                }}
              />
            </View>
          </View>
        ) : (
          <View />
        )}

        {isError ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFF99",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "30%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 50,
                alignSelf: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight * 0.015,
                  color: "#333333",
                  marginTop: "10%",
                }}
              >
                {errMsg}
              </Text>

              <View style={{ height: "10%" }} />
            </View>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

export default Advisor;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    position: "absolute",
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    borderRadius: 50,
    width: "30%",
    height: "4%",
    padding: 8,
    shadowRadius: 10,
    backgroundColor: "#F7567C",
    zIndex: 999,
    top: 0.5 * windowHeight,
  },
  button2: {
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    width: "8%",
    height: "2%",
    padding: 18,
    shadowRadius: 5,
    backgroundColor: "#3D9EBC",
  },

  gap: {
    height: 10,
  },
  field: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "96.5%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  subField: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 10,
    width: "48%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  pwfield: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 5,
    width: "100%",
    height: "25%",
    padding: 15,
    backgroundColor: "white",
    flexDirection: "row",
  },
  textInputContainer: {
    marginBottom: 20,
    marginTop: 20,
  },

  roundedTextInput: {
    borderRadius: 10,
    backgroundColor: "white",
    borderWidth: 1,
    marginLeft: 20,
    marginRight: 20,
  },
});
