import {
  Animated,
  StyleSheet,
  Text,
  View,
  ImageBackground,
  Image,
  Easing,
  TouchableOpacity,
  TextInput,
  Dimensions,
  Alert,
  CheckBox,
  ScrollView,
} from "react-native";
import React, { useEffect, useRef, useState } from "react";
import OTPTextView from "react-native-otp-textinput";
import DropDownPicker from "react-native-dropdown-picker";
import * as ImagePicker from "expo-image-picker";
import axios from "axios";
import {
  useFonts,
  Roboto_100Thin,
  Roboto_100Thin_Italic,
  Roboto_300Light,
  Roboto_300Light_Italic,
  Roboto_400Regular,
  Roboto_400Regular_Italic,
  Roboto_500Medium,
  Roboto_500Medium_Italic,
  Roboto_700Bold,
  Roboto_700Bold_Italic,
  Roboto_900Black,
  Roboto_900Black_Italic,
} from "@expo-google-fonts/roboto";
import NavigationBar from "../navigation-bar";
import config from "../../../config";
const windowWidth = Dimensions.get("window").width;
const windowHeight = Dimensions.get("window").height;

var logo = require("../../../assets/logo.png");
var loading = require("../../../assets/loading.gif");
var km = require("../../../assets/icon.png");
var previous = require("../../../assets/Previous.png");
var Contact = ({ route, navigation }) => {
  let [fontsLoaded] = useFonts({
    Roboto_100Thin,
    Roboto_100Thin_Italic,
    Roboto_300Light,
    Roboto_300Light_Italic,
    Roboto_400Regular,
    Roboto_400Regular_Italic,
    Roboto_500Medium,
    Roboto_500Medium_Italic,
    Roboto_700Bold,
    Roboto_700Bold_Italic,
    Roboto_900Black,
    Roboto_900Black_Italic,
  });

  const [isLoading, setLoading] = useState(false);
  const [isError, setError] = useState(false);
  const [errMsg, setErrorMsg] = useState("");
  const [message, setMessage] = useState("");
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [number, setNumber] = useState("");
  const [organization, setOrganization] = useState("");

  var saveContact = () => {
    setLoading(true);
    let form_data = new FormData();
    form_data.append("contactEmail", email);
    form_data.append("contactName", name);
    form_data.append("contactMphone", number);
    form_data.append("organizationName", organization);
    form_data.append("message", message);

    axios
      .post(config.host + "/api/contact/postContact/", form_data, {
        headers: {
          "Content-Type": "multipart/form-data",
        },
      })
      .then((response) => {
        setLoading(false);
        setError(true);
        setErrorMsg(
          "Your message has been sent successfully! Thanks we will respond as soon as possible."
        );
        setEmail("");
        setMessage("");
        setName("");
        setNumber("");
        setOrganization("");
      })
      .catch((e) => {
        setLoading(false);
        setError(true);
        setErrorMsg("Please complete all fields");
      });
  };

  useEffect(() => {
    console.log(config);
  }, []);
  if (!fontsLoaded) {
    return <View></View>;
  } else {
    return (
      <View
        style={{
          width: "100%",
          height: "100%",
          overflow: "hidden",
          backgroundColor: "#FFFFFF",
        }}
      >
        <View style={{ position: "absolute", bottom: 50, left: 50 }}>
          <Text>{config.version}</Text>
        </View>
        <View
          style={{
            height: "11%",
            width: "100%",
            left: 0,
            top: 0,
            borderBottomWidth: 3,
            borderBottomColor: "#C4C4C4",
            alignItems: "center",
            justifyContent: "flex-start",

            flexDirection: "row",
          }}
        >
          <View style={{ height: "15%" }}></View>
          <Image
            source={logo}
            style={{
              width: 214,
              height: "70%",
              marginLeft: 20,
              resizeMode: "contain",
            }}
          />
          <View style={{ height: "15%", backgroundColor: "#3D9EBC88" }}></View>
        </View>
        <View
          style={{ backgroundColor: "#3D9EBC88", width: "100%", height: 25 }}
        >
          <TouchableOpacity
            style={{ width: "3%", height: "100%" }}
            onPress={() => {
              navigation.pop();
            }}
          >
            <Image
              source={previous}
              style={{
                width: windowHeight <= 1080 ? 60 : 80,
                height: windowHeight <= 1080 ? 40 : 50,
                resizeMode: "contain",
                marginLeft: 20,
              }}
            />
          </TouchableOpacity>
        </View>

        <View
          style={{
            height: "89%",
            width: "100%",

            borderRadius: 4,
            alignItems: "center",
            alignSelf: "center",
            zIndex: -2,
            backgroundColor: "#3D9EBC88",
            alignItems: "center",
          }}
        >
          <View
            style={{
              height: "60%",
              width: "40%",

              borderRadius: 4,
              alignItems: "center",
              alignSelf: "center",
              zIndex: -2,
              backgroundColor: "#FFFFFF",
              shadowColor: "grey",
              shadowRadius: 10,
              marginTop: "5%",
            }}
          >
            <Text
              style={{
                fontFamily: "Roboto_700Bold",
                fontSize: windowHeight <= 1080 ? 24 : 32,
                color: "#04475C",
                alignSelf: "center",
                marginBottom: "1%",
                marginTop: "2%",
              }}
            >
              Contact us
            </Text>
            <View
              style={{
                height: "70%",
                width: "95%",
                padding: 10,
                alignItems: "center",
              }}
            >
              <View
                style={{
                  width: "100%",
                  height: "20%",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <View style={styles.field}>
                  {name != "" ? (
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      Full name
                    </Text>
                  ) : (
                    <View />
                  )}

                  <TextInput
                    style={{
                      borderColor: "black",
                      height: "90%",
                      fontSize: windowHeight <= 1080 ? 12 : 18,
                      fontFamily: "Roboto_400Regular",
                      borderWidth: 2,
                      padding: 10,
                      borderRadius: 4,
                    }}
                    autoCorrect={false}
                    placeholder={"Full name"}
                    onChangeText={(newText) => setName(newText)}
                  ></TextInput>
                </View>
              </View>
              <View
                style={{
                  width: "100%",
                  height: "20%",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <View style={styles.field}>
                  {email != "" ? (
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      Email address
                    </Text>
                  ) : (
                    <View />
                  )}

                  <TextInput
                    style={{
                      borderColor: "black",
                      height: "90%",
                      fontSize: windowHeight <= 1080 ? 12 : 18,
                      fontFamily: "Roboto_400Regular",
                      borderWidth: 2,
                      padding: 10,
                      borderRadius: 4,
                    }}
                    autoCorrect={false}
                    placeholder={"Email address"}
                    onChangeText={(newText) => setEmail(newText)}
                  ></TextInput>
                </View>
              </View>
              <View
                style={{
                  width: "100%",
                  height: "20%",
                  flexDirection: "row",
                  justifyContent: "center",
                }}
              >
                <View style={styles.subField}>
                  {number != "" ? (
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      Phone number
                    </Text>
                  ) : (
                    <View />
                  )}

                  <TextInput
                    style={{
                      borderColor: "black",
                      height: "90%",
                      fontSize: windowHeight <= 1080 ? 12 : 18,
                      fontFamily: "Roboto_400Regular",
                      borderWidth: 2,
                      padding: 10,
                      borderRadius: 4,
                    }}
                    autoCorrect={false}
                    placeholder={"Phone number"}
                    onChangeText={(newText) => setNumber(newText)}
                  ></TextInput>
                </View>
                <View style={{ width: "0.5%" }} />
                <View style={[styles.subField, { zIndex: 999 }]}>
                  {organization != "" ? (
                    <Text
                      style={{
                        fontFamily: "Roboto_700Bold",
                        fontSize: windowHeight <= 1080 ? 12 : 18,
                        color: "#04475C",
                      }}
                    >
                      Organization name
                    </Text>
                  ) : (
                    <View />
                  )}
                  <TextInput
                    style={{
                      borderColor: "black",
                      height: "90%",
                      fontSize: windowHeight <= 1080 ? 12 : 18,
                      fontFamily: "Roboto_400Regular",
                      borderWidth: 2,
                      padding: 10,
                      borderRadius: 4,
                    }}
                    autoCorrect={false}
                    placeholder={"Organization name"}
                    onChangeText={(newText) => setOrganization(newText)}
                  ></TextInput>
                </View>
              </View>
              <View
                style={{
                  height: "50%",
                  width: "95%",
                  padding: 10,
                  justifyContent: "center",
                }}
              >
                {message != "" ? (
                  <Text
                    style={{
                      fontFamily: "Roboto_700Bold",
                      fontSize: windowHeight <= 1080 ? 12 : 18,
                      color: "#04475C",
                    }}
                  >
                    Message
                  </Text>
                ) : (
                  <View />
                )}

                <TextInput
                  style={{
                    borderColor: "black",
                    height: "100%",
                    fontSize: windowHeight <= 1080 ? 12 : 18,
                    fontFamily: "Roboto_400Regular",
                    padding: 10,
                    borderWidth: 2,
                    borderRadius: 4,
                  }}
                  multiline={true}
                  autoCorrect={false}
                  placeholder={"Message"}
                  onChangeText={(newText) => setMessage(newText)}
                ></TextInput>
              </View>
            </View>

            <TouchableOpacity style={styles.button} onPress={saveContact}>
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 12 : 18,
                  color: "#FFFFFF",
                }}
              >
                Submit
              </Text>
            </TouchableOpacity>
          </View>
        </View>

        {isLoading ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFFCC",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "20%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 500,
                alignSelf: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Image
                source={loading}
                style={{
                  width: "90%",
                  height: "40%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "70%",
                }}
              />
            </View>
          </View>
        ) : (
          <View />
        )}

        {isError ? (
          <View
            style={{
              height: "100%",
              width: "100%",
              zIndex: 999,
              backgroundColor: "#FFFFFF99",
              position: "absolute",
              alignContent: "center",
              justifyContent: "center",
            }}
          >
            <View
              style={{
                height: "30%",
                width: "20%",
                backgroundColor: "#FFFFFFDA",
                borderRadius: 50,
                alignSelf: "center",
                alignItems: "center",
              }}
            >
              <Image
                source={km}
                style={{
                  marginTop: "5%",
                  width: "90%",
                  height: "50%",
                  resizeMode: "contain",
                  alignSelf: "center",
                  opacity: "80%",
                }}
              />
              <Text
                style={{
                  fontFamily: "Roboto_400Regular",
                  fontSize: windowHeight <= 1080 ? 12 : 16,
                  color: "#333333",
                  marginTop: "10%",
                  justifyContent: "center",
                }}
              >
                {errMsg}
              </Text>

              <View style={{ height: "5%" }} />
              <TouchableOpacity
                style={{
                  width: "20%",
                  alignSelf: "center",
                  height: "10%",
                  backgroundColor: "#888888",
                  alignItems: "center",
                  justifyContent: "center",
                  borderRadius: 10,
                }}
                onPress={() => {
                  setError(false);
                }}
              >
                <Text
                  style={{
                    fontFamily: "Roboto_700Bold",
                    color: "#FFFFFF",
                    fontSize: windowHeight <= 1080 ? 12 : 16,
                    padding: 10,
                  }}
                >
                  Ok
                </Text>
              </TouchableOpacity>
            </View>
          </View>
        ) : (
          <View />
        )}
      </View>
    );
  }
};

export default Contact;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: "#fff",
    alignItems: "center",
    justifyContent: "center",
  },
  button: {
    alignSelf: "center",
    justifyContent: "center",
    marginTop: "5%",
    alignItems: "center",
    borderRadius: 50,
    width: "20%",
    height: "6%",
    padding: 8,
    shadowRadius: 10,
    backgroundColor: "#F7567C",
    zIndex: 999,
    shadowColor: "#F7567C",
    flexDirection: "row",
  },
  button2: {
    alignSelf: "center",
    justifyContent: "center",
    alignItems: "center",
    width: "8%",
    height: "2%",
    padding: 18,
    shadowRadius: 5,
    backgroundColor: "#3D9EBC",
  },

  gap: {
    height: 10,
  },
  field: {
    justifyContent: "center",
    borderRadius: 10,
    width: "96.5%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  subField: {
    justifyContent: "center",
    borderRadius: 10,
    width: "48%",
    height: "100%",
    padding: 15,
    backgroundColor: "white",
  },
  pwfield: {
    marginTop: "2%",
    justifyContent: "center",
    borderRadius: 5,
    width: "100%",
    height: "25%",
    padding: 15,
    backgroundColor: "white",
    flexDirection: "row",
  },
  textInputContainer: {
    marginBottom: 20,
    marginTop: 20,
  },

  roundedTextInput: {
    borderRadius: 10,
    backgroundColor: "white",
    borderWidth: 1,
    marginLeft: 20,
    marginRight: 20,
  },
});
